
import { defineComponent, reactive, ref } from "vue";
import { Delete, Plus } from "@element-plus/icons";
import { categoryType, findids, gettreeList } from "./build";
import { findIds } from "@/utils/help";
import {
  createFAQ,
  getFAQDetail,
  deleteFAQQuestion,
  addFAQQuestion,
  delteFAQAnswer,
  createFAQAnswer,
  updateFAQAnswer,
  startFAQ,
  questionValid,
  recommendation,
  getCommitBaseId,
} from "@/api/deli/faq";
import { submitCommit } from "@/api/deli/faqCommit";
import E from "wangeditor";
import _ from "lodash";
export default defineComponent({
  components: {
    Delete,
  },
  props: {
    mode: {
      type: String,
      default: "normal", // approve/normal
    },
    commitId: {
      type: String,
      default: "",
    },
  },
  setup() {
    const dialogVisible = ref(false);
    const questionList = ref<
      { content: string; faqQuestionId: number | string }[]
    >([]);
    const question = ref("");
    const form = reactive({
      time: "default",
      faqId: "",
      desc: "",
      categoryId: [],
    });
    const loading = ref(false);
    const start = ref(true);
    const type = ref("img");
    const sureTime = ref([]);
    const answerGroup = ref<{ plainText: string; richAnswer: string }[]>([]);
    const answerContent = ref();

    const props = reactive({
      value: "categoryId",
      label: "categoryTitle",
      checkStrictly: true,
    });

    const editorVisible = ref(false);
    const editor = ref();
    const editorIndex = ref();

    const questionTimer = ref<NodeJS.Timeout>();
    const sameQuestion = ref<string[]>([]);
    const baseId = ref();

    // 分页
    const questionPageInfo = reactive({
      pageNum: 1,
      pageSize: 5,
    });

    const answerPageInfo = reactive({
      pageNum: 1,
      pageSize: 5,
    });
    return {
      questionPageInfo,
      answerPageInfo,
      dialogVisible,
      questionList,
      question,
      form,
      start,
      answerGroup,
      Plus,
      type,
      categoryType,
      props,
      sureTime,
      editorVisible,
      editor,
      answerContent,
      editorIndex,
      questionTimer,
      sameQuestion,
      gettreeList,
      baseId,
      findIds,
      loading,
    };
  },
  methods: {
    // 分页
    removeAnser(index: number) {
      this.answerGroup.splice(index, 1);
    },
    // 删除问题
    async deleteQuestion(idx, item) {
      if (this.form.faqId) {
        await deleteFAQQuestion(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            questionId: item.faqQuestionId,
          }
        );
      }
      this.questionList.splice(idx, 1);
    },
    // 添加问题
    async addQuestion() {
      if (!this.question) {
        return;
      }
      console.log("ewytu i", this.commitId);
      if (this.mode === "approve") {
        this.questionList.push({
          content: this.question,
          faqQuestionId: "",
        });
        this.question = "";
      } else {
        // 判断一下问题的有效性
        const validResult = await questionValid(
          {
            baseId:
              (this.$route.query.id && Number(this.$route.query.id)) ||
              this.baseId,
          },
          {
            question: this.question,
          }
        );
        if (validResult.data) {
          if (this.form.faqId) {
            await addFAQQuestion(
              {
                baseId: Number(this.$route.query.id),
              },
              {
                faqId: Number(this.form.faqId),
                question: this.question,
              }
            );
            this.getFaqDetail(this.form.faqId);
          } else {
            this.questionList.push({
              content: this.question,
              faqQuestionId: "",
            });
          }
          this.question = "";
        } else {
          this.$message.warning("已有相似问题！");
        }
      }
    },
    // 删除回答
    async deleteAnswer(index, item) {
      if (this.form.faqId) {
        await delteFAQAnswer(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            answerId: item.faqAnswerId,
          }
        );
      }
      this.answerGroup.splice(index, 1);
    },
    // 添加回答
    async addAnswer() {
      if (!this.answerContent) {
        return;
      }
      if (this.form.faqId) {
        await createFAQAnswer(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            plainText: this.answerContent,
            richText: this.answerContent,
          }
        );
        this.getFaqDetail(this.form.faqId);
      } else {
        this.answerGroup.push({
          plainText: this.answerContent,
          richAnswer: this.answerContent,
        });
      }
      this.answerContent = "";
    },
    openEditor(index, item) {
      if (this.mode === "approve") {
        return;
      }
      this.editorVisible = true;
      this.$nextTick(() => {
        if (!this.editor) {
          this.editor = new E("#editor");
          this.editor.config.uploadImgShowBase64 = true;
          this.editor.create();
        } else {
          this.editor.txt.html("");
        }
        this.editor.txt.html(item.richAnswer);
        this.editorIndex = index;
      });
    },
    async saveEditor() {
      this.editorVisible = false;
      const answer: any = this.answerGroup[this.editorIndex];
      if (this.form.faqId) {
        await updateFAQAnswer(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            answerId: answer.faqAnswerId,
            richText: this.editor.txt.html(),
            plainText: this.editor.txt.text(),
          }
        );
        this.getFaqDetail(this.form.faqId);
      }
      this.answerGroup[this.editorIndex].richAnswer = this.editor.txt.html();
      this.answerGroup[this.editorIndex].plainText = this.editor.txt.text();
    },
    async saveFaq() {
      if (this.form.time !== "default" && this.sureTime.length === 0) {
        this.$message.error("时间不能为空！");
        return;
      }
      if (this.mode === "approve") {
        if (
          (this.form.categoryId.length === 1 && !this.form.categoryId[0]) ||
          this.form.categoryId.length === 0
        ) {
          this.$message.error("请先选择一个知识分类！");
          return;
        }
        this.loading = true;
        // 审批
        try {
          await submitCommit({
            faqDesc: this.form.desc,
            faqAnswers: this.answerGroup.map((item) => item.plainText),
            faqQuestions: this.questionList.map((item) => item.content),
            categoryId:
              this.form.categoryId[this.form.categoryId.length - 1] === ""
                ? 0
                : this.form.categoryId[this.form.categoryId.length - 1],
          });
        } catch (error) {
          console.error(error);
        }
        this.$message.success("添加成功！");
        this.dialogVisible = false;
        this.loading = false;
      } else {
        if (
          (this.form.categoryId.length === 1 && !this.form.categoryId[0]) ||
          this.form.categoryId.length === 0
        ) {
          this.$message.error("请先选择一个知识分类！");
          return;
        }
        try {
          await createFAQ(
            {
              baseId:
                (this.$route.query.id && Number(this.$route.query.id)) ||
                this.baseId,
            },
            {
              answers: this.answerGroup,
              categoryId:
                this.form.categoryId[this.form.categoryId.length - 1] === ""
                  ? 0
                  : this.form.categoryId[this.form.categoryId.length - 1],
              desc: this.form.desc,
              expireTime: this.form.time === "default" ? "" : this.sureTime[1],
              validateTime:
                this.form.time === "default" ? "" : this.sureTime[0],
              questions: this.questionList.map((item) => item.content),
            }
          );
        } catch (error) {
          console.log(error);
        }

        if (!this.commitId) {
          this.$message.success("创建成功！");
        }
        this.dialogVisible = false;
        this.$emit("intiData");
        this.initValue();
      }
    },
    async getFaqDetail(id) {
      this.dialogVisible = true;
      const res: any = await getFAQDetail(
        { baseId: Number(this.$route.query.id) },
        {
          faqId: id,
        }
      );
      this.form.desc = res.data.faqDesc;
      this.form.categoryId = findids(res.data.categoryId);
      this.form.time = res.data.validateType == 0 ? "default" : "time";
      this.form.faqId = res.data.faqId;
      this.start = res.data.faqStatus === 0 ? false : true;
      this.questionList = res.data.questions;
      this.answerGroup = res.data.answers;
    },
    async changeStart(val) {
      await startFAQ(
        {
          baseId: Number(this.$route.query.id),
        },
        {
          faqId: Number(this.form.faqId),
          status: val ? 1 : 0,
        }
      );
      this.getFaqDetail(this.form.faqId);
    },
    initValue() {
      this.form.desc = "";
      this.form.faqId = "";
      this.form.time = "default";
      this.form.categoryId = [];
      this.questionList = [];
      this.answerGroup = [];
      this.answerContent="";
      this.question="";
    },
    // 减提高和你问题
    async watchQuestion() {
      if (this.mode === "approve" || this.commitId) {
        return;
      }
      clearTimeout(this.questionTimer as NodeJS.Timeout);
      this.questionTimer = setTimeout(async () => {
        const res = await recommendation(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            question: this.question,
          }
        );
        this.sameQuestion = res.data as string[];
      }, 200);
    },
    async getTreeList() {
      const res: any = await getCommitBaseId();
      this.baseId = res.data;
      this.gettreeList(res.data as number);
    },
    setFormCategory(id) {
      this.form.categoryId = findIds(id, this.categoryType.lists, {
        value: "categoryId",
        children: "children",
      }) as any;
    },
  },
  computed: {
    getQuestionList() {
      let list = _.chunk(this.questionList, this.questionPageInfo.pageSize);
      return list[this.questionPageInfo.pageNum - 1];
    },
    getAnswerList() {
      let list = _.chunk(this.answerGroup, this.answerPageInfo.pageSize);
      return list[this.answerPageInfo.pageNum - 1];
    },
  },
  mounted() {
    console.log("111", JSON.parse(localStorage.getItem("user") as string));
    this.getTreeList();
  },
});
