// 全局请求封装
import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { ElMessage } from "element-plus";
import { Service } from "@/gtypes/Service";
import "element-plus/theme-chalk/el-message.css";
import { downFile } from "@/utils/help";
// import { ApiUrl } from "@/config/index";
export const prefix =
  process.env.NODE_ENV === "production" ? "/deli/" : "/deli/";
// axios.defaults.baseURL = prefix;

axios.defaults.withCredentials = true;
import {
  setCheckVisible,
  checkUserVisible,
  tipText,
  netWordkTip,
} from "@/hook/checktUser";
import { errorLog } from "@/api/error";

// axios返回结果全局处理
axios.interceptors.response.use(
  function (response) {
    // console.log("responseresponse", response);
    // 对响应数据做点什么
    if (
      response.headers["content-disposition"] ||
      response.headers["content-type"] === "application/octet-stream"
    ) {
      const result = Object.assign({}, response);
      const name = response.headers["content-disposition"] || "";
      const reg = new RegExp("filename=(.*)");
      reg.test(name);
      downFile(
        response.data,
        decodeURIComponent(RegExp.$1) ||
          decodeURI(response.headers["content-disposition"])
      );
      result.data = {
        code: 0,
        data: {
          fileName:
            decodeURIComponent(RegExp.$1) ||
            decodeURI(response.headers["content-disposition"]),
          data: response.data,
        },
        msg: "请求成功",
      };
      return result;
    }
    return response;
  },
  function (error) {
    // 对响应错误做点什么
    console.log("error", error.message);
    // throw error
    return Promise.reject(error.message || "接口请求异常");
  }
);

// interface ResponseResult {
//   code: Number;
//   data: null | {} | any[];
//   mag: string;
// }

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
const Request = function ({
  url,
  data = {},
  params = {},
  method,
  headers,
  ...config
}: AxiosRequestConfig): Promise<Service.Result> {
  const token = localStorage.getItem("token") || "";
  return new Promise((resolve, reject) => {
    const options: AxiosRequestConfig = {
      url: url?.indexOf("/sys_error") == -1 ? prefix + url : url,
      method: method || "GET",
      headers: {
        token: token,
        ...headers,
      },
      ...config,
      data,
      params,
    };
    axios(options)
      .then((response: AxiosResponse) => {
        if (options.url?.indexOf("api/error") !== -1) {
          return;
        }
        if (response.status === 200) {
          // 请求成功
          if (response.data.code === 0) {
            resolve(response.data);
          } else if (response.data.code === 401) {
            ElMessage.error(response.data.msg);
            console.error("401登陆失效");
            localStorage.removeItem("user");
            window.location.href = "/login";
          } else if (response.data.code === 402) {
            checkUserVisible.value = false;
            checkUserVisible.value = true;
            tipText.value = "去登陆页面";
            netWordkTip.value = response.data.msg;
          } else if (response.data.code === 406) {
            // errorLog({
            //   type: "触发406",
            //   content: {
            //     url: options.url,
            //   },
            //   user: localStorage.getItem("user"),
            // });
            setCheckVisible(true);
          } else {
            reject(response.data.msg);
            ElMessage.error(response.data.msg);
          }
        } else {
          ElMessage.error(response.data.msg);
          reject(response);
        }
      })
      .catch((error) => {
        if (options.url?.indexOf("api/error") === -1) {
          ElMessage.error(error);
          reject(error);
        }
      });
  });
};

export { Request };
