
import { defineComponent, ref } from "vue";
import { getIMMessage, setIMMessage } from "@/api/deli/setting";
import TitleCom from "@/components/title/index.vue";
import { checkAuth } from "@/utils/help";
export default defineComponent({
  components: {
    TitleCom,
  },
  setup() {
    const start = ref(false);
    const message = ref();
    const loading = ref(false);
    const num = ref();
    return {
      start,
      message,
      loading,
      checkAuth,
      num,
    };
  },
  methods: {
    async getInitData() {
      this.loading = true;
      try {
        const res: any = await getIMMessage({
          groupId: "1",
        });
        if (res.data.code === 20000) {
          this.message = res.data.result.autoKillMessage;
          this.start = res.data.result.autoKillStatus === 1 ? true : false;
          this.num = res.data.result.autoKillTime;
        } else {
          this.$message.error(res.data.msg);
        }
      } catch (error) {
        console.error("getIMMessage", error);
      }

      this.loading = false;
    },
    async save() {
      const res: any = await setIMMessage({
        autoKillStatus: 1,
        autoKillMessage: this.message,
        autoKillTime: this.num,
        groupId: "1",
      });
      if (res.data.code === 20000) {
        this.$message.success("设置成功！");
      } else {
        this.$message.error(res.data.msg);
      }
    },
  },
  mounted() {
    this.getInitData();
  },
});
