import { DefineComponent, ref } from "vue";
import { checkAuth } from "@/utils/help";
const isCollapse = ref(true);

const changeCollapse: () => void = () => {
  isCollapse.value = !isCollapse.value;
};

interface Menu {
  menu: string;
  icon?: any;
  key: string;
  path?: string;
  auth?: number[];
  children?: Menu[];
}

const filterMenu: (menu: Menu[]) => Menu[] = (menu, list = []) => {
  const arr: Menu[] = [...list];
  menu.forEach((element) => {
    if (element.children && element.children.length > 0) {
      arr.push({
        ...element,
        children: filterMenu(element.children),
      });
    } else {
      if (element.auth) {
        if (checkAuth(element.auth)) {
          arr.push(element);
        }
      } else {
        arr.push(element);
      }
    }
  });
  return arr;
};

export { isCollapse, changeCollapse, filterMenu };
