
import { defineComponent, reactive, ref, computed, watch } from "vue";
import { Delete, Plus } from "@element-plus/icons";
import { categoryType, findids } from "./faqHook";
import _ from "lodash";
import {
  updateFAQDesc,
  createFAQ,
  updateFAQCategory,
  getFAQDetail,
  deleteFAQQuestion,
  addFAQQuestion,
  delteFAQAnswer,
  createFAQAnswer,
  updateFAQAnswer,
  startFAQ,
  questionValid,
  recommendation,
  updateValidateTime,
} from "@/api/deli/faq";
import E from "wangeditor";
export default defineComponent({
  components: {
    Delete,
  },
  props: ["categoryId"],
  setup(props1) {
    const showCategory = reactive({ ...props1.categoryId });
    const treeCategory = reactive({ ...props1.categoryId });
    const dialogVisible = ref(false);
    const questionList = ref<
      { content: string; faqQuestionId: number | string }[]
    >([]);
    const question = ref("");
    const form = reactive({
      time: "default",
      faqId: "",
      desc: "",
      categoryId: [""],
    });
    watch(props1.categoryId, () => {
      showCategory.categoryId = props1.categoryId.categoryId;
      treeCategory.categoryId = props1.categoryId.categoryId;
      if (props1.categoryId.categoryId == 0) {
        showCategory.categoryId = [""];
        treeCategory.categoryId = [""];
        form.categoryId.push("-1");
        return;
      }
      console.log("showCategory.categoryId", showCategory.categoryId);
      form.categoryId.push(showCategory.categoryId);
    });

    const start = ref(true);
    const type = ref("img");
    const sureTime = ref<any[]>([]);
    const answerGroup = ref<{ plainText: string; richAnswer: string }[]>([]);
    const answerContent = ref();

    const props = reactive({
      value: "categoryId",
      label: "categoryTitle",
      checkStrictly: true,
    });

    const editorVisible = ref(false);
    const editor = ref();
    const editorIndex = ref();

    const questionTimer = ref<NodeJS.Timeout>();
    const sameQuestion = ref<string[]>([]);
    const status = ref();
    const modification = ref(true);

    // 分页
    const questionPageInfo = reactive({
      pageNum: 1,
      pageSize: 5,
    });

    const answerPageInfo = reactive({
      pageNum: 1,
      pageSize: 5,
    });

    return {
      treeCategory,
      showCategory,
      dialogVisible,
      questionList,
      question,
      form,
      start,
      answerGroup,
      Plus,
      type,
      categoryType,
      props,
      sureTime,
      editorVisible,
      editor,
      answerContent,
      editorIndex,
      questionTimer,
      sameQuestion,
      status,
      modification,
      questionPageInfo,
      answerPageInfo,
    };
  },
  computed: {
    getQuestionList() {
      // @ts-ignore
      const list = _.chunk(this.questionList, this.questionPageInfo.pageSize);
      // @ts-ignore
      return list[this.questionPageInfo.pageNum - 1];
    },
    getAnswerList() {
      // @ts-ignore
      let list = _.chunk(this.answerGroup, this.answerPageInfo.pageSize);
      // @ts-ignore
      return list[this.answerPageInfo.pageNum - 1];
    },
  },
  methods: {
    typechange(e) {
      this.form.categoryId = e;
      this.showCategory.categoryId = e[e.length - 1];
    },
    removeAnser(index: number) {
      this.answerGroup.splice(index, 1);
    },
    // 删除问题
    async deleteQuestion(idx, item) {
      if (this.form.faqId) {
        await deleteFAQQuestion(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            questionId: item.faqQuestionId,
          }
        );
      }
      this.questionList.splice(idx, 1);
    },
    // 添加问题
    async addQuestion() {
      if (!this.question) {
        return;
      }
      // 判断一下问题的有效性
      const validResult = await questionValid(
        {
          baseId: Number(this.$route.query.id),
        },
        {
          question: this.question,
        }
      );
      if (validResult.data) {
        if (this.form.faqId) {
          await addFAQQuestion(
            {
              baseId: Number(this.$route.query.id),
            },
            {
              faqId: Number(this.form.faqId),
              question: this.question,
            }
          );
          this.getFaqDetail(this.form.faqId);
        } else {
          this.questionList.push({
            content: this.question,
            faqQuestionId: "",
          });
        }
        this.question = "";
      } else {
        this.$message.warning("已有相似问题！");
      }
    },
    // 删除回答
    async deleteAnswer(index, item) {
      if (this.form.faqId) {
        await delteFAQAnswer(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            answerId: item.faqAnswerId,
          }
        );
      }
      this.answerGroup.splice(index, 1);
    },
    // 添加回答
    async addAnswer() {
      if (!this.answerContent) {
        return;
      }
      if (this.form.faqId) {
        await createFAQAnswer(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            plainText: this.answerContent,
            richText: this.answerContent,
          }
        );
        this.getFaqDetail(this.form.faqId);
      } else {
        this.answerGroup.push({
          plainText: this.answerContent,
          richAnswer: this.answerContent,
        });
      }
      this.answerContent = "";
    },
    openEditor(index, item) {
      if (this.status === "readonly") {
        return;
      }
      this.editorVisible = true;
      this.$nextTick(() => {
        if (!this.editor) {
          this.editor = new E("#editor");
          this.editor.config.uploadImgShowBase64 = true;
          this.editor.create();
        } else {
          this.editor.txt.html("");
        }
        this.editor.txt.html(item.richAnswer || item.richText);
        this.editorIndex = index;
      });
    },
    async saveEditor() {
      const answer: any = this.answerGroup[this.editorIndex];
      if (this.form.faqId) {
        await updateFAQAnswer(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            answerId: answer.faqAnswerId,
            richText: this.editor.txt.html(),
            plainText: this.editor.txt.text(),
          }
        );
        this.getFaqDetail(this.form.faqId);
      }
      this.answerGroup[this.editorIndex].richAnswer = this.editor.txt.html();
      this.answerGroup[this.editorIndex].plainText = this.editor.txt.text();
    },
    async saveFaq() {
      // 选择全部分类，默认添加到未分类
      if (!this.showCategory.categoryId[0]) {
        this.form.categoryId.push("-1");
      } else {
        this.form.categoryId.push(this.showCategory.categoryId);
      }
      console.log("123", this.form.categoryId);

      if (this.form.time !== "default" && this.sureTime.length === 0) {
        this.$message.error("时间不能为空！");
        return;
      }
      if (this.form.categoryId.length === 1 && !this.form.categoryId[0]) {
        this.$message.error("请先选择一个知识分类！");
        return;
      }

      await createFAQ(
        {
          baseId: Number(this.$route.query.id),
        },
        {
          answers: this.answerGroup,
          categoryId:
            this.form.categoryId[this.form.categoryId.length - 1] === ""
              ? 0
              : this.form.categoryId[this.form.categoryId.length - 1],
          desc: this.form.desc,
          expireTime: this.form.time === "default" ? "" : this.sureTime[1],
          validateTime: this.form.time === "default" ? "" : this.sureTime[0],
          questions: this.questionList.map((item) => item.content),
        }
      );
      this.$message.success("创建成功！");
      this.dialogVisible = false;
      this.$emit("intiData");
      this.initValue();
    },
    // 查询当前
    async getFaqDetail(id) {
      console.log(id);
      this.initValue();
      this.modification = true;
      this.dialogVisible = true;
      const res: any = await getFAQDetail(
        { baseId: Number(this.$route.query.id) },
        {
          faqId: id,
        }
      );
      console.log("res", res.data.expireTime);
      this.sureTime = [res.data.validateTime, res.data.expireTime];
      console.log("sureTime", this.sureTime);
      this.form.desc = res.data.faqDesc;
      this.form.categoryId = findids(res.data.categoryId);
      this.showCategory.categoryId = res.data.categoryId.toString();
      if (!findids(res.data.categoryId)) {
        this.showCategory.categoryId = [""];
        this.form.categoryId = [""];
      }
      this.form.time = res.data.validateType == 0 ? "default" : "time";
      this.form.faqId = res.data.faqId;
      this.start = res.data.faqStatus === 0 ? false : true;
      this.questionList = res.data.questions;
      this.answerGroup = res.data.answers;
    },
    async changeStart(val) {
      await startFAQ(
        {
          baseId: Number(this.$route.query.id),
        },
        {
          faqId: Number(this.form.faqId),
          status: val ? 1 : 0,
        }
      );
      this.getFaqDetail(this.form.faqId);
    },
    initValue() {
      this.modification = false;
      this.form.desc = "";
      this.form.faqId = "";
      this.form.time = "default";
      this.form.categoryId = [""];
      this.questionList = [];
      this.answerGroup = [];
      // this.showCategory.categoryId = "";
    },
    async watchQuestion() {
      clearTimeout(this.questionTimer as NodeJS.Timeout);
      this.questionTimer = setTimeout(async () => {
        const res = await recommendation(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            question: this.question,
          }
        );
        this.sameQuestion = res.data as string[];
      }, 200);
    },
    // 更新分类，描述，有效时间
    async updatecategory() {
      if (this.form.faqId) {
        await updateFAQCategory(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            categoryId:
              this.form.categoryId[this.form.categoryId.length - 1] === ""
                ? 0
                : this.form.categoryId[this.form.categoryId.length - 1],
          }
        );
        await updateFAQDesc(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            desc: this.form.desc,
          }
        );

        this.sureTime = [
          this.timeType(this.sureTime[0]),
          this.timeType(this.sureTime[1]),
        ];
        console.log(this.sureTime);

        await updateValidateTime(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            faqId: Number(this.form.faqId),
            validateTime: this.form.time === "default" ? "" : this.sureTime[0],
            expireTime: this.form.time === "default" ? "" : this.sureTime[1],
          }
        );
        this.$message.success("修改成功！");
      }
      this.dialogVisible = false;
      this.initValue();
      this.$emit("intiData");
      this.showCategory.categoryId = this.treeCategory.categoryId;
      if (this.treeCategory.categoryId == 0) {
        this.showCategory.categoryId = [""];
      }
      console.log("showCategory", this.showCategory.categoryId);
    },
    // 转换日期时间格式
    timeType(time) {
      let date1 = Date.parse(time);
      let date = new Date(date1);
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D =
        (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
      let h =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
      let m =
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        ":";
      let s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      let date2 = Y + M + D + h + m + s;
      return date2;
    },
    cancel() {
      this.dialogVisible = false;
      this.showCategory.categoryId = this.treeCategory.categoryId;
      if (this.treeCategory.categoryId == 0) {
        this.showCategory.categoryId = [""];
      }
    },
  },
  mounted() {
    this.showCategory.categoryId = [""];
    console.log("this.categoryId1", this.showCategory.categoryId);
  },
});
