import { reactive, ref } from "vue";
import IM from "./websocket";
import dayjs from "dayjs";
import { getHistory } from "@/api/deli/im";
export type typeEnum = "text" | "image" | "file" | "video" | "audio" | "link"; // 1文本 2图片，4文件
interface MsgType {
  type: typeEnum;
  content: string;
  is_custom: boolean;
  userName?: string;
  sendTime?: string;
  fileName?: string;
}

const visible = ref(false);
// 可能还需要加继承，后续实际情况在看
interface ChatType {
  list: MsgType[];
  loading: boolean;
}

const chatList: ChatType = reactive({
  list: [],
  loading: false,
});

const sendMessage = (msg: MsgType): void => {
  if (IM.IMInstance) {
    if (!msg.sendTime) {
      msg.sendTime = dayjs().format("YYYY-MM-DD HH:mm:ss");
    }
    IM.IMInstance.send(msg);
    chatList.list.push(msg);
  }
};

const getHistoryList = async (params: {
  customerName: string;
  groupId: string;
  limit: number;
}) => {
  chatList.list = [];
  const res: any = await getHistory(params);
  // for (let i = res.data.length - 1; i >= 0; i--) {
  //   res.data[i].records.forEach((item) => {
  //     chatList.list.push({
  //       type: item.contentType,
  //       content: item.content,
  //       is_custom: item.userType === 1 ? true : false,
  //       sendTime: dayjs(item.createTime).format("YYYY-MM-DD HH:mm:ss"),
  //       userName: item.userName,
  //     });
  //   });
  // }
  console.warn("res", res);
  const userList = res.data.result.usersList;
  res.data.result.messagesList.forEach((item) => {
    const matchUser = userList.find((u) => u.id === item.from);
    chatList.list.push({
      type: item.type,
      content: item.content,
      is_custom: matchUser && matchUser.type === 1 ? true : false,
      sendTime: dayjs(item.createTime).format("YYYY-MM-DD HH:mm:ss"),
      userName: matchUser.username,
    });
  });

  if (IM.defaultMessage && IM.defaultMessage.length > 0) {
    IM.defaultMessage.forEach((item) => {
      chatList.list.push({
        type: item.messageType,
        content: item.messageContent,
        is_custom: true,
        sendTime: dayjs(item.sendTime).format("YYYY-MM-DD HH:mm:ss"),
        userName: item.serverName,
      });
    });
  }
};

export { sendMessage, chatList, visible, getHistoryList };
