
import { defineComponent, reactive, ref } from "vue";
import { getBases, deleteBase, createBase, updateBase } from "@/api/deli/faq";
import { checkAuth } from "@/utils/help";
// import img1 from "@/assets/"
import dayjs from "dayjs";
import TitleCom from "@/components/title/index.vue";
export default defineComponent({
  components: {
    TitleCom,
  },
  setup() {
    const storeInfo: {
      lists: any[];
      loading: boolean;
    } = reactive({
      lists: [],
      loading: false,
    });
    const dialogVisible = ref(false);
    const current: {
      title: string;
      desc: string;
      baseId?: number;
    } = reactive({
      title: "",
      desc: "",
    });

    const rules = reactive({
      title: [{ required: true, message: "仓库名称必填", trigger: "blur" }],
    });

    return {
      storeInfo,
      dialogVisible,
      current,
      rules,
    };
  },
  methods: {
    dayjs,
    checkAuth,
    async getBasesList() {
      this.storeInfo.loading = true;
      const res = await getBases();
      this.storeInfo.loading = false;
      this.storeInfo.lists = res.data instanceof Array ? res.data : [];
    },
    selectStore(row) {
      this.$router.push({
        path: "storeDetail",
        query: {
          id: row.id,
          type: row.type,
          auth:
            row.name === "集团客服部知识库"
              ? "1,4"
              : row.name === "售后通用知识库"
              ? "1,4"
              : "1,2,4",
          name: row.name,
        },
      });
    },
    async deleteStore(id) {
      await deleteBase({
        baseId: id,
      });
      this.$message.success("删除成功!");
      this.getBasesList();
    },
    async addStore() {
      if (this.current.baseId) {
        await updateBase(
          {
            baseId: this.current.baseId,
          },
          this.current
        );
        this.$message.success("修改成功！");
      } else {
        await createBase(this.current);
        this.$message.success("创建成！");
      }
      this.current = { title: "", desc: "" };
      this.getBasesList();
    },
    async editStore(item) {
      this.current = {
        title: item.name,
        desc: item.desc,
        baseId: item.id,
      };
      this.dialogVisible = true;
    },
  },
  mounted() {
    this.getBasesList();
  },
});
