import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";

interface DeptParams {
  deptName: string;
  deptType: number | undefined;
}

interface DeptUser extends Service.PageInfo {
  deptId: number;
  roleId: number | string;
}

// 创建部门
export const createDept: (data: DeptParams) => Promise<Service.Result> = (
  data
) => {
  return Request({
    url: "/setting/create/dept",
    data,
    method: "post",
  });
};

// 创建子部门
export const createChildDept: (params: {
  deptName: string;
  parentDeptId: number;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/setting/create/dept/child",
    method: "post",
    params,
  });
};

// 查询部门树
export const getDeptTree: () => Promise<Service.Result> = () => {
  return Request({
    url: "/setting/dept/tree",
  });
};

// 分页查询部门用户
export const getDeptUser: (params: DeptUser) => Promise<Service.Result> = (
  params
) => {
  return Request({
    url: "/setting/dept/users",
    params,
  });
};

// 获取角色列表
export const getRoles: () => Promise<Service.Result> = () => {
  return Request({
    url: "/setting/roles",
  });
};

