type CommonWordsType = 1 | 2;
interface CommonWords {
  content: string;
  type: CommonWordsType;
}
// 创建常用语
export const createCommonWords: (
  params: CommonWords
) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/setting/common-words/create",
    method: "post",
    params,
  });
};

// 删除常用语
export const deleteCommonWords: (params: {
  id: number;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/setting/common-words/delete",
    method: "delete",
    params,
  });
};

// 获取可用的常用语
export const getCommonList: () => Promise<Service.Result> = () => {
  return Request({
    url: "/setting/common-words/list/my",
  });
};

// 更新常用语
export const updateCommonWords: (params: {
  content: string;
  id: number;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/setting/common-words/update",
    method: "put",
    params,
  });
};

