
import { defineComponent, reactive, ref } from "vue";
// import PageHeader from "@/components/pageHader/index";
import TitleCom from "@/components/title/index.vue";
import BuildCom from "./components/build.vue";
import FaqType from "./components/faqType.vue";
import ProductInfo from "@/views/assist/components/productInfo.tsx";
import RsvpPreviewImg from "@/components/rsvp-previewImg";
import {
  getFAQPage,
  getProductpage,
  deleteFAQ,
  trainFAQ,
  importFaq,
  exportFaq,
} from "@/api/deli/faq";
import dayjs from "dayjs";
import { checkAuth } from "@/utils/help";
export default defineComponent({
  components: {
    TitleCom,
    BuildCom,
    FaqType,
    ProductInfo,
    RsvpPreviewImg,
  },
  setup() {
    const defaultProps = reactive({
      children: "children",
      label: "label",
    });
    const pageInfo = reactive({
      pageNum: 1,
      pageSize: 10,
      categoryId: 0,
      minScore: 0.1,
      question: "",
    });
    const exportStart = ref(1);
    const exportEnd = ref(10);
    const productForm = reactive({
      productDesc: "",
      productCode: "",
    });
    const faqList = reactive({
      lists: [],
      loading: false,
    });
    const search = ref();
    const start = ref(false);
    const dom = ref<InstanceType<typeof BuildCom>>();
    const total = ref(0);
    const productInfo = ref();
    const visible = ref(false);
    const uploadDom = ref();
    const exportVisible = ref(false);
    const importVisible = ref(false);

    const preImgDom = ref();

    const categoryId = pageInfo;
    return {
      categoryId,
      defaultProps,
      search,
      start,
      dom,
      pageInfo,
      faqList,
      total,
      productInfo,
      visible,
      productForm,
      checkAuth,
      exportVisible,
      exportStart,
      exportEnd,
      uploadDom,
      importVisible,
      preImgDom,
    };
  },
  methods: {
    dayjs,
    handleNodeClick(val: any, ids) {
      (this.$refs.dom as InstanceType<typeof BuildCom>).form.categoryId = ids;
      this.pageInfo.categoryId = val.categoryId ? val.categoryId : 0;
      if (this.$route.query.type === "1") {
        this.getProductlist();
      } else {
        this.getFAQList();
      }
    },
    async getFAQList() {
      this.faqList.loading = true;
      let res: any = await getFAQPage(
        {
          baseId: Number(this.$route.query.id),
        },
        {
          ...this.pageInfo,
          question: encodeURIComponent(this.pageInfo.question),
        }
      );
      console.log("pageInfo", this.pageInfo.categoryId);

      this.faqList.loading = false;
      this.faqList.lists = (res.data as any).records;
      this.total = res.data.total;

      // console.log("res", res);
    },
    async getProductlist() {
      const res = await getProductpage(
        { baseId: Number(this.$route.query.id) },
        {
          ...this.productForm,
          ...this.pageInfo,
          productDesc: encodeURIComponent(this.productForm.productDesc),
        }
      );
      this.faqList.loading = false;
      this.faqList.lists = (res.data as any).records;
      this.total = (res.data as any).total;
    },
    searchReset() {
      this.productForm.productDesc = "";
      this.productForm.productCode = "";
      this.getProductlist();
    },
    searchOpt() {
      this.pageInfo.pageNum = 1;
      // console.log("123",this.$route.query.type);
      if (this.$route.query.type === "1") {
        this.getProductlist();
      } else {
        this.getFAQList();
      }
      console.log("faqList", this.faqList.lists);
    },
    changePage(page) {
      this.pageInfo.pageNum = page;
      if (this.$route.query.type === "1") {
        this.getProductlist();
      } else {
        this.getFAQList();
      }
    },
    showProduct(item) {
      console.log("item", item);
      this.productInfo = {
        ...item,
      };
      this.visible = true;
    },
    async deletFaq(id: number) {
      await deleteFAQ(
        { baseId: Number(this.$route.query.id) },
        {
          faqId: id,
        }
      );
      this.$message.success("删除成功！");
      this.getFAQList();
    },
    // 操做编辑方法
    editFaq(id, type) {
      (this.$refs.dom as InstanceType<typeof BuildCom>).getFaqDetail(id);
      (this.$refs.dom as InstanceType<typeof BuildCom>).status = type;
    },
    addFaq() {
      (this.$refs.dom as InstanceType<typeof BuildCom>).dialogVisible = true;
      (this.$refs.dom as InstanceType<typeof BuildCom>).initValue();
    },
    async trainFAQOpt() {
      await trainFAQ(
        {
          baseId: Number(this.$route.query.id),
        },
        {
          stage: "test",
        }
      );
      // 能训练的必然是FAQ
      this.$message.success("训练成功！");
      this.getFAQList();
    },
    async changeFile(file) {
      let data = new FormData();
      data.append("file", file.raw);
      (this.$refs.uploadDom as any).handleRemove(file);
      const mes = this.$message({
        message: "文件导入中，请稍后.....",
        type: "info",
        duration: 0,
      });
      try {
        const res: any = await importFaq(
          {
            baseId: Number(this.$route.query.id),
          },
          data
        );
        if (res.data.success) {
          this.$message.success("导入成功！");
          this.importVisible = false;
          this.getFAQList();
        } else {
          this.$message.error(res.data.errorMessage);
        }
        mes.close();
      } catch (error) {
        mes.close();
      }
    },
    async exportFaqData() {
      if (this.exportEnd > this.exportStart) {
        await exportFaq(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            start: this.exportStart === 1 ? 0 : this.exportStart,
            end: this.exportEnd,
          }
        );
        this.exportVisible = false;
      } else {
        this.$message.error("起始值需小于上限值！");
      }
    },
  },
  mounted() {
    // 产品
    if (this.$route.query.type === "1") {
      this.getProductlist();
      console.log(111);
    } else {
      this.getFAQList();
    }
  },
});
