
import { defineComponent, onMounted, reactive, ref } from "vue";
import { dialogVisible, createUser, userForm } from "./hook";
import { roles, getRoleList } from "../../set/hook";
import { deptList } from "../memberHook";
import { rules } from "./rules";
import { ElForm } from "element-plus";
import { checkAuth } from "@/utils/help";
export default defineComponent({
  setup() {
    const userDom = ref<InstanceType<typeof ElForm>>();
    onMounted(async () => {
      if (roles.list.length === 0) {
        await getRoleList();
      }
    });
    const defaultProps = reactive({
      label: "deptName",
      value: "deptId",
    });
    return {
      dialogVisible,
      userForm,
      roles,
      deptList,
      defaultProps,
      createUser,
      rules,
      userDom,
      checkAuth,
    };
  },
  computed: {
    getDeptList() {
      let list2: any[] = [];
      let list4: any[] = [];
      let list3: any[] = [];
      this.deptList.forEach((item) => {
        if (item.deptType === 1) {
          list4.push(item);
        } else {
          list2.push({
            ...item,
            children: [],
          });
          list3.push(item);
        }
      });
      if (this.userForm.userRoleId === 2) {
        return list2;
      } else if (this.userForm.userRoleId === 4) {
        return list4;
      } else {
        return list3;
      }
    },
  },
  methods: {
    changeRole(value) {
      if (value === 4) {
        this.userForm.deptId = [this.getDeptList[0].deptId] as any;
      } else {
        this.userForm.deptId = [];
      }
    },
    async submitUser() {
      (this.$refs.userDom as InstanceType<typeof ElForm>).validate(
        async (valid) => {
          if (valid) {
            await this.createUser();
            this.$emit("refresh");
          }
        }
      );
    },
  },
});
