
import { defineComponent, ref } from "vue";
import { getIMServerTime, setIMServerTime } from "@/api/deli/setting";
import RsvpWrapper from "@/components/rsvp-wrapper/index.vue";
import TitleCom from "@/components/title/index.vue";
import { checkAuth } from "@/utils/help";
const week = [
  "monday",
  "tuesday",
  "wednesday",
  "thursday",
  "friday",
  "saturday",
  "sunday",
];
export default defineComponent({
  components: {
    RsvpWrapper,
    TitleCom,
  },
  setup() {
    const status = ref(false);
    const checkList = ref<number[]>([]);
    const time = ref<any[]>([]);
    const num = ref();
    const killTime = ref(0);
    const textarea = ref();
    const text = ref("");
    return {
      status,
      checkList,
      time,
      num,
      killTime,
      checkAuth,
      textarea,
      text,
    };
  },
  methods: {
    async getInitData() {
      const user = JSON.parse(localStorage.getItem("user") as string);
      const res: any = await getIMServerTime({
        deptId: user.parentDeptId === 0 ? user.deptId : user.parentDeptId,
      });
      this.status = res.data.result.state;
      let arr: any = [];
      week.forEach((item, idx) => {
        if (res.data.result[item]) {
          arr.push(idx);
        }
      });
      this.checkList = arr;
      this.num = res.data.result.max_size;
      this.time = [
        new Date(
          2016,
          9,
          10,
          res.data.result.start_hh,
          res.data.result.start_mm
        ),
        new Date(2016, 9, 10, res.data.result.end_hh, res.data.result.end_mm),
      ];
      this.textarea = res.data.result.content;
    },
    async save() {
      const temp = {};
      const userInfo = JSON.parse(localStorage.getItem("user") as string);
      week.forEach((item, idx) => {
        if (this.checkList.indexOf(idx) !== -1) {
          temp[item] = true;
        } else {
          temp[item] = false;
        }
      });
      const res: any = await setIMServerTime({
        content: this.textarea,
        deptId: userInfo.deptId,
        end_hh: this.time[1].getHours(),
        end_mm: this.time[1].getMinutes(),
        start_hh: this.time[0].getHours(),
        start_mm: this.time[0].getMinutes(),
        max_size: this.num,
        state: 1,
        ...temp,
      });
      if (res.data.code === 20000) {
        this.$message.success("保存成功！");
      } else {
        this.$message.error(res.data.msg);
      }
    },
  },
  mounted() {
    this.getInitData();
  },
});
