import { defineComponent, toRefs, ref } from "vue";
import "./index.scss";
export default defineComponent({
  props: {
    currentProduct: {
      type: Object,
      default() {
        return {};
      },
    },
    isStore: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["previewImg"],
  setup(props: any, content) {
    const { currentProduct, isStore } = toRefs(props);
    const showValue = ref(["1", "2", "3", "4", "5"]);
    return () => (
      <>
        <div class="diy-modal2">
          {isStore.value ? null : (
            <div>
              <p class="productDesc ac sb">
                <span class="ellipsis">
                  {currentProduct.value.productDesc}[
                  {currentProduct.value.productTypeCode}]
                </span>

                {/* <i class="f-r iconfont icon-fenxiang_2"></i> */}
              </p>
              <div class="m-t-10 font-12">
                <el-row>
                  <el-col span={12}>
                    <el-button
                      type="primary"
                      class="el-button--small m-r-15"
                      plain
                    >
                      {currentProduct.value.productPriceType === "1"
                        ? "出厂价"
                        : currentProduct.value.productPriceType === "2"
                        ? "一口价"
                        : "实价"}
                    </el-button>
                    <span class="font-300 font-14 fineTuning">
                      {currentProduct.value.productPrice && "￥"}
                      {currentProduct.value.productPrice}
                    </span>
                  </el-col>
                  <el-col span={12}>
                    <el-button
                      type="primary"
                      class="el-button--small m-r-15"
                      plain
                    >
                      货号
                    </el-button>
                    <span class="font-300 font-14 fineTuning">
                      {currentProduct.value.cargoNumber}
                    </span>
                  </el-col>
                </el-row>
              </div>
            </div>
          )}
          <el-collapse v-model={showValue.value}>
            <el-collapse-item title="产品参数" name="1">
              <ul>
                {currentProduct.value.productAttribute &&
                  Object.keys(currentProduct.value.productAttribute).map(
                    (item) => {
                      return (
                        <li
                          v-show={
                            currentProduct.value.productAttribute[item] !== ""
                          }
                        >
                          <p>{item}</p>
                          <p class="price">
                            {currentProduct.value.productAttribute[item]}
                          </p>
                        </li>
                      );
                    }
                  )}
              </ul>
              {!currentProduct.value.productAttribute && (
                <el-empty description="暂无产品参数" />
              )}
            </el-collapse-item>
            <el-collapse-item title="基本属性" name="2">
              <ul>
                <li>
                  <p>物料编码</p>
                  <p class="price">{currentProduct.value.productCode || "/"}</p>
                </li>
                <li>
                  <p>物料组</p>
                  <p class="price">{currentProduct.value.productType || "/"}</p>
                </li>
                <li>
                  <p>外部物料组</p>
                  <p class="price">
                    {currentProduct.value.externalProductType || "/"}
                  </p>
                </li>
                <li>
                  <p>产品特性</p>
                  <p class="price">
                    {currentProduct.value.proCharacteristics || "/"}
                  </p>
                </li>
                <li style={"width:100%"}>
                  <p>新品有效期</p>
                  <p class="flex ">
                    <div class="price" style={"width:50%"}>
                      {currentProduct.value.validFromDate || "/"}
                    </div>
                    <div class="price" style={"width:50%"}>
                      {currentProduct.value.validToDate || "/"}
                    </div>
                  </p>
                </li>
                <li style={"width:100%"}>
                  <p>产品标签</p>
                  <p class="price">
                    {currentProduct.value.productLabel || "/"}
                  </p>
                </li>
                <li style={"width:100%"}>
                  <p>条码/体积/重量</p>
                  {currentProduct.value.uoms &&
                    currentProduct.value.uoms.map((item) => {
                      return (
                        <div class="flex">
                          <span class="flex-1 ta-center m-t-5">
                            {item.newPackingUom || "/"}
                          </span>
                          <span class="flex-1 price">
                            {item.newPackingCode || "/"}
                          </span>
                          <span class="flex-1 price">
                            {item.newGrossWeight || "/"}
                          </span>
                          <span class="flex-1 price">
                            {item.newVolume || "/"}
                          </span>
                        </div>
                      );
                    })}
                  {(!currentProduct.value.uoms ||
                    currentProduct.value.uoms.length === 0) &&
                    "/"}
                </li>
                <li style={"width:100%"}>
                  <p>包装规格/装箱配比</p>
                  {currentProduct.value.uoms &&
                    currentProduct.value.uoms.map((item) => {
                      return (
                        <div class="flex">
                          <span class="flex-1 ta-center m-t-5">
                            {item.newPackingUom || "/"}
                          </span>
                          <span class="flex-1 price">
                            {item.newMolecule || "/"}
                          </span>
                          <span class="flex-1 price">
                            {item.newPackingRatio || "/"}
                          </span>
                          <span class="flex-1 price"></span>
                        </div>
                      );
                    })}
                  {(!currentProduct.value.uoms ||
                    currentProduct.value.uoms.length === 0) &&
                    "/"}
                </li>
              </ul>
            </el-collapse-item>
            <el-collapse-item title="其他" name="3">
              <ul>
                <li>
                  <p>总仓库存</p>
                  <p class="price">/</p>
                </li>
                <li>
                  <p>集团销量</p>
                  <p class="price">{currentProduct.value.salesGroup}</p>
                </li>
                <li>
                  <p>省区库存</p>
                  <p class="price">/</p>
                </li>
                <li>
                  <p>省区销量</p>
                  <p class="price">{currentProduct.value.salesProvince}</p>
                </li>
              </ul>
            </el-collapse-item>
            <el-collapse-item title="图片" name="4">
              {currentProduct.value?.productPicUrls &&
              currentProduct.value?.productPicUrls.length !== 0 ? (
                <el-row gutter={20} class="m-t-20">
                  {currentProduct.value?.productPicUrls.map((item, idx) => {
                    return (
                      <el-col span={6} class="m-b-10">
                        <div
                          class="pointer flex ac"
                          style={
                            "width:100%;height:140px;background:#fff;overflow:hidden;justify-content:center"
                          }
                          onClick={() => content.emit("previewImg", idx)}
                        >
                          <img
                            src={item}
                            class="pointer"
                            style={"max-width:100%;width:auto;max-height:140px"}
                            alt=""
                          />
                        </div>
                      </el-col>
                    );
                  })}
                </el-row>
              ) : (
                <el-empty
                  image={require("@/assets/no-img.png")}
                  image-size={100}
                  description="暂无图片"
                ></el-empty>
              )}
            </el-collapse-item>
            <el-collapse-item title="关联产品" name="5">
              {currentProduct.value.productAssociatedDetails &&
                currentProduct.value.productAssociatedDetails.map((item) => {
                  return (
                    item && (
                      <ul>
                        <li v-show={item.productDesc}>
                          <p>产品名称</p>
                          <p class="price">{item.productDesc}</p>
                        </li>
                        <li v-show={item.productType}>
                          <p>类型</p>
                          <p class="price">{item.productType}</p>
                        </li>
                      </ul>
                    )
                  );
                })}
            </el-collapse-item>
          </el-collapse>
        </div>
      </>
    );
  },
});
