
import {
  defineComponent,
  onMounted,
  reactive,
  ref,
  watchEffect,
  watch,
} from "vue";
import {
  storeVisible,
  initData,
  categoryInfo,
  filter,
  search,
  productInfo,
} from "./storeHook";
import { getUserId, sendMsg } from "../imhook";
import { getMainScreen } from "@/api/deli/mainScreen";
import { ArrowLeft } from "@element-plus/icons";
import RsvpPreviewImg from "@/components/rsvp-previewImg";
import ProductInfo from "./productInfo.tsx";
import { userAccountInfo } from "./userHook";
export default defineComponent({
  components: {
    ArrowLeft,
    RsvpPreviewImg,
    ProductInfo,
  },
  setup() {
    onMounted(() => {
      initData();
    });
    const props = reactive({
      label: "categoryTitle",
      value: "categoryId",
      checkStrictly: true,
    });
    const showDetail = ref({ productPicUrls: [0] });
    const showVisible = ref(false);
    const num = ref("");
    const preImgDom = ref<InstanceType<typeof RsvpPreviewImg>>();
    watchEffect(() => {
      if (storeVisible.value) {
        showVisible.value = false;
      }
    });
    return {
      storeVisible,
      categoryInfo,
      props,
      filter,
      search,
      productInfo,
      showDetail,
      showVisible,
      num,
      preImgDom,
      sendMsg,
      userAccountInfo,
    };
  },
  methods: {
    async showProduct(row) {
      this.showVisible = true;
      const imgList = row.productPicUrls;
      this.showDetail = {
        ...row,
        productPicUrls: row.productDetailPicUrl
          ? imgList.concat([row.productDetailPicUrl])
          : row.productPicUrls,
      };
      console.log("wer", row);
      if (getUserId()) {
        const res: any = await getMainScreen({
          imUserId: getUserId(),
          productCode: row.productCode,
        });
        this.num = res.data?.amount as string;
      }
    },
    changePage(page) {
      this.filter.pageNum = page;
      this.search();
    },
  },
});
