import { ref, reactive } from "vue";
import { sendOfflineMessage, getOfflineStatus } from "@/api/deli/activeTouch";
import { getHistoryOne, currentGroupId, chatList } from "../imhook";
import { ElMessage } from "element-plus";
import { getImUserId } from "@/api/deli/im";
import { getConversationList } from "@/api/deli/conversation";
import dayjs from "dayjs";
import { ElNotification } from "element-plus";
// 客户列表,用户的信息，包含staffCode, userId, list
const userAccountInfo = reactive({
  loading: false,
  isAccount: false,
  staffCode: "",
  staffName: "",
  userId: "",
  list: [],
});

export const serveName = ref();

const offlineIds = ref<string[]>([]);
const offlineTimer = ref<NodeJS.Timer | null>(null);

const sendMessage = async (content: string): Promise<void> => {
  try {
    if (userAccountInfo.staffCode) {
      const res: any = await sendOfflineMessage({
        content: content,
        staffCodeList: [userAccountInfo.staffCode],
      });
      if (res.data.errorMsg.length > 0) {
        ElNotification({
          message: res.data.errorMsg.join(","),
          duration: 0,
          type: "error",
        });
        return;
      }
      offlineIds.value = [
        ...new Set([...offlineIds.value, res.data.messageIds]),
      ];
      if (!offlineTimer.value) {
        offlineTimer.value = setInterval(() => {
          checkOfflineStatus();
        }, 1000);
      }
    }
  } catch (error) {
    console.error("sendOfflineMessage", error);
    clearInterval(offlineTimer.value as NodeJS.Timer);
    offlineTimer.value = null;
  }
};

const checkOfflineStatus = async () => {
  const result: any = await getOfflineStatus({
    ids: offlineIds.value.join(","),
  });

  const error = result.data.filter(
    (item) => item === "CONSUME_FAIL" || item === "USER_INVALID"
  );

  const ids: number[] = [];
  result.data.forEach((item, idx) => {
    if (item === "SUCCESS" || item === null) {
      ids.push(idx);
    }
  });

  if (ids.length > 0) {
    getHistoryData();
    offlineIds.value = offlineIds.value.filter(
      (item, idx) => ids.indexOf(idx) === -1
    );
  }
  if (offlineIds.value.length === 0) {
    clearOfflineTimer();
    ElMessage.success("发送成功！");
  }

  if (error.length > 0) {
    clearOfflineTimer();
    ElMessage.error("发送失败！");
  }
};

const getServeName = async () => {
  if (userAccountInfo.staffCode) {
    const result = await getImUserId({
      staffCode: userAccountInfo.staffCode,
    });
    const res: any = await getConversationList({
      customerId: result.data,
      endTime: dayjs(dayjs().format("YYYY-MM-DD") + " 23:59:00").format(
        "YYYY-MM-DDTHH:mm:ss.SSS[Z]"
      ),
      startTime: dayjs(dayjs().format("YYYY-MM-DD") + " 00:00:00").format(
        "YYYY-MM-DDTHH:mm:ss.SSS[Z]"
      ),
      groupId: 1,
      page: 1,
      limit: 1,
    });
    serveName.value = JSON.parse(
      localStorage.getItem("user") as string
    ).userName;
    if (
      res.data.result.rows.length > 0 &&
      res.data.result.rows[0].status !== 1
    ) {
      serveName.value = res.data.result.rows[0].staffName;
    }
  }
};

const getHistoryData = () => {
  getHistoryOne(
    {
      linkmanId: userAccountInfo.userId,
      existCount: 0,
    },
    (list) => {
      userAccountInfo.loading = false;
      currentGroupId.value = "";
      if(!list) {
        list = []
      }
      userAccountInfo.list = list.map((item) => ({
        ...item,
        speak: item.from.type,
      }));
      const index = chatList.value.findIndex(
        (item) => parseInt(item.userId) === parseInt(userAccountInfo.userId)
      );
      if (index !== -1) {
        chatList.value[index].list = [...userAccountInfo.list];
      }
      getServeName();
    }
  );
};

const clearOfflineTimer = () => {
  clearInterval(offlineTimer.value as NodeJS.Timer);
  offlineTimer.value = null;
  offlineIds.value = [];
};

export { userAccountInfo, sendMessage, getHistoryData, clearOfflineTimer };
