import { ref } from "vue";
import { getNotice } from "@/api/assist";
import { logout as userlogout } from "@/api/deli/sys";
import { setStatus } from "@/api/deli/im";
import { ImUser, close, initIM, endAllsession } from "@/views/assist/imhook";
import { clearHeart } from "@/hook/checktUser";
// import { errorLog } from "@/api/error";
// import { logout as userlogout } from "@/api/user";
type IMStatus = 1 | 2;
const noticeNum = ref(0);
const getNoticeInfo: () => void = async () => {
  const res = await getNotice();
  noticeNum.value = res.data instanceof Array ? res.data.length : 0;
};

const logout: (str?: string) => void = async (str) => {
  console.log("说的分开解释的话给客户");
  if (window.location.href.indexOf("login") === -1) {
    try {
      await setStatus({
        status: 2,
      });
      endAllsession();
      await userlogout();
    } catch (error) {
      console.log(error);
      window.location.href = "/login";
    }
  }
  if ((window as any).apiKey && str === "app") {
    (window as any).apiKey.quit();
  }
  close();
  clearHeart();
  localStorage.removeItem("user");
  // localStorage.removeItem("token");
  window.location.href = "/login";
};

const appLogout = () => {
  logout("app");
};

const setIMStatus: () => void = async () => {
  console.log("change");
  if (ImUser.value) {
    const res: any = await setStatus({
      status: ImUser.value.online ? 2 : 1,
    });
    console.log("ImUser", ImUser.value);
    if (res.data) {
      ImUser.value.online = !ImUser.value.online;
      initIM();
    }
  }
};

export { noticeNum, getNoticeInfo, logout, setIMStatus, appLogout };
