
import { defineComponent, reactive, ref } from "vue";
import TitleCom from "@/components/title/index.vue";
import RsvpWrapper from "@/components/rsvp-wrapper/index.vue";
import { getTemplateList, sendTemplateMessage } from "@/api/deli/activeTouch";
import type { ElTable } from "element-plus";
import Title1Com from "./components/title.vue";
import { getAccountsByQuery } from "@/api/deli/im";
import { prefix } from "@/utils/request";
import { ElNotification } from "element-plus";
export default defineComponent({
  components: {
    TitleCom,
    RsvpWrapper,
    Title1Com,
  },
  setup() {
    const isIndeterminate = ref(false);
    const keyword = ref("");
    const rowSelect = ref<any[]>([]);
    const userList = reactive({
      list: [],
      loading: false,
    });
    const importVisible = ref(false);
    const selectList = ref<string[]>([]);
    const multipleTableRef = ref<InstanceType<typeof ElTable>>();
    const currentTemplate = ref();
    const remark = ref();
    const searchType = ref();
    const headers = reactive({
      token: JSON.parse(localStorage.getItem("user") as string).token,
    });
    return {
      isIndeterminate,
      getTemplateList,
      keyword,
      userList,
      multipleTableRef,
      currentTemplate,
      remark,
      selectList,
      rowSelect,
      searchType,
      headers,
      prefix,
      importVisible,
    };
  },
  methods: {
    async getUser() {
      this.userList.loading = true;
      const config = {
        sapName: "",
        sapNumber: "",
        staffName: "",
      };
      if (this.searchType) {
        config[this.searchType] = this.keyword;
      }
      const res: any = await getAccountsByQuery(config);
      this.userList.list = res.data;
      this.userList.loading = false;
    },
    async sendTemplateMessage() {
      if (this.selectList.length === 0) {
        this.$message.error("请先选择用户！");
        return;
      }
      if (!this.remark) {
        this.$message.error("请先填写备注信息！");
        return;
      }
      const res: any = await sendTemplateMessage({
        content: this.remark,
        toUserList: this.selectList,
      });
      if (res.data.errorMsg && res.data.errorMsg.length > 0) {
        ElNotification({
          message: res.data.errorMsg.join(","),
          duration: 0,
          type: "error",
        });
        return;
      }
      this.$message.success("发送成功！");
    },
    handleSelectionChange(val) {
      this.rowSelect = val;
      this.selectList = [];
      val.forEach((item) => {
        this.selectList = this.selectList.concat(
          item.staffList.map((s) => s.staffCode)
        );
      });
    },
    uploadSuccess(res) {
      if (res.code === 0) {
        this.$message.success("导入文件成功！");
        this.importVisible = false;
        res.data.accountResponseList.forEach((item) => {
          this.selectList = this.selectList.concat(
            item.staffList.map((s) => s.staffCode)
          );
        });
        this.getUser();
      }
    },
  },
  mounted() {
    this.getUser();
  },
});
