import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";
import { method } from "lodash";

// 获取我的排班
export const getMineCalendar = (params) => {
  return Request({
    url: "/attendance/arrange/my",
    params,
  });
};

// 获取指定用户列表
export const getHelpers = (params) => {
  return Request({
    url: "/attendance/available/helpers",
    params,
  });
};

// 创建排班
export const createCalendar = (data) => {
  return Request({
    url: "/attendance/create/arrange",
    method: "post",
    data,
  });
};

// 根据日期查询部门排班信息
export const getUserCalendar = (params) => {
  return Request({
    url: "/attendance/date/arrange/users",
    params,
  });
};

// 更新排班接口
export const updateCalendar = (data) => {
  return Request({
    url: "/attendance/update/arrange",
    method: "put",
    data,
  });
};

// 根据时间断获取排班信息
export const getAllCalendar = (params) => {
  return Request({
    url: "/attendance/dates/arrange",
    params,
  });
};
