
import { defineComponent, ref, watchEffect, toRefs } from "vue";
import { uploadFile } from "@/api/deli/im";
import { sendMsg } from "@/views/assist/imhook";
import { wexinEmo } from "@/utils/emo";
import { ElMessage } from "element-plus";
export default defineComponent({
  props: ["modelValue", "disabled", "placeholder", "isAccount"],
  emits: ["update:modelValue", "mouseEnter", "keyup", "blur"],
  setup(props: { disabled: boolean }) {
    const textDom = ref<HTMLDivElement>();
    const { disabled } = toRefs(props);
    const isPin = ref(false);
    watchEffect(() => {
      if (disabled.value) {
        textDom.value &&
          (textDom.value as HTMLDivElement).setAttribute(
            "contenteditable",
            "false"
          );
      } else {
        textDom.value &&
          (textDom.value as HTMLDivElement).setAttribute(
            "contenteditable",
            "true"
          );
        textDom.value && (textDom.value as HTMLDivElement).focus();
      }
    });
    return {
      textDom,
      isPin,
    };
  },
  methods: {
    moveEnd() {
      var selection = window.getSelection();
      var range: any = document.createRange();
      range.selectNodeContents(this.$refs.textDom as HTMLDivElement);
      range.collapse(false);
      selection && selection.removeAllRanges();
      selection && selection.addRange(range);
    },
    keydown(event) {
      if (event.keyCode === 13 && !event.shiftKey) {
        event.preventDefault();
      }
    },
    keyup(event) {
      if (
        event.keyCode === 13 &&
        event.key === "Enter" &&
        !event.shiftKey &&
        !event.ctrlKey
      ) {
        event.preventDefault();
        event.stopPropagation();
        event.cancelBubble = true;
        // console.log("呼呼玩儿哦i", this.modelValue);
        // this.$emit("mouseEnter", this.modelValue);
        this.sendRichMsg();
        return false;
      }
      if (this.isPin) {
        if (event.code === "Space") {
          console.log("中文", (this.$refs.textDom as HTMLDivElement).innerText);
          setTimeout(() => {
            this.$emit(
              "update:modelValue",
              (this.$refs.textDom as HTMLDivElement).innerText
            );
            this.$emit("keyup", event);
          }, 10);
        }
      } else {
        setTimeout(() => {
          this.$emit(
            "update:modelValue",
            (this.$refs.textDom as HTMLDivElement).innerText
          );
          this.$emit("keyup", event);
        }, 10);
      }
    },
    initData() {
      this.$nextTick(() => {
        const RichTextareaDom = this.$refs.textDom as HTMLDivElement;
        RichTextareaDom && (RichTextareaDom.innerHTML = this.modelValue);
        this.moveEnd();
        // 图片上传
        this.listenImgUpload();
        // 监听输入法
        RichTextareaDom.addEventListener("compositionstart", () => {
          this.isPin = true;
        });
        RichTextareaDom.addEventListener("compositionend", () => {
          this.isPin = false;
        });
      });
    },
    listenImgUpload() {
      (this.$refs.textDom as HTMLDivElement).addEventListener(
        "paste",
        async (e) => {
          e.preventDefault();
          e.stopPropagation();

          let items = e.clipboardData?.items || [];
          for (let i = 0; i < items?.length; i++) {
            const item = items[i];
            if (item.kind === "string" && item.type === "text/plain") {
              item.getAsString(function (str) {
                document.execCommand("insertHTML", true, str);
              });
            } else if (
              item.kind === "file" &&
              item.type.indexOf("image") !== -1 &&
              !this.isAccount
            ) {
              const pasteFile = item.getAsFile();
              if (pasteFile && pasteFile.size < 4 * 1024 * 1024) {
                const form = new FormData();
                form.append("file", pasteFile);
                const res = await uploadFile(
                  {
                    groupId: 1,
                  },
                  form
                );
                if (res.code === 0) {
                  document.execCommand(
                    "insertHTML",
                    true,
                    `<img src=${res.data} style="max-width: 240px; max-height: 240px;vertical-align: bottom"></img>`
                  );
                }
              }
            }
          }
        }
      );
    },
    setText(text) {
      const dom = this.$refs.textDom as HTMLDivElement;
      dom.innerText = text;
    },
    setHtml(html) {
      const dom = this.$refs.textDom as HTMLDivElement;
      dom.innerHTML = html;
    },
    getHtml() {
      const dom = this.$refs.textDom as HTMLDivElement;
      return dom.innerHTML;
    },
    getText() {
      // 这个需要处理一下，返回数组，【文本，图片，文本】
      const dom = this.$refs.textDom as HTMLDivElement;
      return dom.innerText;
    },
    insertHtml(html) {
      document.execCommand("insertHTML", true, html);
    },
    sendRichMsg() {
      const text = this.getText();
      if (text.length > 500) {
        ElMessage.error("内容不能超过500个字");
        return;
      }
      console.log("this.isEmpty()", this.isEmpty());
      if (this.isEmpty()) {
        ElMessage.error("内容不能为空！");
        return;
      }

      const dom = this.$refs.textDom as HTMLDivElement;
      let m = "";
      let arr: any = [];
      for (let i = 0; i < dom.childNodes.length; i++) {
        // console.log("dom", dom[i]);
        if (dom.childNodes[i].nodeName === "IMG") {
          const c = dom.childNodes[i] as HTMLImageElement;
          if (c.className.indexOf("qqface") !== -1) {
            // @ts-ignore
            const index = c.attributes.data.value;
            m += Object.keys(wexinEmo)[index];
          } else {
            if (m) {
              arr.push({
                type: "text",
                content: m,
              });
            }
            m = "";
            arr.push({
              type: "image",
              content: (dom.childNodes[i] as any).currentSrc,
            });
          }
        } else {
          m +=
            dom.childNodes[i].nodeName === "#text"
              ? (dom.childNodes[i] as any).data
              : (dom.childNodes[i] as any).innerText;
        }
      }
      if (m) {
        arr.push({
          type: "text",
          content: m,
        });
      }
      arr.forEach((item) => {
        if (item.type === "image") {
          sendMsg({
            type: "image",
            url: item.content,
          });
        } else {
          if (item.content.replaceAll(" ", "")) {
            this.$emit("mouseEnter", item.content);
          }
        }
      });
      console.log("arr", arr);
      dom.innerHTML = "";
    },
    isEmpty() {
      const html = this.getHtml();
      let result = true;
      if (html.indexOf("img") !== -1 && html.indexOf("src") !== -1) {
        result = false;
      } else {
        const text = this.getText();
        if (text.replaceAll(" ", "").replaceAll("\n", "")) {
          result = false;
        }
      }
      return result;
    },
    focus() {
      const RichTextareaDom = this.$refs.textDom as HTMLDivElement;
      RichTextareaDom && RichTextareaDom.focus();
      this.moveEnd();
    },
  },
  mounted() {
    this.initData();
  },
});
