import { defineComponent, onMounted, reactive, ref, watch } from "vue";
import {
  getPersonCommon,
  getDeptTree,
  getPersonDetail,
} from "@/api/deli/setting";

export default defineComponent({
  setup() {
    const filter = reactive({
      pageNum: 1,
      pageSize: 10,
      parentDeptId: "",
      deptId: "",
      keyword: "",
    });

    const commonInfo = reactive({
      list: [],
      loading: false,
      total: 0,
    });

    const options = ref([]);
    const ids = ref([]);
    const getList = async () => {
      commonInfo.loading = true;
      console.log(filter);
      const res: any = await getPersonCommon(filter);
      commonInfo.list = res.data.records;
      commonInfo.total = res.data.total;
      commonInfo.loading = false;
    };
    const searchList = () => {
      filter.pageNum = 1;
      filter.parentDeptId = ids.value.length > 0 ? ids.value[0] : "";
      filter.deptId = ids.value.length === 2 ? ids.value[1] : "";
      getList();
    };
    onMounted(async () => {
      const res: any = await getDeptTree();
      options.value = res.data.deptList;
      getList();
    });

    const dialogVisible = ref(false);
    const list = ref([]);
    const showDetail = async (userId: number) => {
      dialogVisible.value = true;
      const res: any = await getPersonDetail({
        userId,
      });
      list.value = res.data;
    };

    return () => (
      <>
        <el-space>
          <rsvp-cascader
            options={options.value}
            props={{ checkStrictly: true, label: "deptName", value: "deptId" }}
            clearable
            v-model={ids.value}
          />
          <el-input
            placeholder="坐席姓名或工号"
            v-model={filter.keyword}
          ></el-input>
          <el-button type="primary" onClick={searchList}>
            搜索
          </el-button>
        </el-space>
        <div class="m-t-20">
          <el-table data={commonInfo.list} style="width: 100%">
            <el-table-column prop="workId" label="坐席工号" />
            <el-table-column prop="userName" label="坐席姓名" />
            <el-table-column prop="parentDeptName" label="所属客服部" />
            <el-table-column prop="deptName" label="所属分公司" />
            <el-table-column prop="count" label="常用语条数" />
            <el-table-column
              label="操作"
              v-slots={{
                default: (scope) => (
                  <el-link
                    type="primary"
                    underline={false}
                    onClick={() => showDetail(scope.row.userId)}
                  >
                    详情
                  </el-link>
                ),
              }}
            />
          </el-table>
        </div>
        <div class="m-t-20">
          {
            <el-pagination
              background
              layout="prev, pager, next"
              total={commonInfo.total}
              class="f-r"
              // hide-on-single-page
              v-model={filter.pageNum}
              onCurrentChange={(page) => {
                filter.pageNum = page;
                getList();
              }}
            />
          }
        </div>
        <el-dialog v-model={dialogVisible.value} title="常用语详情" width="30%">
          <el-table
            data={list.value}
            show-header={false}
            style="width: 100%"
            onRowClick={(e) => console.log(e)}
          >
            <el-table-column
              label="常用语"
              v-slots={{ default: (scope) => scope.row }}
            ></el-table-column>
          </el-table>
        </el-dialog>
      </>
    );
  },
});
