
import { defineComponent, ref, inject, defineProps } from "vue";
import RecomardCom from "./recomard.tsx";
import UserPortait from "./userPortrait.tsx";
import { getUserId } from "../imhook";
import { getOrderList } from "@/api/deli/mainScreen";
import { userAccountInfo } from "./userHook";
import dayjs from "dayjs";
export default defineComponent({
  components: {
    RecomardCom,
    UserPortait,
  },
  props: {
    isWx: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const activeName = ref(props.isWx ? "user" : "recommend");
    const orderList = ref([]);
    const userDom = ref<InstanceType<typeof UserPortait>>();
    const recomardDom = ref<InstanceType<typeof RecomardCom>>();
    const wxorder = inject("wxorder");
    return {
      activeName,
      orderList,
      userDom,
      recomardDom,
      userAccountInfo,
      wxorder,
    };
  },
  methods: {
    dayjs,
    async getOrder() {
      if (this.userAccountInfo.isAccount || getUserId()) {
        const res = await getOrderList({
          imUserId: this.userAccountInfo.userId || getUserId(),
        });
        this.orderList = res.data as any;
      }
    },
    async getAllData() {
      this.getOrder();
      (this.$refs.userDom as InstanceType<typeof UserPortait>).getUser();
      (this.$refs.userDom as InstanceType<typeof UserPortait>).getHot();
    },
  },
  watch: {
    userAccountInfo: {
      handler(n) {
        if (n.isAccount) {
          this.activeName = "user";
        } else {
          this.activeName = "recommend";
        }
      },
      deep: true,
    },
  },
});
