import { defineComponent, ref } from "vue";
import { ArrowLeft, ArrowRight } from "@element-plus/icons";
export default defineComponent({
  props: {
    isPre: {
      type: Boolean,
      default: false,
    },
    isAccount: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["sendMsg"],
  setup() {
    const imgPreview = ref(false);
    const currentIndex = ref(0);
    const productPicUrls = ref<string[]>([]);
    const open = (config: { index: number; list: string[] }) => {
      imgPreview.value = true;
      currentIndex.value = config.index;
      productPicUrls.value = config.list;
    };
    return {
      imgPreview,
      currentIndex,
      productPicUrls,
      open,
    };
  },
  render() {
    return (
      <el-dialog
        v-model={this.imgPreview}
        title={this.isPre ? "预览图片" : "发送图片"}
        width="420px"
        custom-class="diy-modal"
        onClosed={() => this.currentIndex}
        v-slots={{
          footer: () => {
            if (this.isPre || this.isAccount) {
              return "";
            } else {
              return (
                <el-button
                  type="primary"
                  onClick={() => {
                    this.$emit("sendMsg", {
                      type: "image",
                      url: this.productPicUrls[this.currentIndex],
                    });
                    this.imgPreview = false;
                  }}
                >
                  发送
                </el-button>
              );
            }
          },
        }}
      >
        <div class="preview-img-wrapper">
          {this.currentIndex !== 0 ? (
            <div
              class="left-tip tip pointer"
              onClick={(e) => {
                this.currentIndex -= 1;
              }}
            >
              <el-icon size="30">
                <ArrowLeft />
              </el-icon>
            </div>
          ) : null}

          <img
            src={this.productPicUrls[this.currentIndex]}
            style={"max-width: 100%;pointer-events: none;user-select: none;"}
            onClick={(e) => e.preventDefault()}
            alt=""
          />
          {this.currentIndex !== this.productPicUrls.length - 1 ? (
            <div
              class="right-tip tip pointer"
              onClick={(e) => {
                this.currentIndex += 1;
              }}
            >
              <el-icon size="30">
                <ArrowRight />
              </el-icon>
            </div>
          ) : null}
        </div>
      </el-dialog>
    );
  },
});
