import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";

//  会话列表
export const getConversationList: (data: any) => Promise<Service.Result> = (
  data
) => {
  return Request({
    url: "api/conversation/list",
    method: "post",
    data,
  });
};

// 会话详情
export const getConversationInfo: (params: {
  groupId: number;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "api/conversation/detail",
    params,
  });
};

// 统计导出文件接口
export const exportConversation = (data) => {
  return Request({
    url: "/api/conversation/export/download",
    method: "post",
    data,
    responseType: "blob",
  });
};

// 查询用户归属公司和id
export const getSapInfo = (data) => {
  return Request({
    url: "/api/conversation/sap/info",
    method: "post",
    data,
  });
};

// 更新客户账户
export const updateCustomer = (data) => {
  return Request({
    url: "/accounts/update/incremental",
    method: "post",
    data,
  });
};
