import { defineComponent, ref } from "vue";
import { createUseStyles } from "vue-jss";
import { Camera, Folder } from "@element-plus/icons";
import { typeEnum, visible } from "../chatHook";
import { uploadFile } from "@/api/deli/im";
import { sendMessage } from "../chatHook";
import { ElMessage } from "element-plus";

const useStyles = createUseStyles({
  uplaodWrapper: {
    width: "40px",
    height: "40px",
    borderRadius: "10%",
    backgroundColor: "#f5f5f5",
    textAlign: "center",
    lineHeight: "40px",
    fontSize: "18px",
  },
});
export default defineComponent({
  setup() {
    const styles = useStyles();

    const imageFile = ref([]);
    const file = ref([]);

    const uploadFileMessage = async (info) => {
      if (info.file.size / 1024 / 1024 > 300) {
        ElMessage.error("文件大小不能超过300兆");
        return;
      }
      const form = new FormData();
      form.append("file", info.file);
      const res = await uploadFile(
        {
          groupId: 1,
        },
        form
      );

      let type: typeEnum = "link";
      if (info.file.type.split("/")[0] === "image") {
        type = "image";
      } else if (info.file.type.split("/")[0] === "video") {
        type = "video";
      } else if (info.file.type.split("/")[0] === "audio") {
        type = "audio";
      }
      // console.log("file type", type);
      sendMessage({
        type,
        content:
          type === "link"
            ? JSON.stringify({ text: info.file.name, url: res.data })
            : (res.data as any),
        is_custom: false,
        fileName: info.file.name,
      });
    };

    return () => (
      <>
        <el-drawer
          v-model={visible.value}
          with-header={false}
          modal={true}
          show-close={false}
          direction={"btt"}
          size={"80px"}
        >
          <el-space size={40}>
            <el-upload
              // onChange={imageFileChange}
              accept="image/*"
              action=""
              // auto-upload={false}
              http-request={uploadFileMessage}
              show-file-list={false}
              multiple={true}
              file-list={imageFile.value}
            >
              <div class={styles.value.uplaodWrapper}>
                <el-icon>
                  <Camera />
                </el-icon>
              </div>
            </el-upload>
            <el-upload
              action=""
              show-file-list={false}
              http-request={uploadFileMessage}
              multiple={true}
              file-list={file.value}
            >
              <div class={styles.value.uplaodWrapper}>
                <el-icon>
                  <Folder />
                </el-icon>
              </div>
            </el-upload>
          </el-space>
        </el-drawer>
      </>
    );
  },
});
