
import {
  userInfo,
  mineInfo,
  currentDate,
  getUserCalendarData,
  getMineCalendarData,
  getAllCalendarData,
  deptFilter,
} from "../hook";
import { defineComponent, reactive, ref } from "vue";
import {
  getHelpers,
  createCalendar,
  updateCalendar,
} from "@/api/deli/attendance";
import CardList from "./card.vue";
import { getTreebyDept } from "@/api/deli/userManagement";
import dayjs from "dayjs";
import { checkAuth } from "@/utils/help";
import { Edit } from "@element-plus/icons";
export default defineComponent({
  components: {
    CardList,
    Edit,
  },
  props: {
    activeCalendar: {
      type: String,
      default: "mine",
    },
  },
  setup() {
    const dialogVisible = ref(false);
    const helperInfo: {
      list: { id: string; name: string }[];
      loading: boolean;
    } = reactive({
      list: [],
      loading: false,
    });
    const rules = reactive({
      arrangeType: [{ required: true, message: "请选择排班", trigger: "blur" }],
      arrangeDetail: [
        { required: true, message: "请选择请假时间", trigger: "blur" },
      ],
    });
    const formDom = ref();
    const form: {
      arrangeType: number | null;
      arrangeDetail: any[];
      helperList: string[];
      userId: string;
    } = reactive({
      arrangeType: null,
      arrangeDetail: [],
      helperList: [],
      userId: "",
    });

    const deptInfo = reactive({
      loading: false,
      list: [],
    });

    const deptProps = reactive({
      label: "deptName",
      value: "deptId",
      checkStrictly: true,
    });
    const current = ref();
    return {
      userInfo,
      mineInfo,
      dialogVisible,
      form,
      helperInfo,
      current,
      rules,
      formDom,
      currentDate,
      deptInfo,
      deptFilter,
      deptProps,
      dayjs,
      checkAuth,
      getUserCalendarData,
    };
  },
  methods: {
    async editSchedule(info) {
      this.dialogVisible = true;
      this.form.userId = info.userId;
      this.helperInfo.loading = true;
      const res: any = await getHelpers({
        userId: info.userId,
      });
      if (info.arrangeDetail !== "无") {
        this.current = info;
        this.form.arrangeType = info.arrangeType;
        this.form.helperList = info.helperList.map((item) => item.id);
        if (info.arrangeType === 3) {
          const time = info.arrangeDetail.split("-");
          this.form.arrangeDetail = [
            dayjs(currentDate.value + " " + time[0]),
            dayjs(currentDate.value + " " + time[1]),
          ];
        }
      } else {
        this.current = {};
        this.form.arrangeType = null;
        this.form.helperList = [];
        this.form.arrangeDetail = [];
      }
      this.helperInfo.list = res.data;
      this.helperInfo.loading = false;
    },
    async saveData() {
      (this.$refs.formDom as HTMLFormElement).validate(async (res) => {
        if (res) {
          try {
            let res!: any;
            const config = {
              ...this.form,
              arrangeDate: currentDate.value,
              arrangeDetail:
                this.form.arrangeType === 1
                  ? "全天"
                  : this.form.arrangeType === 2
                  ? "休息"
                  : dayjs(this.form.arrangeDetail[0]).format("HH:mm") +
                    "-" +
                    dayjs(this.form.arrangeDetail[1]).format("HH:mm"),
              helperList: this.findHelperList(),
            };
            console.log("this.current", this.current);
            if (!this.current.arrangeId) {
              res = await createCalendar(config);
            } else {
              res = await updateCalendar({
                ...config,
                id: this.current.arrangeId,
              });
            }
            if (res.code === 0) {
              this.$message.success(
                !this.current.arrangeId ? "创建成功！" : "修改成功！"
              );
            }
          } catch (error) {
            console.error("创建失败!", error);
          }
          this.dialogVisible = false;
          getUserCalendarData();
          if (this.activeCalendar === "work") {
            getAllCalendarData();
          }
          // 如果修改的为当前用户，则刷新用户列表
          const userInfo = JSON.parse(localStorage.getItem("user") as string);
          if (this.form.userId === userInfo.userId) {
            getMineCalendarData();
          }
        }
      });
    },
    findHelperList() {
      if (this.form.helperList.length === 0) {
        return [];
      } else {
        const arr: any[] = [];
        this.form.helperList.forEach((id) => {
          arr.push(this.helperInfo.list.find((h) => h.id === id));
        });
        return arr;
      }
    },
    async getTree() {
      this.deptInfo.loading = true;
      try {
        const res: any = await getTreebyDept({
          deptId: JSON.parse(localStorage.getItem("user") as string).deptId,
        });
        this.deptInfo.list = res.data.deptList;
        console.warn("res", res);
      } catch (error) {
        console.error("getTreebyDept", error);
      }
      this.deptInfo.loading = false;
    },
    changeArrangeType(val) {
      this.form.helperList = [];
    },
  },
  mounted() {
    // console.log("this", this.activeCalendar);
    if (this.checkAuth([2])) {
      // 获取部门树
      this.getTree();
    }
  },
});
