import { ref, reactive } from "vue";
import { ChatType } from "@/gtypes/Chat";
import { getConversationInfo } from "@/api/deli/conversation";
import { checkAuth } from "@/utils/help";
import { getDeptTree } from "@/api/deli/setting";
import dayjs from "dayjs";
interface Record {
  conversationId: string;
  customerName: string;
  customerCompanyName: string;
  serverName: string;
  startTime: string;
  duration: number;
  chatList: any[];
  label: string[];
  companyId?: string;
  orgId?: string;
}
const name = reactive({
  Tname: "",
});
const drawer = ref(false);
const recordInfo: Record = reactive({
  conversationId: "",
  customerName: "-",
  customerCompanyName: "",
  serverName: "-",
  startTime: "-",
  duration: 0,
  chatList: [],
  label: [],
});

const treeInfo = reactive({
  list: [],
});

const getRecordInfo: (item: any) => void = async (item) => {
  drawer.value = true;
  const res: any = await getConversationInfo({
    groupId: item.groupId,
  });
  recordInfo.chatList = res.data.result.messagesList.map((h) => {
    let speak = 1;
    if (h.from === item.userId) {
      speak = 0;
    }
    return {
      ...h,
      speak,
    };
  });
  recordInfo.conversationId = item.customerCompanyNumber;
  recordInfo.customerCompanyName = item.customerCompanyName;
  recordInfo.customerName = item.customerName;
  recordInfo.serverName = item.staffName;
  recordInfo.startTime = dayjs(item.createTime).format("YYYY-MM-DD HH:mm:ss");
  recordInfo.duration = Number((item.durationTime / 1000 / 60).toFixed(2));
  recordInfo.orgId = item.orgId;
  recordInfo.companyId = item.companyId;
  getTreeName(recordInfo.orgId as string, recordInfo.companyId);
};

const findTree = () => {
  const user = JSON.parse(localStorage.getItem("user") as string);
  let deptId, companyId;
  if (checkAuth([3])) {
    companyId = user.deptId;
  } else {
    deptId = checkAuth([4]) ? "" : user.deptId;
    companyId = "";
  }
  return {
    deptId,
    companyId,
  };
};

const getTreeData = async (): Promise<void> => {
  const res: any = await getDeptTree();
  const user = JSON.parse(localStorage.getItem("user") as string);
  if (checkAuth([1, 4])) {
    treeInfo.list = [
      {
        deptName: res.data.groupName,
        deptId: 0,
        children: res.data.deptList,
      },
    ] as any;
    filter.dept = [0] as any;
  } else if (checkAuth([2]) || user.deptType === 2) {
    const data = res.data.deptList.filter(
      (item) => item.deptId === user.deptId
    );
    treeInfo.list = data;
    filter.dept = [data[0].deptId] as any;
  }
};

const getTreeName = async (deptId: string | number, companyId = "") => {
  const res: any = await getDeptTree();
  const obj: any = res.data.deptList.find(
    (item: any) => item.deptId === Number(deptId)
  );
  if (obj) {
    name.Tname = obj.deptName;
    if (companyId) {
      const com: any = obj.children.find(
        (item: any) => item.deptId === Number(companyId)
      );
      if (com) {
        name.Tname += "/" + com.deptName;
      }
    }
  }
};

const filter = reactive({
  status: "", // 客服状态
  channel: "", // 渠道类型
  // companyId: findTree().companyId,
  // deptId: findTree().deptId,
  dept: [],
  customerCompanyName: "",
  uid: "",
  label: "",
  score: "", // 是否满意
});

export {
  drawer,
  recordInfo,
  getRecordInfo,
  findTree,
  filter,
  getTreeData,
  treeInfo,
  getTreeName,
  name,
};
