import { reactive } from "vue";
const rules = reactive({
  userEmail: [
    { required: true, message: "请输入邮箱地址", tigger: "blur" },
    { type: "email", message: "邮箱地址错误", tigger: "blur" },
  ],
  userNickName: [{ required: true, message: "请输入客服昵称", tigger: "blur" }],
  userName: [
    {
      required: true,
      message: "请输入真实姓名",
      tigger: "blur",
    },
  ],
});

const passForm = reactive({
  oldPassword: "",
  newPassword: "",
  checkPassword: "",
});

const validatePass = (rule, value, callback) => {
  if (value === "") {
    callback(new Error("请输入新密码"));
  } else if (value === passForm.oldPassword) {
    callback(new Error("新密码不能与旧密码相同！"));
  }
  callback();
};

const checkPass = (rule, value, callback) => {
  if (value === "") {
    callback(new Error("请输入新密码"));
  } else if (value !== passForm.newPassword) {
    callback(new Error("两次输入密码不一致！"));
  }
  callback();
};

const passwordRules = reactive({
  oldPassword: [{ required: true, message: "请输入密码", tigger: "blur" }],
  newPassword: [
    { validator: validatePass, tigger: "blur" },
    { required: true, message: "请输入密码", tigger: "blur" },
  ],
  checkPassword: [
    { validator: checkPass, tigger: "blur" },
    { required: true, message: "请输入密码", tigger: "blur" },
  ],
});

export { passwordRules, passForm, rules };
