import { ref } from "vue";
import { close } from "@/views/assist/imhook";
import { userSync } from "@/api/deli/mainScreen";
// import { errorLog } from "@/api/error";
import dayjs from "dayjs";
const { Version } = require("@/config/version");
const checkUserVisible = ref(false);
const netWordkTip = ref("");
const tipText = ref("确定");

const setCheckVisible = (status) => {
  if (tipText.value === "去登陆页面") {
    window.location.href = "/login";
    return;
  }

  if (netWordkTip.value) {
    window.location.reload();
  } else {
    checkUserVisible.value = status;
    if (status === true) {
      close();
      localStorage.removeItem("user");
      // localStorage.removeItem("token");
    } else {
      window.location.href = "/login";
    }
  }
};

const timer = ref<NodeJS.Timer>();

const sendHeart = () => {
  // const n = Math.random() * 10 + new Date().getTime();
  const user = JSON.parse(localStorage.getItem("user") as string);
  let time = checkNeedSend() ? 600000 : 1200000;
  if (user && user.userId) {
    if (!timer.value) {
      // 定时器开始前，先发一次
      sendHeaderRequest();
      timer.value = setInterval(async () => {
        time = checkNeedSend() ? 600000 : 1200000;
        sendHeaderRequest();
      }, time);
    }
  } else {
    clearHeart();
  }
};

const checkNeedSend = () => {
  const day = dayjs().format("YYYY-MM-DD");
  let result = false;
  if (dayjs().isBefore(`${day} 08:00`) || dayjs().isAfter(`${day} 18:00`)) {
    result = true;
  }
  return result;
};

const sendHeaderRequest = async () => {
  const userId = JSON.parse(localStorage.getItem("user") as string).userId;
  // errorLog({
  //   type: "发送心跳",
  //   content: dayjs().format("YYYY-MM-DD HH:mm:ss"),
  //   user: userId + "_" + n,
  //   isUpdate: true,
  // });
  try {
    const res: any = await userSync({
      userId,
    });
    if (res.code === 0) {
      if (res.data.versionCode !== Version) {
        checkUserVisible.value = true;
        netWordkTip.value = "发现新版本，请刷新页面更新！";
        tipText.value = "更新";
      }
    }
  } catch (error) {
    // errorLog({
    //   type: "心跳异常",
    //   content: error,
    // });
    console.log(error);
  }
};

const clearHeart = () => {
  clearInterval(timer.value as NodeJS.Timer);
  timer.value = undefined;
};

export {
  checkUserVisible,
  setCheckVisible,
  sendHeart,
  netWordkTip,
  clearHeart,
  tipText,
};
