import "./index.scss";
import {
  defineComponent,
  PropType,
  ref,
  toRefs,
  onMounted,
  reactive,
} from "vue";
import { createUseStyles } from "vue-jss";
import dayjs from "dayjs";
const relativeTime = require("dayjs/plugin/relativeTime");
dayjs.extend(relativeTime);
import { Search } from "@element-plus/icons";
import { getAccounts, getImUserId } from "@/api/deli/im";
import { userAccountInfo, getHistoryData, clearOfflineTimer } from "./userHook";
import { inputDom } from "@/components/chat/hook";
import { ImUser } from "../imhook";
const useStyles = createUseStyles({
  "user-wrapper": {
    "& li": {
      padding: "10px",
      cursor: "pointer",
      "&.active": {
        backgroundColor: "#4e7cca",
        color: "#fff",
      },
    },
  },
  "user-tabs": {
    // "& .el-tabs__item": {
    //   padding: "0 15px !important",
    // },
  },
  "not-read-wrappeer": {
    display: "inline-block",
    minWidth: "20px",
    padding: "1px 2px",
    boxSizing: "border-box",
    height: "20px",
    borderRadius: "10px",
    color: "#fff",
    textAlign: "center",
    lineHeight: "20px",
    backgroundColor: "#d03b32",
  },
  "account-wrapper": {
    "& .el-collapse-item__header": {
      backgroundColor: "#fff",
      color: "#333",
      "&:hover": {
        backgroundColor: "#f0f0f0",
      },
    },
    "& .el-collapse-item__content": {
      paddingBottom: "10px",
      boxSizing: "border-box",
    },
    "& .el-collapse": {
      borderColor: "#fff",
    },
    "& ul": {
      "& li": {
        paddingLeft: "20px",
        height: "30px",
        lineHeight: "30px",
        "&.active": {
          backgroundColor: "#dbe4f4",
        },
        "&:hover": {
          backgroundColor: "#dbe4f4",
        },
      },
    },
  },
});

export default defineComponent({
  emits: ["changeGroupId", "getUserInfo"],
  props: {
    userList: {
      type: Array as PropType<any[]>,
      default() {
        return [];
      },
    },
    currentGroupId: {
      type: String as PropType<string>,
      default: "",
    },
  },
  unmounted() {
    clearOfflineTimer();
  },
  setup(props: { userList: any[]; currentGroupId: string }, context) {
    const { userList, currentGroupId } = toRefs(props);
    const active = ref("user");
    const styles = useStyles();
    const getUserContent = (item) => {
      let result = "-";
      if (item.list && item.list.length > 0) {
        const i = item.list.length - 1;
        if (item.list[i].type === "text") {
          result = item.list[i].content;
        } else if (item.list[i].type === "image") {
          result = "[图片]";
        } else if (item.list[i].type === "video") {
          result = "[视频]";
        } else if (item.list[i].type === "audio") {
          result = "[音频]";
        } else if (item.list[i].type === "link") {
          const obj = JSON.parse(item.list[i].content);
          result = obj.text;
        }
      }
      return result;
    };

    const getCurrentName = (item) => {
      console.log("item", item);

      let result = "-";
      if (item.list && item.list.length > 0) {
        for (let i = item.list.length - 1; i >= 0; i--) {
          if (item.list[i].from.type === 0) {
            result = item.list[i].from.username;
          }
        }
      } else {
        result = item.username;
      }
      if (!result || result === "-") {
        // console.log(localStorage.getItem(item.username));
        result = item.username || localStorage.getItem("name");
        // localStorage.setItem(item.username,item.username);
      } else {
        localStorage.removeItem("username");
      }
      return result;
    };

    onMounted(() => {
      userAccountInfo.isAccount = false;
    });

    // 客户列表
    const keyword = ref("");
    const accountInfo = reactive({
      loading: false,
      isRequest: false,
      list: [],
    });
    const isSearch = ref(false);
    const accountActive = ref();
    const getAccountList = async () => {
      accountInfo.loading = true;
      const res: any = await getAccounts({
        keyword: keyword.value,
      });
      // res.data && res.data.length > 0
      //   ? (accountActive.value = [res.data[0].sapNumber])
      //   : (accountActive.value = []);

      accountInfo.list = res.data;
      accountInfo.isRequest = true;
      accountInfo.loading = false;
    };
    const searchAccount = (e) => {
      if (e.key === "Enter") {
        getAccountList();
        isSearch.value = true;
      }
    };

    // 点击客户列表
    const openMessage = async (p) => {
      userAccountInfo.isAccount = true;
      userAccountInfo.loading = true;
      userAccountInfo.staffCode = p.staffCode;
      userAccountInfo.staffName = p.staffName;
      if (p.staffCode) {
        const res = await getImUserId({
          staffCode: p.staffCode,
        });
        userAccountInfo.userId = res.data as string;
        context.emit("getUserInfo");
        getHistoryData();
      }
    };

    const renderCustomUser = () => {
      return (
        <div class={styles.value["account-wrapper"]}>
          <el-collapse v-model={accountActive.value}>
            {accountInfo.list.map((item: any, idx: number) => {
              return (
                <el-collapse-item
                  key={idx}
                  name={item.sapNumber}
                  v-slots={{
                    title: () => {
                      return (
                        <div class="ellipsis">
                          <img
                            src={require("@/assets/company.png")}
                            style={"width: 24px"}
                            class="m-r-5"
                            alt=""
                          />
                          {item.accountName}
                        </div>
                      );
                    },
                  }}
                >
                  <ul>
                    {item.staffList.map((p) => {
                      return (
                        <li
                          key={p.staffCode}
                          class={[
                            userAccountInfo.staffCode === p.staffCode
                              ? "active"
                              : "",
                            "pointer",
                            "ellipsis",
                          ]}
                          onClick={() => openMessage(p)}
                        >
                          <img
                            src={require("@/assets/m.png")}
                            style={"width: 24px"}
                            class="m-r-5"
                            alt=""
                          />
                          {p.staffName}
                        </li>
                      );
                    })}
                  </ul>
                </el-collapse-item>
              );
            })}
          </el-collapse>
        </div>
      );
    };

    const showRelativeDate = (item) => {
      const time = item.list[item.list.length - 1].createTime;
      const currentTime = ImUser.value?.currentTime;
      // 判断time是否是一天前
      if (dayjs(time).isBefore(dayjs(currentTime), "day")) {
        return (dayjs(currentTime) as any).from(dayjs(time));
      } else {
        return dayjs(time).format("HH:mm");
      }
    };

    return () => (
      <>
        <div class="h-100">
          <el-tabs
            v-model={active.value}
            onTabClick={(tabs) => {
              if (tabs.props.name === "custom") {
                getAccountList();
              } else {
                userAccountInfo.isAccount = false;
                userAccountInfo.list = [];
                userAccountInfo.userId = "";
                clearOfflineTimer();
                if (userList.value.length > 0) {
                  context.emit("changeGroupId", userList.value[0].groupId);
                }
              }
            }}
            stretch={true}
            class={styles.value["user-tabs"]}
          >
            <el-tab-pane label="当前会话" name="user">
              <el-scrollbar height="calc(100vh - 140px)">
                {userList.value && userList.value.length > 0 && (
                  <ul class={styles.value["user-wrapper"]}>
                    {userList.value.map((item: any) => {
                      return (
                        <li
                          class={
                            currentGroupId.value === item.groupId
                              ? "active"
                              : ""
                          }
                          key={item.groupId}
                          onClick={() => {
                            userAccountInfo.isAccount = false;
                            // userAccountInfo.staffCode = "";
                            userAccountInfo.list = [];
                            userAccountInfo.userId = "";
                            context.emit("changeGroupId", item.groupId);
                            if (currentGroupId.value !== item.groupId) {
                              (inputDom.value as any).setHtml("");
                            }
                          }}
                        >
                          <div class="flex ac">
                            <div class="block">
                              <el-avatar
                                size={34}
                                src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
                              />
                            </div>
                            {/* <i class="iconfont icon-user font-30"></i> */}
                            <div class="font-14 flex-1 m-l-10  ellipsis">
                              <div class="flex sb w-100 m-b-5">
                                <div
                                  class="font-14  ellipsis"
                                  style={"width: calc(100% - 20px)"}
                                >
                                  {getCurrentName(item)}
                                  <span class="font-12 m-l-10 font-yellow">
                                    {item.portrait?.accountName}
                                  </span>
                                </div>
                                {item.list && item.list.length > 0 ? (
                                  <span
                                    class="font-12"
                                    style={`color: ${
                                      currentGroupId.value === item.groupId
                                        ? "#f5f5f5"
                                        : "#333"
                                    }`}
                                  >
                                    {showRelativeDate(item)}
                                  </span>
                                ) : null}
                              </div>
                              <div class="flex sb">
                                <div class="ellipsis">
                                  {getUserContent(item)}
                                </div>
                                {item.list.filter(
                                  (item) => item.speak === 0 && item.noRead
                                ).length > 0 ? (
                                  <span
                                    class={styles.value["not-read-wrappeer"]}
                                  >
                                    {item.list.filter(
                                      (item) => item.speak === 0 && item.noRead
                                    ).length > 99
                                      ? "99+"
                                      : item.list.filter(
                                          (item) =>
                                            item.speak === 0 && item.noRead
                                        ).length}
                                  </span>
                                ) : null}
                              </div>
                            </div>
                          </div>
                        </li>
                      );
                    })}
                  </ul>
                )}
                {!userList.value ||
                  (userList.value.length === 0 && (
                    <div class="ta-center p-t-20">
                      <el-empty
                        image={require("@/assets/no-custom.png")}
                        image-size={120}
                        description="暂无客户"
                      ></el-empty>
                    </div>
                  ))}
              </el-scrollbar>
            </el-tab-pane>
            <el-tab-pane label="客户列表" name="custom">
              <el-scrollbar height="calc(100vh - 140px)">
                <div class="p-10" v-loading={accountInfo.loading}>
                  {isSearch.value || accountInfo.isRequest ? (
                    <div>
                      <div class="m-b-10">
                        <el-input
                          placeholder="搜索客户及联系人"
                          v-model={keyword.value}
                          v-slots={{
                            append: () => (
                              <el-button
                                icon={Search}
                                onClick={() => searchAccount({ key: "Enter" })}
                              />
                            ),
                          }}
                          onKeydown={searchAccount}
                        ></el-input>
                      </div>
                      {renderCustomUser()}
                    </div>
                  ) : (
                    <div class="ta-center p-t-20">
                      <el-empty
                        image={require("@/assets/no-company.png")}
                        image-size={120}
                        description="暂无公司"
                      ></el-empty>
                    </div>
                  )}
                </div>
              </el-scrollbar>
            </el-tab-pane>
          </el-tabs>
        </div>
      </>
    );
  },
});
