import { wexinEmo } from "@/utils/emo";

interface Ob {
  [propName: string]: any;
}

function checkAuth(auth: number[]): boolean {
  const user = localStorage.getItem("user");
  if (user) {
    const userAuth = JSON.parse(user).roleList;
    let result = false;
    auth.forEach((element) => {
      if (userAuth.find((item) => item.roleId === Number(element))) {
        result = true;
        return result;
      }
    });
    return result;
  } else {
    console.error('localStorage.getItem("user")失效');
    if (
      window.location.pathname !== "/login" &&
      window.location.pathname !== "/chatlive" &&
      window.location.pathname !== "/weixin"
    ) {
      window.location.href = "/login";
    }
    return false;
  }
}

// 多级需要遍历
function resetReactiveData<T extends Ob>(data: any, config: Ob = {}): T {
  Object.keys(data).forEach((key) => {
    if (key in config) {
      data[key] = config[key];
    } else {
      if (data[key] === undefined || data[key] === null) {
        data[key] = undefined;
      } else if (
        typeof data[key] === "string" ||
        typeof data[key] === "number"
      ) {
        data[key] = "";
      } else if (typeof data[key] === "object") {
        // 数组， 对象
        if (data[key] instanceof Array) {
          data[key] = [];
        } else {
          const list = Object.keys(data[key]);
          if (list.length > 0) {
            data[key] = resetReactiveData(data[key]);
          } else {
            data[key] = {};
          }
        }
      }
    }
  });
  return data;
}

function haveMatch(content: string): any[] {
  const list: any[] = [];
  Object.keys(wexinEmo).forEach((key) => {
    if (content.indexOf(key) !== -1) {
      list.push(key);
    }
  });
  return list;
}

function renderEmo(content: string): string {
  let str = content.replaceAll("&gt;", ">").replaceAll("&lt;", "<");
  let result = "";
  const match = haveMatch(str);
  if (match.length > 0) {
    match.forEach((item) => {
      str = str.replaceAll(
        item,
        `<div class="qqface qqface${wexinEmo[item]}"></div>`
      );
    });
    return str;
  } else {
    result = content;
    return result;
  }
}

function resetForm<T extends Ob>(params: any): T {
  Object.keys(params).forEach((item) => {
    if (params[item] instanceof Array) {
      params[item] = [];
    } else if (params[item] instanceof Object) {
      params[item] = {};
    } else {
      params[item] = "";
    }
  });
  return params;
}

function downFile(data, name) {
  const objectURL = window.URL.createObjectURL(data);
  const a = window.document.createElement("a");
  a.href = objectURL;
  if (name.slice(0, 4) === "成员列表") {
    a.download = name + ".xlsx";
  } else {
    a.download = name;
  }

  a.click();
  a.remove();
  // 释放创建的url对象
  window.URL.revokeObjectURL(objectURL);
}

function createNotice(title, body) {
  console.log("currentStatus", (window as any).currentStatus);
  if ((window as any).currentStatus === "hidden") {
    const permission = Notification.permission;
    if (permission === "granted") {
      // new Notification(title, {
      //   body,
      //   image: "/favicon.ico",
      //   icon: "/favicon.ico",
      // });
      (window as any).apiKey && (window as any).apiKey.fn(title, body);
    } else {
      Notification.requestPermission().then(function (permission) {
        if (permission === "granted") {
          console.log("用户同意授权");
          // // 随时可以显示通知
          // new Notification(title, {
          //   body,
          //   image: "/favicon.ico",
          //   icon: "/favicon.ico",
          // });
          (window as any).apiKey && (window as any).apiKey.fn(title, body);
        } else if (permission === "default") {
          console.log("用户关闭授权,可以再次请求授权");
        } else {
          console.log("用户拒绝授权,不能显示通知");
        }
      });
    }
  }
}

function sendNotice(data: { type: string; content: any }) {
  let body = "";
  let title = "";
  if (data.type === "acceptMessage") {
    const msg = data.content;
    if (msg.type === "text") {
      body = msg.content;
    } else if (msg.type === "video") {
      body = "您收到一个视频文件";
    } else if (msg.type === "audio") {
      body = "您收到一个音频文件";
    } else if (msg.type === "image") {
      body = "您收到一张图片";
    } else if (msg.type === "link") {
      if (JSON.parse(msg.content).text === "请对本次服务进行评价") {
        return;
      }
      body = "您收到一个文件";
    }
    title = "消息提醒";
    createNotice(title, body);
  } else if (data.type === "closeMessage") {
    title = "会话结束提醒";
    body = `您与用户${data.content}的会话已结束！`;
    createNotice(title, body);
  } else if (data.type === "showGroup") {
    title = "会话提醒";
    body = `${data.content}发起了一个新会话`;
    createNotice(title, body);
  }
}

// list, config: { value: 'categoryId', children: 'children' }, ids
// 通用递归方法
function findIds(
  id: any,
  list: any[],
  config = { value: "id", children: "children" }
) {
  let arr: any[] = [];
  for (let i = 0; i < list.length; i++) {
    arr = [];
    if (list[i][config.value].toString() === id.toString()) {
      arr.push(list[i][config.value]);
      break;
    } else {
      if (list[i][config.children] && list[i][config.children].length > 0) {
        arr.push(list[i][config.value]);
        const temp = findIds(id, list[i][config.children], config);
        if (temp.length > 0) {
          arr = arr.concat(temp);
          break;
        }
      }
    }
  }
  return arr;
}

