
import { login } from "../api/deli/sys";
// import { login } from "@/api/user"
import { reactive, ref } from "@vue/reactivity";
import { defineComponent } from "vue";
// import { useStore } from "vuex"
import { User } from "@/gtypes/User";
export default defineComponent({
  computed: {
    getUserList() {
      let arr: any[] = [];
      arr = this.userList.filter((item) => {
        return item.loginName.indexOf(this.ruleForm.loginName) !== -1
          ? true
          : false;
      });
      return arr;
    },
  },
  setup() {
    const dialogVisible = ref(false);
    const ispc = ref(false);
    const ruleForm = reactive({
      loginName: "",
      password: "",
      forceLogin: false,
    });
    const userVisible = ref(false);
    const userList = ref<any[]>([]);
    const remeber = ref(true);
    const rules = reactive({
      loginName: [{ required: true, message: "请输入账号!", trigger: "blur" }],
      password: [{ required: true, message: "请输入密码!", trigger: "blur" }],
    });
    const formRef = ref();
    const failReason = "";
    return {
      ruleForm,
      rules,
      formRef,
      ispc,
      remeber,
      userList,
      userVisible,
      dialogVisible,
      failReason,
    };
  },
  methods: {
    forceLland() {
      this.dialogVisible = false;
      this.ruleForm.forceLogin = true;
      this.submitForm();
    },
    submitForm() {
      (this.$refs.formRef as HTMLFormElement).validate((valid) => {
        if (valid) {
          login({
            ...this.ruleForm,
            oldToken: localStorage.getItem("token") || "",
          }).then((res) => {
            const data = res.data as User;
            if (!data.loginSuccess) {
              this.dialogVisible = true;
              this.failReason = data.failReason;
              return;
            }
            localStorage.setItem("token", data.token);
            localStorage.setItem("user", JSON.stringify(data));
            this.$store.commit("setUser", data);
            // this.$router.push("/");
            window.location.href = "/";
            if (this.ispc && this.remeber) {
              let userList: any[] = localStorage.getItem("cookieUser")
                ? JSON.parse(localStorage.getItem("cookieUser") as string)
                : [];
              let index = userList.findIndex(
                (item) => item.loginName === this.ruleForm.loginName
              );
              if (index !== -1) {
                // 移到第一个
                userList.splice(index, 1);
              } else {
                if (userList.length >= 5) {
                  userList.pop();
                }
              }
              userList.unshift({
                ...this.ruleForm,
                time: new Date().getTime() + 24 * 60 * 60 * 1000 * 30,
              });
              // console.log('userList', userList)
              localStorage.setItem("cookieUser", JSON.stringify(userList));
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    selectUser(item) {
      console.log("item", item);
      this.ruleForm.loginName = item.loginName;
      this.ruleForm.password = item.password;
      this.userVisible = false;
    },
    showPop() {
      if (this.userList.length > 0 && this.getUserList.length > 0) {
        this.userVisible = true;
      } else {
        this.userVisible = false;
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      console.log("windoiw", window);
      if ((window as any).apiKey) {
        this.ispc = true;
        let userList: any[] = localStorage.getItem("cookieUser")
          ? JSON.parse(localStorage.getItem("cookieUser") as string)
          : [];
        let now = new Date().getTime();
        for (let i = 0; i < userList.length; i++) {
          if (now > userList[i].time) {
            userList.splice(i, 1);
          }
        }
        this.userList = [...userList];
        console.log("this.userList", this.userList);
        if (userList.length > 0) {
          this.ruleForm.loginName = userList[0].loginName;
          this.ruleForm.password = userList[0].password;
        }
      }
    });
  },
});
