import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";

type NumverType = number | "";

interface UserId {
  userId: number;
}

interface BaseUser {
  userEmail: string;
  userName: string;
  userNickName: string;
  userPhone: NumverType;
  workId?: string;
  userRoleId: any;
  userDeptId: any;
}

interface UserParams extends BaseUser {
  deptId: NumverType;
  userLoginName: string;
  userLoginPassword: string;
  userNickName: string;
  userRoleId: NumverType;
}

export const uolpadAvatar: (
  params: UserId,
  data: FormData
) => Promise<Service.Result> = (params, data) => {
  return Request({
    url: "/user-management/avatar/upload",
    method: "post",
    params,
    data,
  });
};

export const createUser: (
  data: Omit<UserParams, "userDeptId">
) => Promise<Service.Result> = (data) => {
  return Request({
    url: "/user-management/create/user",
    method: "post",
    data,
  });
};

export const deleteUser: (params: UserId) => Promise<Service.Result> = (
  params
) => {
  return Request({
    url: "/user-management/delete/user",
    method: "post",
    params,
  });
};

export const banUser: (params: UserId) => Promise<Service.Result> = (
  params
) => {
  return Request({
    url: "/user-management/disable/user",
    method: "post",
    params,
  });
};

export const getUserInfo: (params: UserId) => Promise<Service.Result> = (
  params
) => {
  return Request({
    url: "/user-management/info",
    params,
  });
};

interface UpdateInfo extends BaseUser, UserId {}

export const updateUserInfo: (
  params: UserId,
  data: UpdateInfo
) => Promise<Service.Result> = (params, data) => {
  return Request({
    url: "/user-management/info/update",
    params,
    data,
    method: "post",
  });
};

export const updatePassword: (params: {
  newPassword: string;
  oldPassword: string;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/user-management/password",
    params,
    method: "post",
  });
};

export const getUsers: (
  params: Service.PageInfo,
  data: {
    deptId: number | string;
    parentDeptId: number | string;
    userNameKeyword: string;
    workIdKeyword: string;
    onlineStatus: NumverType;
  }
) => Promise<Service.Result> = (params, data) => {
  return Request({
    url: "/user-management/users",
    params,
    data,
    method: "post",
  });
};

export const getTreebyDept: (params: {
  deptId: number;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/user-management/dept/tree",
    params,
  });
};
// 获取所有分公司坐席人员信息
export const getServerList: () => Promise<Service.Result> = () => {
  return Request({
    url: "/user-management/server/list",
  });
};

// 成员列表导出
export const exportUsers: (data: {
  deptId: NumverType;
  onlineStatus: NumverType;
  parentDeptId: number | string;
  roleId: number;
  userNameKeyword: string;
  workIdKeyword: string;
}) => Promise<Service.Result> = (data) => {
  console.log("data", data);

  return Request({
    url: "/user-management/export/users",
    method: "post",
    data,
    responseType: "blob",
  });
};

