import {
  defineComponent,
  PropType,
  toRefs,
  ref,
  watchEffect,
  onActivated,
  nextTick,
  computed,
} from "vue";
import { createUseStyles } from "vue-jss";
import { ChatType } from "@/gtypes/Chat";
import MsgType from "./msgType";
import dayjs from "dayjs";
const useStyles = createUseStyles({
  userChat: {
    display: "inline-block",
    maxWidth: "60%",
    marginBottom: "15",
    "& .chat-content": {
      marginTop: "6px",
      backgroundColor: "#fff8eb",
      padding: "10px",
      wordSpacing: "normal",
      wordBreak: "break-all",
      display: "inline-block",
    },
  },
  serviceChat: {
    float: "right",
    maxWidth: "60%",
    position: "relative",
    "& img": {
      width: "50px",
      height: "50px",
    },
    "& .chat-content": {
      maxWidth: "280px",
      marginTop: "6px",
      backgroundColor: "#fff",
      padding: "10px",
      wordSpacing: "normal",
      wordBreak: "break-all",
      float: "right",
    },
  },
  listWrapper: {
    padding: "10px",
    height: "100%",
    backgroundColor: "#f0f2f5",
    "& .el-radio__label": {
      display: "none",
    },
  },
});
import { question, answer } from "./hook";
export default defineComponent({
  props: {
    chatList: {
      type: Object as PropType<ChatType[]>,
      required: true,
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  setup(props: { chatList: ChatType[]; isEdit: boolean }) {
    console.log("查看一下chatList", props.chatList);
    const classes = useStyles();
    const { chatList, isEdit } = toRefs(props);
    // console.log("tututu", chatList);
    const scrollbarRef = ref();

    const renderUser = (message: ChatType, idx: number) => {
      return (
        <div class={classes.value.userChat}>
          <div class="flex">
            {isEdit.value ? (
              message.type === "text" ? (
                <el-radio
                  v-model={question.value}
                  label={message.content + "_" + idx}
                  class="m-r-15"
                ></el-radio>
              ) : (
                <div style={"width:30px"}></div>
              )
            ) : null}
            <i class="iconfont icon-user font-30 font-primary"></i>
            <div class="m-l-10">
              <p class="font-14">
                <el-space>
                  <span>{dayjs(message.createTime).format("MM-DD HH:mm")}</span>
                </el-space>
              </p>
              <div class="chat-content">
                <MsgType message={message}></MsgType>
              </div>
            </div>
          </div>
        </div>
      );
    };

    watchEffect(async () => {
      if (chatList.value.length > 0) {
        await nextTick();
        scrollbarRef.value!.setScrollTop(99999);
      }
    });

    const renderServer = (message: ChatType, idx: number) => {
      return (
        <div>
          <div class={classes.value.serviceChat}>
            <el-space alignment="stretch">
              <div class="flex">
                <div class="nati">
                  <p class="font-14 ta-right flexsx">
                    <el-space>
                      {dayjs(message.createTime).format("MM-DD HH:mm")}
                      {message.from.username}
                    </el-space>
                  </p>
                  <div class="chat-content">
                    <MsgType message={message}></MsgType>
                  </div>
                </div>
                <img src={require("../../assets/deli.png")} alt="" />
                {isEdit.value ? (
                  message.type === "text" ? (
                    <el-radio
                      v-model={answer.value}
                      label={message.content + "_" + idx}
                      class="m-r-15"
                    ></el-radio>
                  ) : (
                    <div style={"width: 16px"}></div>
                  )
                ) : null}
              </div>
            </el-space>
          </div>
        </div>
      );
    };
    return () => (
      <el-scrollbar height="100%" ref={scrollbarRef}>
        {chatList.value.length === 0 ? (
          <div class={classes.value.listWrapper}>
            {/* <el-empty
              image={require("@/assets/no-session.png")}
              image-size={240}
              class="m-t-40"
              description="暂无会话内容"
            ></el-empty> */}
          </div>
        ) : (
          <ul class={classes.value.listWrapper}>
            {chatList.value.map((item: ChatType, idx: number) => {
              return (
                <li class="clearfix m-b-15">
                  {item.speak === 0
                    ? renderUser(item, idx)
                    : renderServer(item, idx)}
                </li>
              );
            })}
          </ul>
        )}
      </el-scrollbar>
    );
  },
});
