
import TitleCom from "@/components/title/index.vue";
import { ref, reactive } from "vue";
import { defineComponent } from "vue";
import { updateCustomer } from "@/api/deli/conversation";
import { ElMessage } from "element-plus";
import { Item } from "ant-design-vue/lib/menu";
import { string } from "vue-types";
import { number } from "echarts";

export default defineComponent({
  components: {
    TitleCom,
  },
  setup() {
    const customers = ref("");
    const customerNumber = reactive([""]);
    const estimate = ref(true);
    const lineNum = ref("");
    return {
      customers,
      customerNumber,
      estimate,
      lineNum,
    };
  },
  methods: {
    uploading() {
      if (this.customers) {
        this.customerNumber = this.customers
          .replace(/\r\n/g, ",")
          .replace(/\n/g, ",")
          .split(",");
        let est = /^\d{7}$/;
        for (let i = 0; i < this.customerNumber.length; i++) {
          if (!est.test(this.customerNumber[i])) {
            this.estimate = false;
            let str = (i + 1).toString();
            this.lineNum = this.lineNum + str + " ";
            continue;
          } else {
            if (i != 0 && !est.test(this.customerNumber[i - 1])) {
              this.estimate = false;
              break;
            }
            this.estimate = true;
          }
        }
        if (this.estimate === true) {
          ElMessage({
            message: "客户更新中...",
            type: "success",
          });
          updateCustomer(this.customerNumber)
            .then((e) => {
              this.customers = "";
              ElMessage({
                message: "客户更新成功！",
                type: "success",
              });
            })
            .catch((e) => {
              ElMessage.error("客户更新失败！");
            });
        } else {
          ElMessage.error("请检查" + this.lineNum + "行客户编码格式是否正确！");
          this.lineNum = "";
        }
      } else {
        ElMessage.error("请填写客户编码！");
      }
    },
  },
});
