
import { defineComponent } from "vue";
export default defineComponent({
  setup() {
    return {};
  },
  mounted() {
    if (window.navigator.onLine) {
      window.location.href =
        (localStorage.getItem("locationUrl") as string) || "/";
    }
  },
  methods: {
    reload() {
      window.location.reload();
    },
  },
});
