import { reactive, ref, inject } from "vue";
import {
  getMineCalendar,
  getUserCalendar,
  getAllCalendar,
} from "@/api/deli/attendance";
import dayjs, { Dayjs } from "dayjs";
import { checkAuth } from "@/utils/help";

//判断是管理员还是坐席
const auth = ref(checkAuth([2, 4]) ? "admin" : "user");

// 改from和to
const changeT = reactive({
  Time: parseInt(dayjs().startOf("month").format("M")),
});

const changeTime = (changeM, Pages) => {
  if (changeM != dayjs().format("M")) {
    if (Pages === "mine") {
      changeT.Time = parseInt(changeM) - 1;
      mineInfo.from = dayjs()
        .set("month", parseInt(changeM) - 1)
        .startOf("month")
        .format("YYYY-MM-DD");
      mineInfo.to = dayjs()
        .set("month", parseInt(changeM) - 1)
        .endOf("month")
        .format("YYYY-MM-DD");
      getMineCalendarData();
      return;
    } else {
      caledarList.list = [];
      userInfo.list = [];
      changeT.Time = parseInt(changeM) - 1;
      caledarList.from = dayjs()
        .set("month", parseInt(changeM) - 1)
        .startOf("month")
        .format("YYYY-MM-DD");
      caledarList.to = dayjs()
        .set("month", parseInt(changeM) - 1)
        .endOf("month")
        .format("YYYY-MM-DD");
      getAllCalendarData();
      getUserCalendarData();
    }
  } else {
    if (Pages === "mine") {
      changeT.Time = parseInt(changeM) - 1;
      mineInfo.from = dayjs().startOf("month").format("YYYY-MM-DD");
      mineInfo.to = dayjs().endOf("month").format("YYYY-MM-DD");
      getMineCalendarData();
      return;
    } else {
      caledarList.list = [];
      userInfo.list = [];
      changeT.Time = parseInt(changeM) - 1;
      caledarList.from = dayjs().startOf("month").format("YYYY-MM-DD");
      caledarList.to = dayjs().endOf("month").format("YYYY-MM-DD");
      getAllCalendarData();
      getUserCalendarData();
    }
    return;
  }
  return;
};

const mineInfo = reactive({
  loading: false,
  list: [],
  from: dayjs().startOf("month").format("YYYY-MM-DD"),
  to: dayjs().endOf("month").format("YYYY-MM-DD"),
});
// const user = JSON.parse(localStorage.getItem('user') as string)

const isCurrent = (time: Dayjs | string) => dayjs().diff(time, "day");

const timeJoin = (y, m, d) =>
  `${y}-${m.toString().padStart(2, "0")}-${d.toString().padStart(2, "0")}`;

const userInfo = reactive({
  loading: false,
  list: [],
});

const deptFilter = reactive({
  deptId: [],
});

const caledarList = reactive({
  list: [],
  loading: false,
  from: dayjs().startOf("month").format("YYYY-MM-DD"),
  to: dayjs().endOf("month").format("YYYY-MM-DD"),
});

const currentDate = ref(
  dayjs()
    .set("month", changeT.Time - 1)
    .format("YYYY-MM-DD")
);
// ref(dayjs().format("YYYY-MM-DD"));

// 获取我的排班
const getMineCalendarData = async () => {
  mineInfo.loading = true;
  try {
    const res: any = await getMineCalendar({
      to: mineInfo.to,
      from: mineInfo.from,
    });
    mineInfo.list = res.data;
  } catch (error) {
    console.error("获取我的排班", error);
  }
  mineInfo.loading = false;
};

const getUserCalendarData = async () => {
  userInfo.loading = true;
  try {
    const res: any = await getUserCalendar({
      date: currentDate.value,
      deptId:
        !deptFilter.deptId || deptFilter.deptId.length === 0
          ? ""
          : deptFilter.deptId[deptFilter.deptId.length - 1],
      parentDeptId:
        !deptFilter.deptId || deptFilter.deptId.length === 0
          ? ""
          : deptFilter.deptId[0],
    });
    userInfo.list = res.data;
  } catch (error) {
    console.error("获取坐席", error);
  }
  userInfo.loading = false;
};

const getAllCalendarData = async () => {
  const res: any = await getAllCalendar({
    startTime: caledarList.from,
    endTime: caledarList.to,
    deptId: "",
    parentDeptId: "",
  });
  caledarList.list = res.data;
};

export {
  getMineCalendarData,
  getUserCalendarData,
  getAllCalendarData,
  userInfo,
  auth,
  mineInfo,
  currentDate,
  deptFilter,
  isCurrent,
  timeJoin,
  caledarList,
  changeTime,
};
