import {
  defineComponent,
  ref,
  reactive,
  inject,
  watch,
  watchEffect,
  toRefs,
} from "vue";
import "./index.scss";
import {
  // getOrderList,
  getUserprofile,
  getHotTopic,
} from "@/api/deli/mainScreen";
import { addLabel, getLabel, deletLabel as deletLabelApi } from "@/api/deli/im";
import { ElMessage } from "element-plus";
import { getUserId } from "../imhook";
import { userAccountInfo } from "./userHook";
// import { OrderResult, UserResult } from "../mock.js";
export default defineComponent({
  props: {
    isWx: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const activeName = ref("user");
    const userInfo: any = reactive({
      info: props.isWx
        ? inject("wxuser")
        : {
            accountName: "", // 客户名称 ,
            categoryProportion: "", // 品类占比 ,
            channelType: "", // 客户渠道类型 ,
            contract: "", // 联系方式 ,
            dateOfArchiving: "", // 合作时间 ,
            frequency: "", // 要货频次 ,
            label: "", // 客户标签 ,
            lastYearSales: "", // 去年销售额 ,
            officeName: "", // 分公司名称 ,
            ordPayment: "", // 付款条件 ,
            popularProducts: "", // 爆款产品 ,
            purchasingCycle: "", // 要货周期 ,
            sapNumber: "", // 客户编码 ,
            staffName: "", // 业务员姓名 ,
            submitTime: "", // 最近一次下单时间 ,
            thisYearSales: "", // 今年销售额 ,
            userName: "", // 咨询人
            officeIdCode: "", //
          },
      loading: false,
    });
    const labelList = reactive({
      list: [],
      loading: false,
    });
    const inputVisible = ref(false);
    const inputValue = ref("");
    const hotInfo = reactive({
      list: [],
      loading: false,
    });
    const wxUser: any = inject("wxuser");

    return {
      activeName,
      userInfo,
      labelList,
      inputVisible,
      inputValue,
      // OrderResult,
      hotInfo,
      wxUser,
      // setChatList,
    };
  },
  methods: {
    async getUser() {
      console.warn("用户画像 status", userAccountInfo.isAccount, getUserId());
      if (userAccountInfo.isAccount || getUserId()) {
        this.userInfo.loading = true;
        const res: any = await getUserprofile({
          imUserId: userAccountInfo.isAccount
            ? userAccountInfo.userId
            : getUserId(),
        });
        console.warn("用户画像", res);
        this.userInfo.info = res.data;
        this.userInfo.loading = false;
        this.getLabelData();
        // this.setChatList(res.data);
      }
    },
    async getLabelData() {
      this.labelList.loading = true;
      const label: any = await getLabel({
        imUserId: userAccountInfo.isAccount
          ? (userAccountInfo.userId as any)
          : Number(getUserId()),
      });
      this.labelList.loading = false;
      this.labelList.list = label.data;
    },

    async getHot() {
      if (userAccountInfo.isAccount || getUserId()) {
        this.hotInfo.loading = true;
        const res: any = await getHotTopic({
          imUserId: userAccountInfo.isAccount
            ? userAccountInfo.userId
            : getUserId(),
          topN: 3,
        });
        this.hotInfo.loading = false;
        this.hotInfo.list = res.data;
      }
    },
    async addLabel() {
      if (!this.inputValue) {
        // ElMessage.error("标签名不能为空！");
        this.inputVisible = false;
        return;
      }
      if (userAccountInfo.isAccount || getUserId()) {
        await addLabel({
          imUserId: userAccountInfo.isAccount
            ? (userAccountInfo.userId as any)
            : Number(getUserId()),
          labelContent: this.inputValue,
        });
        this.inputVisible = false;
        this.inputValue = "";
        ElMessage.success("添加标签成功！");
        this.getUser();
      }
    },
    async deleteLabel(item) {
      await deletLabelApi({
        labelId: item.id,
      });
      ElMessage.success("删除标签成功！");
      this.getLabelData();
    },
    openAddLabel() {
      this.inputVisible = true;
      this.$nextTick(() => {
        // 在render函数中，使用ref未生效
        // 在setup中，又不能在父组件中使用ref
        document.getElementById("label-input")?.focus();
      });
    },
  },
  render() {
    return (
      <div class="p-10 h-100 user-portrait-wrapper" style={"overflow-y:auto"}>
        {userAccountInfo.isAccount || getUserId() || this.isWx ? (
          <div class="m-t-10" v-loading={this.userInfo.loading}>
            <div class="m-b-10">
              {this.labelList.list &&
                this.labelList.list.map((item: any) => {
                  return (
                    <el-tag
                      class="m-r-10"
                      closable
                      onClose={() => this.deleteLabel(item)}
                    >
                      {item.label}
                    </el-tag>
                  );
                })}
              {this.inputVisible ? (
                <el-input
                  id="label-input"
                  v-model={this.inputValue}
                  class="ml-1 w-20 m-t-10"
                  size="small"
                  onKeyup={(e) => {
                    if (e.key === "Enter") {
                      this.addLabel();
                    }
                  }}
                  onBlur={this.addLabel}
                />
              ) : (
                getUserId() && (
                  <el-button
                    class="button-new-tag ml-1"
                    size="small"
                    onClick={() => this.openAddLabel()}
                  >
                    + 添加标签
                  </el-button>
                )
              )}
            </div>
            <el-button type="primary" plain size="small" class="m-b-5">
              客户概况
            </el-button>
            <el-descriptions size="small" column={2} border>
              <el-descriptions-item label="客户编码：">
                {this.userInfo.info?.sapNumber}
              </el-descriptions-item>
              <el-descriptions-item label="客户名称：">
                {this.userInfo.info?.accountName}
              </el-descriptions-item>
              <el-descriptions-item label="销售办公室编码：">
                {this.userInfo.info?.officeIdCode}
              </el-descriptions-item>
              <el-descriptions-item label="客户渠道类型：">
                {this.userInfo.info?.channelType}
              </el-descriptions-item>
              <el-descriptions-item label="咨询人：">
                {this.isWx
                  ? this.wxUser.userName
                  : this.userInfo.info?.userName}
              </el-descriptions-item>
              <el-descriptions-item label="业务员：">
                {this.userInfo.info?.staffName}
              </el-descriptions-item>
              <el-descriptions-item label="联系方式：">
                {this.userInfo.info?.contract}
              </el-descriptions-item>

              <el-descriptions-item label="分公司：">
                {this.userInfo.info?.officeName}
              </el-descriptions-item>
            </el-descriptions>
            <el-button type="primary" plain size="small" class="m-b-5 m-t-20">
              要货品类
            </el-button>
            <el-descriptions size="small" column={2} border>
              <el-descriptions-item label="要货频次：">
                {this.userInfo.info?.frequency}
              </el-descriptions-item>
              <el-descriptions-item label="要货周期：">
                {this.userInfo.info?.purchasingCycle}
              </el-descriptions-item>
              <el-descriptions-item span={2} label="品类占比">
                {this.userInfo.info?.categoryProportion}
              </el-descriptions-item>
              <el-descriptions-item label="爆款产品：">
                {this.userInfo.info?.popularProducts}
              </el-descriptions-item>
            </el-descriptions>
            <el-button type="primary" plain size="small" class="m-b-5 m-t-20">
              客户合作
            </el-button>
            <el-descriptions size="small" column={2} border>
              <el-descriptions-item label="合作时间：">
                {this.userInfo.info?.dateOfArchiving}
              </el-descriptions-item>
              <el-descriptions-item label="最后一次下单时间：">
                {this.userInfo.info?.submitTime}
              </el-descriptions-item>
              <el-descriptions-item label="去年销售额：">
                {this.userInfo.info?.lastYearSales}
              </el-descriptions-item>
              <el-descriptions-item label="今年销售额：">
                {this.userInfo.info?.thisYearSales}
              </el-descriptions-item>
              <el-descriptions-item label="付款条件：" span={2}>
                {this.userInfo.info?.ordPayment}
              </el-descriptions-item>
            </el-descriptions>
            <el-button type="primary" plain size="small" class="m-b-5 m-t-20">
              高频问题
            </el-button>
            {this.hotInfo.list &&
              this.hotInfo.list.map((item: any) => {
                return (
                  <div class="question-item">
                    <span class="ask-item">问</span>
                    {item.question}
                    <span class="tip">{item.hitTimes}</span>
                  </div>
                );
              })}
          </div>
        ) : (
          <el-empty
            image={require("@/assets/no-user.png")}
            image-size={100}
            description="暂无用户信息"
          ></el-empty>
        )}
      </div>
    );
  },
});
