import "./index.scss";
import { defineComponent, ref, reactive } from "vue";
import { wordInfo, recomardInfo } from "./recomardHook";
import { currentGroupId, sendMsg } from "../imhook";
import { inputDom } from "@/components/chat/hook";
import "@/assets/iconfount/download/font_jupfor0uetc/iconfont.css";
import RsvpPreviewImg from "@/components/rsvp-previewImg";
import ProductInfo from "./productInfo";
export default defineComponent({
  emits: ["send"],
  setup() {
    const getCurrentProduct = () => {
      const idx = recomardInfo.value.findIndex(
        (item) => item.id === currentGroupId.value
      );
      if (idx !== -1) {
        const list = recomardInfo.value[idx]["productList"];
        const current = list.find(
          (item) => item.productCode === recomardInfo.value[idx].activeId
        );
        if (current) {
          const imgList = current?.productPicUrls || [];
          return {
            ...current,
            productPicUrls: current?.productDetailPicUrl
              ? imgList.concat([current.productDetailPicUrl])
              : current.productPicUrls,
          };
        }
      } else {
        return null;
      }
    };
    const selectActiveProduct = (item) => {
      const idx = recomardInfo.value.findIndex(
        (item) => item.id === currentGroupId.value
      );
      recomardInfo.value[idx]["activeId"] = item.productCode;
    };

    const getProductList = () => {
      const idx = recomardInfo.value.findIndex(
        (item) => item.id === currentGroupId.value
      );
      let arr = [];
      if (recomardInfo.value[idx] && recomardInfo.value[idx]["productList"]) {
        arr = recomardInfo.value[idx]["productList"];
      }
      return arr;
    };

    const preImgDom = ref<InstanceType<typeof RsvpPreviewImg>>();

    const previewImg = (index) => {
      preImgDom.value?.open({
        index: index,
        list: getCurrentProduct()?.productPicUrls,
      });
    };

    const timer = ref();

    return () => (
      <>
        <div class="item-wrapper">
          {/* 推荐信息 */}
          <div v-loading={wordInfo.loading} class="item-top">
            <el-scrollbar height="100%">
              {recomardInfo.value.find(
                (item) => item.id === currentGroupId.value
              )?.words.length ? (
                <div>
                  <h4 class="m-b-10">话术推荐</h4>
                  {recomardInfo.value
                    .find((item) => item.id === currentGroupId.value)
                    ?.words?.map((item) => (
                      <div
                        class="recomard-content m-b-10"
                        onClick={() => {
                          if (timer.value) {
                            clearTimeout(timer.value);
                            timer.value = null;
                            return;
                          }
                          timer.value = setTimeout(() => {
                            (inputDom.value as any).setHtml(`${item}`);
                            clearTimeout(timer.value);
                            timer.value = null;
                          }, 500);
                        }}
                        onDblclick={() => {
                          sendMsg({
                            type: "text",
                            content: item,
                          });
                        }}
                      >
                        {item}
                      </div>
                    ))}
                </div>
              ) : (
                <el-empty
                  image={require("@/assets/no-recommend.png")}
                  image-size={80}
                  description="暂无推荐话术"
                ></el-empty>
              )}
            </el-scrollbar>
          </div>
          <el-divider />
          <div class="item-bottom">
            {getProductList().length === 0 ? (
              <div>
                <el-empty
                  image={require("@/assets/no-recommend-product.png")}
                  image-size={240}
                  description="暂无推荐产品"
                ></el-empty>
              </div>
            ) : (
              <h4 class="m-b-10">产品推荐</h4>
            )}
            <div class="flex">
              <el-scrollbar>
                <div style={"white-space:nowrap;padding-bottom: 10px"}>
                  {getProductList().length > 0 &&
                    getProductList().map((item: any, idx) => {
                      return (
                        <div
                          class={[
                            item.productCode ===
                            recomardInfo.value[
                              recomardInfo.value.findIndex(
                                (item) => item.id === currentGroupId.value
                              )
                            ]?.activeId
                              ? "active"
                              : "",
                            "product-info",
                          ]}
                          onClick={() => selectActiveProduct(item)}
                        >
                          {item.cargoNumber}
                          {item.productType}
                        </div>
                      );
                    })}
                </div>
              </el-scrollbar>
            </div>
            {getCurrentProduct() && (
              <el-scrollbar height="calc(100% - 62px)">
                <ProductInfo
                  currentProduct={getCurrentProduct()}
                  onPreviewImg={(idx) => previewImg(idx)}
                ></ProductInfo>
              </el-scrollbar>
            )}
          </div>
        </div>
        <RsvpPreviewImg ref={preImgDom} onSendMsg={sendMsg} />
      </>
    );
  },
});
