import { defineComponent, onMounted, reactive, ref } from "vue";
import { getDeptCommon, getDeptDetail } from "@/api/deli/setting";

export default defineComponent({
  setup() {
    const filter = reactive({
      pageNum: 1,
      pageSize: 10,
      keyword: "",
    });

    const commonInfo = reactive({
      list: [],
      loading: false,
      total: 0,
    });

    const dialogVisible = ref(false);
    const list = ref([]);

    const getList = async () => {
      commonInfo.loading = true;
      const res: any = await getDeptCommon(filter);
      commonInfo.list = res.data.records;
      commonInfo.total = res.data.total;
      commonInfo.loading = false;
    };
    const searchList = () => {
      filter.pageNum = 1;
      getList();
    };
    onMounted(async () => {
      getList();
    });

    const showDetail = async (deptId: number) => {
      dialogVisible.value = true;
      const res: any = await getDeptDetail({
        deptId,
      });
      list.value = res.data;
    };
    return () => (
      <>
        <el-space>
          <el-input
            placeholder="请输入部门名称"
            v-model={filter.keyword}
          ></el-input>
          <el-button type="primary" onClick={searchList}>
            搜索
          </el-button>
        </el-space>
        <div class="m-t-20">
          <el-table data={commonInfo.list} style="width: 100%">
            <el-table-column prop="deptName" label="所属部门" />
            <el-table-column prop="count" label="常用语条数" />
            <el-table-column
              label="操作"
              v-slots={{
                default: (scope) => (
                  <el-link
                    type="primary"
                    underline={false}
                    onClick={() => showDetail(scope.row.deptId)}
                  >
                    详情
                  </el-link>
                ),
              }}
            />
          </el-table>
          <div class="m-t-20">
            {
              <el-pagination
                background
                layout="prev, pager, next"
                total={commonInfo.total}
                class="f-r"
                pageSize={filter.pageSize}
                // hide-on-single-page
                v-model={filter.pageNum}
                onCurrentChange={(page) => {
                  filter.pageNum = page;
                  getList();
                }}
              />
            }
          </div>
        </div>
        <el-dialog v-model={dialogVisible.value} title="常用语详情" width="30%">
          <el-table
            data={list.value}
            show-header={false}
            style="width: 100%"
            onRowClick={(e) => console.log(e)}
          >
            <el-table-column
              label="常用语"
              v-slots={{ default: (scope) => scope.row }}
            ></el-table-column>
          </el-table>
        </el-dialog>
      </>
    );
  },
});
