import {
  getRecommendProduct,
  getRecommendWords,
  getMainScreen,
} from "@/api/deli/mainScreen";
import { reactive, ref } from "vue";
import { getUserId } from "../imhook";
const wordInfo: {
  loading: boolean;
  list: any;
} = reactive({
  loading: false,
  list: [],
});

const productInfo: {
  loading: boolean;
  list: any;
  // activeId: string;
} = reactive({
  loading: false,
  list: [],
  // activeId: "",
});

const recomardInfo = ref<any[]>([]);

const getRecommendProductData = async (query, id) => {
  const search = query
    .replaceAll(":", "")
    .replaceAll("[", "")
    .replaceAll("]", "");
  productInfo.loading = true;
  const p: any = await getRecommendProduct({
    query: search,
  });
  // console.log("getRecommendProductData", id, getUserId(id));
  if (p.data && p.data.productCode && getUserId(id)) {
    const res: any = await getMainScreen({
      imUserId: getUserId(id),
      productCode: p.data.productCode,
    });
    p.data.num = res.data?.amount || "";
    p.data.productPicUrls = p.data.productPicUrls || [];
    const idx = recomardInfo.value.findIndex((item) => item.id === id);
    if (idx === -1) {
      recomardInfo.value.push({
        id,
        productList: [p.data],
        activeId: p.data.productCode,
        words: [],
        initNum: 0,
      });
    } else {
      const list = recomardInfo.value[idx]["productList"];
      const index = list.findIndex(
        (item) => item.productCode === p.data.productCode
      );
      recomardInfo.value[idx]["activeId"] = p.data.productCode;
      if (index === -1) {
        if (list.length >= 5) {
          list.pop();
        }
      } else {
        recomardInfo.value[idx]["productList"].splice(index, 1);
      }
      recomardInfo.value[idx]["productList"].unshift(p.data);
    }
    productInfo.list = p.data;
  }
  productInfo.loading = false;
};

const getRecommendWordsData = async (query, id) => {
  // console.log("getRecommendWordsData", id, getUserId(id));
  if (getUserId(id)) {
    wordInfo.loading = true;
    const w: any = await getRecommendWords({
      query: query.replaceAll(":", "").replaceAll("[", "").replaceAll("]", ""),
      score: 0.8,
      imUserId: getUserId(id),
    });
    wordInfo.list = w.data || [];
    if (w.data) {
      const idx = recomardInfo.value.findIndex((item) => item.id === id);
      if (idx === -1) {
        recomardInfo.value.push({
          id,
          words: w.data,
          productList: [],
          activeId: "",
          initNum: 0,
        });
      } else {
        recomardInfo.value[idx].words = w.data || [];
      }
    }
    wordInfo.loading = false;
  }
};

const getRecommendData = async (query, id) => {
  getRecommendProductData(query, id);
  getRecommendWordsData(query, id);
};

export { getRecommendData, wordInfo, productInfo, recomardInfo };
