import { Request } from "@/utils/request";

// 提交知识库
export const submitCommit = (data) => {
  return Request({
    url: "/faq-commit/commit",
    data,
    method: "post",
  });
};

//  获取知识审核列表
export const getCommitPage = (params) => {
  return Request({
    url: "/faq-commit/page",
    params,
    method: "post",
  });
};

// 设置知识审批状态
export const getCommitStatus = (params) => {
  return Request({
    url: "/faq-commit/status",
    params,
    method: "post",
  });
};
