
import { defineComponent, ref } from "vue";
export default defineComponent({
  props: ["modelValue"],
  emits: ["update:modelValue", "change", "typechange"],
  setup() {
    const dom = ref();
    return {
      dom,
    };
  },
  methods: {
    changeInput(val, node) {
      this.$emit("typechange", node);
      const data: any = this.$attrs;
      if (data.props.emitPath === false) {
        this.$emit("update:modelValue", [node.value]);
        this.$emit("change", [node.value]);
      } else {
        this.$emit("update:modelValue", node.pathValues);
        this.$emit("change", node.pathValues);
      }
      (this.$refs.dom as any).handleExpandChange();
    },
  },
});
