import {
  defineComponent,
  PropType,
  reactive,
  ref,
  toRefs,
  onMounted,
  computed,
} from "vue";
import Title from "@/components/title/index.vue";
import RsvpRichTextarea from "@/components/rsvp-richTextarea/index.vue";
import ChatCom from "./chatList";

import {
  endIm,
  transferIm,
  getCustomList,
  chatList as chatListData,
  currentGroupId,
} from "@/views/assist/imhook";
import { ElMessage, ElMessageBox } from "element-plus";
import { answer, question, getTree, treeList, inputDom } from "./hook";
import {
  userAccountInfo,
  sendMessage,
  serveName,
} from "@/views/assist/components/userHook";

import RsvpSplit from "@/components/rsvp-split/index.vue";
import dayjs from "dayjs";
import "./index.scss";
import Emo from "./emo";

// import { colSize } from "ant-design-vue/lib/grid/Col";
// import { uploadFile } from "@/api/deli/im";

export default defineComponent({
  emits: ["saveStore", "sendFile", "send"],
  props: {
    name: {
      type: String as PropType<string>,
      default: "",
    },
    commonInfo: {
      type: Object as PropType<{ loading: boolean; list: any[] }>,
      default() {
        return {
          loading: false,
          list: [],
        };
      },
    },
  },
  setup(
    props: {
      name: string;
      commonInfo: { loading: boolean; list: any[] };
    },
    context
  ) {
    const { commonInfo } = toRefs(props) || [];

    const imageFile = ref([]);
    // 表情相关
    const popVisible = ref(false);

    const message = ref("");

    // Im转交
    const dialogVisible = ref(false);
    // const transfromId = ref("");
    // 常用语功能
    const wordsVisible = ref(false);
    const wordsActive = ref("person");
    // 输入框改变时，匹配的常用语
    const matchWordsVisible = ref(false);
    const matchList = ref<any[]>([]);
    const split = ref(0.7);
    const timer = ref();

    const imgFile = ref();
    const linkFile = ref();
    // 转交用户列表
    const userList: {
      config: any;
      loading: boolean;
    } = reactive({
      config: {},
      loading: false,
    });

    const chatList = computed(() => {
      const obj = chatListData.value.find(
        (item) => item.groupId === currentGroupId.value
      );
      inputDom.value?.focus();
      if (obj) {
        return {
          list: obj.list,
          portrait: obj.portrait,
        };
      } else {
        return {
          list: [],
          portrait: {},
        };
      }
    });

    const seachTime = ref();

    onMounted(() => {
      getTree();
    });

    // 生成问答对
    const questionVisible = ref(false);

    // 上传文件
    const uploadFileMessage = async (info) => {
      if (info.file.size / 1024 / 1024 > 100) {
        ElMessage.error("文件大小不能超过100兆");
        return;
      }

      let type = "link";
      if (info.file.type.split("/")[0] === "image") {
        type = "image";
      } else if (info.file.type.split("/")[0] === "video") {
        type = "video";
      } else if (info.file.type.split("/")[0] === "audio") {
        type = "audio";
      }

      context.emit("sendFile", info.file, type);
      imgFile.value && imgFile.value.clearFiles();
      linkFile.value && linkFile.value.clearFiles();
    };

    // 发送消息
    const sendText = (msg?: string) => {
      if (
        (message.value &&
          message.value.replaceAll(" ", "").replaceAll("\n", "")) ||
        msg
      ) {
        if (userAccountInfo.isAccount) {
          sendMessage(msg || message.value);
          message.value = "";
        } else {
          context.emit("send", msg || message.value);
          message.value = "";
        }
      } else {
        ElMessage.error("发送内容不能为空！");
      }
    };

    // 获取转交用户
    const checkTransfer = () => {
      userList.loading = true;
      getCustomList((users) => {
        userList.loading = false;
        dialogVisible.value = true;
        const config: any = {};
        users.forEach((element) => {
          if (!(element.org_id in config)) {
            config[element.org_id] = [];
          }
          console.log("config", config);
          config[element.org_id].push(element);
        });
        if (users.length > 0) {
          userList.config = config;
          console.log("users234234", config);
        } else {
          ElMessage.error("暂无在线客服");
        }
      });
    };

    // 发送表情
    const sendEmo = (text, idx) => {
      document.execCommand(
        "insertHTML",
        false,
        `<img src=${require("@/assets/empty.png")} class='qqface qqface${idx}' data='${idx}' />`
      );
      popVisible.value = false;
    };

    // 打开表情
    const openEmo = (e) => {
      e.stopPropagation();
      wordsVisible.value = false;
      console.log("e", e, popVisible.value);
      popVisible.value = !popVisible.value;
      e.preventDefault();
    };

    // 转交IM
    const tranfromIM = (id) => {
      transferIm(id);
      dialogVisible.value = false;
    };

    // 渲染常用语列表
    const renderCommon = (type: 1 | 2 | 3) => {
      return (
        commonInfo.value.list.length > 0 &&
        commonInfo.value.list
          .filter((item) => item.type === type)
          .map((item) => (
            <li
              onClick={() => {
                if (timer.value) {
                  clearTimeout(timer.value);
                  timer.value = null;
                  return;
                }
                timer.value = setTimeout(() => {
                  (inputDom.value as any).setHtml(`${item.content}`);
                  wordsVisible.value = false;
                  clearTimeout(timer.value);
                  timer.value = null;
                }, 200);
              }}
              onDblclick={(e) => {
                if (userAccountInfo.isAccount) {
                  sendMessage(item.content);
                } else {
                  context.emit("send", item.content);
                }
              }}
            >
              {item.content}
            </li>
          ))
      );
    };

    // 输入文本改变,计算是否有匹配常用语
    const changeMessage = (e) => {
      console.log("e");
      clearTimeout(seachTime.value);
      seachTime.value = setTimeout(() => {
        console.log("e11", e, message.value);
        if (!message.value) {
          matchWordsVisible.value = false;
          return;
        }
        if (commonInfo.value.list.length > 0) {
          matchList.value =
            commonInfo.value.list.filter(
              (item) => item.content.indexOf(message.value) !== -1
            ) || [];
          if (matchList.value.length > 0) {
            matchWordsVisible.value = true;
          } else {
            matchWordsVisible.value = false;
          }
        } else {
          matchWordsVisible.value = false;
        }
      }, 500);
    };

    return () => (
      <div
        class="h-100 p-r-10"
        onMousedown={(e) => {
          // e.preventDefault()
          wordsVisible.value = false;
          popVisible.value = false;
          dialogVisible.value = false;
        }}
      >
        <Title
          title=""
          isBorder={false}
          style={{ display: "block", marginRight: 0 }}
          v-slots={{
            default: () => (
              <div style={"line-height: 1.5"}>
                <span class="font-14 font-600">
                  {userAccountInfo.isAccount
                    ? userAccountInfo.staffName
                    : chatList.value.list &&
                      chatList.value.list.find((item) => item.from.type === 0)
                        ?.from.username}
                </span>
                {userAccountInfo.isAccount ||
                !chatList.value.list.find(
                  (item) => item.from.type === 0
                ) ? null : (
                  <span class="font-12 font-yellow m-l-10">
                    {chatList.value.portrait?.accountName}
                  </span>
                )}
                {userAccountInfo.isAccount ? (
                  <span class="f-r font-14" style={"line-height: 1.5"}>
                    {serveName.value}
                  </span>
                ) : (
                  <div class="f-r">
                    <el-popover
                      placement="bottom"
                      width={300}
                      trigger="click"
                      visible={dialogVisible.value}
                      v-slots={{
                        reference: () => {
                          return (
                            <el-button
                              size="small"
                              disabled={chatListData.value.length === 0}
                              type="primary"
                              onClick={checkTransfer}
                            >
                              转交
                            </el-button>
                          );
                        },
                      }}
                    >
                      <el-scrollbar max-height={500}>
                        <el-collapse accordion>
                          {Object.keys(userList.config).map((key) => {
                            return (
                              <el-collapse-item
                                title={
                                  (
                                    treeList.value.find(
                                      (item: any) => item.deptId === Number(key)
                                    ) as any
                                  ).deptName
                                }
                                name={key}
                              >
                                <ul class="tranform-wrapper">
                                  {userList.config[key].map((item) => {
                                    return (
                                      <li
                                        onClick={() => {
                                          if (item.online) {
                                            dialogVisible.value = false;
                                            ElMessageBox.confirm(
                                              `是否确认将会话转交给${item.username}`,
                                              "转交确认",
                                              {
                                                confirmButtonText: "转交",
                                                cancelButtonText: "取消",
                                                type: "warning",
                                              }
                                            ).then(() => {
                                              tranfromIM(item._id);
                                            });
                                          } else {
                                            ElMessage.error(
                                              "不能转交给离线坐席！"
                                            );
                                          }
                                        }}
                                      >
                                        {item.username}
                                        <span
                                          class={[
                                            item.online ? "online" : "offline",
                                          ]}
                                        >
                                          （{item.online ? "在线" : "离线"}）
                                        </span>
                                      </li>
                                    );
                                  })}
                                </ul>
                              </el-collapse-item>
                            );
                          })}
                        </el-collapse>
                      </el-scrollbar>
                    </el-popover>
                    <el-button
                      size="small"
                      disabled={chatListData.value.length === 0}
                      type="danger"
                      onClick={() => endIm()}
                    >
                      结束服务
                    </el-button>
                  </div>
                )}
              </div>
            ),
          }}
        ></Title>
        <div style={"height:calc(100vh - 124px);"}>
          <RsvpSplit
            value={split.value}
            onInput={(val: any) => (split.value = val)}
            mode="vertical"
            min="400px"
            max="200px"
            v-slots={{
              top: () => (
                <div
                  v-loading={userAccountInfo.loading}
                  style={{ backgroundColor: "#f0f2f5", height: "100%" }}
                >
                  <ChatCom
                    chatList={
                      userAccountInfo.isAccount
                        ? userAccountInfo.list
                        : chatList.value.list
                    }
                  />
                </div>
              ),
              bottom: () => (
                <div class="h-100">
                  {userAccountInfo.isAccount ? (
                    <div class="p-t-10 p-l-10 p-r-10 border-b box-sizing">
                      <el-space szie={30}>
                        <el-popover
                          placement="top"
                          width={400}
                          visible={wordsVisible.value}
                          trigger="click"
                          v-slots={{
                            reference: () => {
                              return (
                                <i
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    popVisible.value = false;
                                    wordsVisible.value = !wordsVisible.value;
                                  }}
                                  class="iconfont icon-changyongyu font-20 pointer"
                                ></i>
                              );
                            },
                          }}
                        >
                          <el-tabs
                            v-model={wordsActive.value}
                            class="demo-tabs"
                          >
                            <el-tab-pane label="个人常用语" name="person">
                              <el-scrollbar height="180px">
                                <ul class="common-wrapper">
                                  {renderCommon(1)}
                                </ul>
                              </el-scrollbar>
                            </el-tab-pane>
                            <el-tab-pane label="省区常用语" name="provice">
                              <el-scrollbar height="180px">
                                <ul class="common-wrapper">
                                  {renderCommon(3)}
                                </ul>
                              </el-scrollbar>
                            </el-tab-pane>
                            <el-tab-pane label="集团常用语" name="group">
                              <el-scrollbar height="180px">
                                <ul class="common-wrapper">
                                  {renderCommon(2)}
                                </ul>
                              </el-scrollbar>
                            </el-tab-pane>
                          </el-tabs>
                        </el-popover>
                      </el-space>
                    </div>
                  ) : (
                    <div class="p-t-10 p-l-10 p-b-5 p-r-10 border-b box-sizing">
                      <el-space size={30}>
                        <el-popover
                          placement="top"
                          visible={popVisible.value}
                          width={450}
                          trigger="click"
                          v-slots={{
                            reference: () => (
                              <i
                                class="iconfont icon-biaoqing font-20 pointer"
                                onMousedown={openEmo}
                              ></i>
                            ),
                          }}
                        >
                          <Emo
                            onSendEmo={(text, idx) => sendEmo(text, idx)}
                          ></Emo>
                        </el-popover>
                        <el-upload
                          // onChange={imageFileChange}
                          accept="image/*"
                          action=""
                          // auto-upload={false}
                          http-request={uploadFileMessage}
                          show-file-list={false}
                          ref={imgFile.value}
                          multiple={true}
                          file-list={imageFile.value}
                        >
                          <div>
                            <i class="iconfont icon-tupian font-20 pointer"></i>
                          </div>
                        </el-upload>
                        <el-upload
                          // onChange={imageFileChange}
                          action=""
                          // auto-upload={false}
                          http-request={uploadFileMessage}
                          show-file-list={false}
                          multiple={true}
                          ref={linkFile.value}
                          file-list={imageFile.value}
                        >
                          <div>
                            <i class="iconfont icon-wenjian font-20 pointer"></i>
                          </div>
                        </el-upload>
                        <el-popover
                          placement="top"
                          width={400}
                          visible={wordsVisible.value}
                          trigger="click"
                          v-slots={{
                            reference: () => {
                              return (
                                <i
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    popVisible.value = false;
                                    wordsVisible.value = !wordsVisible.value;
                                  }}
                                  class="iconfont icon-changyongyu font-20 pointer"
                                ></i>
                              );
                            },
                          }}
                        >
                          <el-tabs
                            v-model={wordsActive.value}
                            class="demo-tabs"
                          >
                            <el-tab-pane label="个人常用语" name="person">
                              <el-scrollbar height="180px">
                                <ul class="common-wrapper">
                                  {renderCommon(1)}
                                </ul>
                              </el-scrollbar>
                            </el-tab-pane>
                            <el-tab-pane label="省区常用语" name="provice">
                              <el-scrollbar height="180px">
                                <ul class="common-wrapper">
                                  {renderCommon(3)}
                                </ul>
                              </el-scrollbar>
                            </el-tab-pane>
                            <el-tab-pane label="集团常用语" name="group">
                              <el-scrollbar height="180px">
                                <ul class="common-wrapper">
                                  {renderCommon(2)}
                                </ul>
                              </el-scrollbar>
                            </el-tab-pane>
                          </el-tabs>
                        </el-popover>
                      </el-space>
                      <span class="f-r" style={"position: relative;top: -2px"}>
                        <i
                          onClick={() => {
                            questionVisible.value = true;
                            question.value = "";
                            answer.value = "";
                          }}
                          class="iconfont font-24 icon-select pointer"
                        ></i>
                      </span>
                    </div>
                  )}

                  <div
                    class="p-10 box-sizing"
                    style={"height: calc(100% - 60px)"}
                  >
                    <div style={"height: calc(100% - 60px)"}>
                      <el-popover
                        placement="top"
                        visible={matchWordsVisible.value}
                        width={450}
                        v-slots={{
                          reference: () => (
                            <RsvpRichTextarea
                              ref={inputDom}
                              v-model={message.value}
                              class="rsvp-textarea"
                              placeholder="请输入回复内容，按Enter发送"
                              disabled={
                                chatListData.value.length === 0 &&
                                !userAccountInfo.isAccount
                              }
                              isAccount={userAccountInfo.isAccount}
                              onMouseEnter={(e) => {
                                sendText(e);
                              }}
                              onKeyup={(e) => {
                                changeMessage(e);
                              }}
                              onBlur={() => {
                                matchWordsVisible.value = false;
                              }}
                            ></RsvpRichTextarea>
                          ),
                        }}
                      >
                        <el-space wrap>
                          {matchList.value.map((item) => {
                            return (
                              <span
                                class="match-content"
                                onClick={() => {
                                  message.value = item.content;
                                  (inputDom.value as any).setHtml(
                                    `<p>${item.content}</p>`
                                  );
                                  matchWordsVisible.value = false;
                                }}
                              >
                                {item.content}
                              </span>
                            );
                          })}
                        </el-space>
                      </el-popover>
                    </div>
                    <div class="m-t-10 ta-right">
                      <el-button
                        type="primary"
                        disabled={
                          chatListData.value.length === 0 &&
                          !userAccountInfo.isAccount
                        }
                        v-debounce={() => {
                          (inputDom.value as any).sendRichMsg();
                        }}
                      >
                        发送
                      </el-button>
                    </div>
                  </div>
                </div>
              ),
            }}
          ></RsvpSplit>
        </div>
        <el-dialog
          v-model={questionVisible.value}
          width="40%"
          custom-class="diy-modal"
          v-slots={{
            footer: () => {
              return (
                <div>
                  <el-button
                    type="primary"
                    round
                    onClick={() => {
                      context.emit("saveStore", {
                        answer: answer.value && answer.value.split("_")[0],
                        question:
                          question.value && question.value.split("_")[0],
                      });
                      questionVisible.value = false;
                    }}
                  >
                    确定选择
                  </el-button>
                </div>
              );
            },
          }}
        >
          <div style={"height: 50vh"}>
            <ChatCom
              chatList={chatList.value.list.filter((item) =>
                dayjs(item.createTime).isAfter(
                  new Date(new Date(new Date().toLocaleDateString()).getTime())
                )
              )}
              isEdit={true}
            />
          </div>
        </el-dialog>
      </div>
    );
  },
});
