
import { defineComponent, ref, reactive } from "vue";
import { getCommitPage, getCommitStatus } from "@/api/deli/faqCommit";
import { getBases, getCommitBaseId, createFAQ } from "@/api/deli/faq";
import TitleCom from "@/components/title/index.vue";
import dayjs from "dayjs";
import KnowledgeBuild from "@/components/knowledge/build.vue";
import checkType from "@/utils/checkType";
import { ElTable } from "element-plus";
export default defineComponent({
  components: {
    TitleCom,
    KnowledgeBuild,
  },
  setup() {
    const filter = reactive({
      pageNum: 1,
      pageSize: 10,
      dept: [],
      time: [
        dayjs().startOf("month").format("YYYY-MM-DD"),
        dayjs().endOf("month").format("YYYY-MM-DD"),
      ],
      storeId: "",
    });
    const approveInfo = reactive({
      loading: false,
      total: 0,
      list: [],
    });
    const status = ref(undefined);
    const props = reactive({
      checkStrictly: true,
      label: "deptName",
      value: "deptId",
    });
    const options = ref([]);
    const faqInfo = reactive({
      loading: false,
      list: [],
    });
    const knowledgeDom = ref<InstanceType<typeof KnowledgeBuild>>();
    const commitId = ref();
    const selectRows = ref<any>({});
    const tableDom = ref<InstanceType<typeof ElTable>>();
    const batchAddVisible = ref(false);
    const percent = ref(0);
    return {
      options,
      filter,
      approveInfo,
      props,
      // visible,
      status,
      // current,
      faqInfo,
      knowledgeDom,
      commitId,
      selectRows,
      tableDom,
      batchAddVisible,
      percent,
    };
  },
  methods: {
    dayjs,
    async getList() {
      this.approveInfo.loading = true;
      try {
        const res: any = await getCommitPage({
          pageNum: this.filter.pageNum,
          pageSize: this.filter.pageSize,
          startTime: dayjs(this.filter.time[0]).format("YYYY-MM-DD"),
          endTime: dayjs(this.filter.time[1]).format("YYYY-MM-DD"),
          deptId:
            this.filter.dept.length > 0
              ? this.filter.dept[this.filter.dept.length - 1]
              : "",
          parentDeptId:
            this.filter.dept.length === 2 ? this.filter.dept[0] : "",
        });
        this.approveInfo.list = res.data.records;
        this.approveInfo.total = res.data.total;

        this.$nextTick(() => {
          const dom = this.$refs.tableDom as InstanceType<typeof ElTable>;
          dom.clearSelection();
          this.selectRows[this.filter.pageNum] &&
            this.selectRows[this.filter.pageNum].forEach((item) => {
              const index = this.approveInfo.list.findIndex(
                (da: any) => da.faqCommitId === item.faqCommitId
              );
              dom.toggleRowSelection(this.approveInfo.list[index], true);
            });
        });
      } catch (error) {
        console.error("getCommitPage", error);
      }
      this.approveInfo.loading = false;
    },
    handleSelectionChange(val) {
      if (checkType.isArray(val)) {
        this.selectRows[this.filter.pageNum] = val;
      } else {
        this.selectRows[this.filter.pageNum] = [val];
      }
    },
    handleSelectionAll(val) {
      if (checkType.isArray(val)) {
        this.selectRows[this.filter.pageNum] = val;
      } else {
        this.selectRows[this.filter.pageNum] = [val];
      }
    },
    async batchAdd() {
      this.batchAddVisible = true;
      let list = [];
      let finsh = 0;
      Object.keys(this.selectRows).forEach((key) => {
        const arr: any = [...new Set(this.selectRows[key])];
        list = list.concat(arr);
      });
      const res: any = await getCommitBaseId();
      try {
        for (let i = 0; i < list.length; i++) {
          await this.batchCreate(res.data, list[i]);
          finsh++;
          this.percent = finsh / list.length;
          if (finsh === list.length) {
            this.$message.success("训练完成！");
            this.selectRows = {};
            this.batchAddVisible = false;
            this.getList();
          }
        }
      } catch (error) {
        this.selectRows = {};
        this.batchAddVisible = false;
      }
    },
    async batchCreate(baseId, item) {
      try {
        await createFAQ(
          {
            baseId,
          },
          {
            answers: JSON.parse(item.answer).map((answer: any) => ({
              plainText: answer,
              richAnswer: answer,
            })),
            categoryId: item.categoryId,
            desc: item.faqDesc,
            expireTime: "",
            validateTime: "",
            questions: JSON.parse(item.question),
          }
        );
        await this.changeStatus(2, item.faqCommitId);
      } catch (error) {
        console.log("error", error);
      }
    },
    async changeStatus(status, id = "") {
      await getCommitStatus({
        commitId: id || this.commitId,
        status: status,
      });
      this.status = undefined;
      if (!id) {
        this.$message.success("训练成功！");
        this.getList();
      }
    },
    async getFaqList() {
      this.faqInfo.loading = true;
      const res = await getBases();
      this.faqInfo.loading = false;
      this.faqInfo.list = res.data as any;
    },
    resetFilter() {
      const config = {
        pageNum: 1,
        pageSize: 10,
        dept: [],
        time: [
          dayjs().startOf("month").format("YYYY-MM-DD"),
          dayjs().endOf("month").format("YYYY-MM-DD"),
        ],
        storeId: "",
      };
      Object.keys(this.filter).forEach((key) => {
        this.filter[key] = config[key];
      });
      this.getList();
    },
    // 操作方法
    approveOpt(scope) {
      const dom = this.$refs.knowledgeDom as InstanceType<
        typeof KnowledgeBuild
      >;
      // dom.form.categoryId = ["0", "-1"];
      console.log(this.$refs);
      dom.dialogVisible = true;
      dom.questionList = JSON.parse(scope.row.question).map((item) => {
        return {
          content: item,
          faqQuestionId: "",
        };
      });
      dom.answerGroup = JSON.parse(scope.row.answer).map((item) => {
        return {
          plainText: item,
          richAnswer: item,
        };
      });
      dom.setFormCategory(scope.row.categoryId);
      dom.form.desc = scope.row.faqDesc;
      // dom.form.categoryId = ["0", "" + scope.row.categoryId];
      // dom.setCategoryId(scope.row.categoryId);
      this.commitId = scope.row.faqCommitId;
    },
  },
  mounted() {
    this.getList();
    // this.getTree();
    this.getFaqList();
  },
});
