
import { defineComponent, reactive, toRefs, ref, shallowRef } from "vue";
import TitleCom from "@/components/title/index.vue";
import { serviceDataStatistics } from "@/api/deli/statistics";
import dayjs from "dayjs";
import { checkAuth } from "@/utils/help";
import TitleCom1 from "./components/title.tsx";
import {
  getTreeList,
  options,
  treeLoading,
  getDeptIdAndCompany,
  jtId,
} from "./hook";
import * as echarts from "echarts";
export default defineComponent({
  components: {
    TitleCom,
    TitleCom1,
  },
  setup() {
    const state = reactive({
      shortcuts: [
        {
          text: "最近一周",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            return [start, end];
          },
        },
        {
          text: "最近一个月",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            return [start, end];
          },
        },
        {
          text: "最近三个月",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            return [start, end];
          },
        },
      ],
      time: [dayjs().subtract(1, "month"), dayjs()],
    });
    const dataInfo = reactive({
      loading: false,
      list: [],
    });

    const deptIds = ref<any[]>([]);

    // const options = ref<
    //   { deptId: string; deptName: string; children: any[] }[]
    // >([]);

    const treePropos = reactive({
      label: "deptName",
      value: "deptId",
      checkStrictly: true,
    });

    const key = ref("totalSessionTime");

    const chartDom = ref<HTMLDivElement>();
    const mycharts = shallowRef();

    // 分页
    const pageinfo = reactive({
      pageNum: 1,
      pageSize: 9999,
    });
    const groupId = ref(0);
    const orderBy = ref(1);
    const total = ref(0);

    const depttype = ref(0);
    return {
      groupId,
      depttype,
      total,
      orderBy,
      pageinfo,
      ...toRefs(state),
      dataInfo,
      checkAuth,
      options,
      treePropos,
      chartDom,
      deptIds,
      key,
      mycharts,
      getTreeList,
      getDeptIdAndCompany,
      treeLoading,
      jtId,
    };
  },
  computed: {
    showProvice() {
      let result = "";
      if (checkAuth([1, 4])) {
        if (this.deptIds.length === 1) {
          result = "all";
        } else if (
          this.deptIds.length === 2 &&
          parseInt(this.deptIds[1]) !== parseInt(this.jtId)
        ) {
          result = "dept";
        } else {
          result = "company";
        }
      } else if (checkAuth([2, 3])) {
        console.log("this.deptIds", this.deptIds, this.jtId);
        console.log("123456789", parseInt(this.deptIds[0]));
        if (
          this.deptIds.length === 1 &&
          parseInt(this.deptIds[0]) !== parseInt(this.jtId) &&
          this.deptIds[0]
        ) {
          result = "dept";
        } else {
          result = "company";
        }
      } else {
        result = "company";
      }
      console.log("result", result);
      return result;
    },
  },
  watch: {
    orderBy() {
      this.getPageList(1);
    },
  },
  methods: {
    async getPageList(page) {
      if (this.time) {
        let time = [
          dayjs(this.time[0]).format("YYYY-MM-DD") + " 00:00:00",
          dayjs(this.time[1]).add(1, "day").format("YYYY-MM-DD") + " 23:59:59",
        ];
        this.pageinfo.pageNum = page;
        this.dataInfo.loading = true;
        console.log("deptIds", this.getDeptIdAndCompany(this.deptIds));
        const res: any = await serviceDataStatistics(
          this.orderBy,
          this.pageinfo,
          {
            ...this.getDeptIdAndCompany(this.deptIds),
            endTime: time[1],
            groupId: 1,
            needUser: this.showProvice === "company" ? 1 : 0,
            startTime: time[0],
          }
        );
        console.log("res", res);
        this.total = res.data.total;
        this.dataInfo.loading = false;
        if (res.code !== 0) {
          this.$message.error(res.msg);
          return;
        }
        const list = res.data.records;
        this.dataInfo.list = list.map((item) => ({
          ...item,
          averageSessionTime: item.averageSessionTime.toFixed(1),
          totalSessionTime: item.totalSessionTime.toFixed(1),
          averageRespTime: item.averageRespTime.toFixed(1),
        }));
        console.log(999, this.dataInfo.list);
        this.drawCharts(this.orderBy);
      }
    },
    drawCharts(orderBy) {
      this.orderBy = orderBy;
      this.$nextTick(() => {
        if (!this.$refs.chartDom) {
          return;
        }
        this.mycharts = echarts.init(this.$refs.chartDom as HTMLElement);
        this.mycharts.setOption({
          tooltip: {
            trigger: "item",
          },
          legend: {
            bottom: 10,
            left: "center",
          },
          xAxis: {
            data: this.dataInfo.list.map((item: any) => item.name),
            nameTextStyle: {
              color: "red",
            },
            axisLabel: {
              interval: 0,
              rotate: 60,
              width: 100,
              overflow: "breakAll",
            },
          },
          // dataZoom: [
          //   {
          //     show: this.dataInfo.list.length > 10 ? true : false,
          //     disabled: this.dataInfo.list.length > 10 ? false : true,
          //     start: 0,
          //     end: this.dataInfo.list.length > 10 ? 50 : 100,
          //   },
          // ],
          grid: {
            left: "60",
            x2: "10",
            y2: 100,
          },
          yAxis: {
            type: "value",
            axisLabel: {
              formatter: function (value, index) {
                if (value > 100000) {
                  return Number(value / 10000) + "万";
                } else {
                  return value;
                }
              },
            },
          },
          series: [
            {
              type: "bar",
              barWidth: this.dataInfo.list.length > 20 ? "20" : "30",
              data: this.dataInfo.list.map((item: any) => item[this.key]),
            },
          ],
        });
      });
    },
    async initData() {
      await this.getTreeList();
      this.depttype = JSON.parse(
        localStorage.getItem("user") as string
      ).deptType;
      console.log("user", JSON.parse(localStorage.getItem("user") as string));
      const id = this.options && this.options[0].deptId;
      this.deptIds = [id] as any[];
      this.getPageList(1);
    },
  },
  mounted() {
    this.initData();
  },
});
