
import { defineComponent, ref, reactive } from "vue";
import { ElForm } from "element-plus";
import CommonTable from "./components/commonTable.vue";
import {
  getCommonList,
  createCommonWords,
  deleteCommonWords,
  updateCommonWords,
} from "@/api/deli/setting";
import { checkAuth } from "@/utils/help";
import { message } from "ant-design-vue";
import TitleCom from "@/components/title/index.vue";
export default defineComponent({
  components: {
    TitleCom,
    CommonTable,
  },
  setup() {
    const dialogVisible = ref(false);
    const form: {
      text: string;
      id?: number;
    } = reactive({
      text: "",
    });
    const fromDom = ref<InstanceType<typeof ElForm>>();
    const rules = reactive({
      text: [
        { required: true, message: "请输入常用语", tigger: "blur" },
        { max: 200, message: "长度请保持在200字以内", tigger: "blur" },
      ],
    });
    const tableData = ref<any[]>([]);
    const tabsActive = ref("person");
    return {
      dialogVisible,
      form,
      rules,
      fromDom,
      tabsActive,
      tableData,
      checkAuth,
    };
  },
  methods: {
    changePage() {
      console.log(1);
    },
    async deleteData(id) {
      await deleteCommonWords({
        id,
      });
      message.success("删除成功！");
      this.getList();
    },
    submit() {
      (this.$refs.fromDom as InstanceType<typeof ElForm>).validate((valid) => {
        if (valid) {
          if (this.form?.id) {
            updateCommonWords({
              id: this.form?.id as number,
              content: this.form.text,
            }).then(() => {
              this.$message.success("修改成功!");
              this.dialogVisible = false;
              this.getList();
            });
          } else {
            createCommonWords({
              content: this.form.text,
              type: this.tabsActive === "person" ? 1 : 2,
            }).then(() => {
              this.$message.success("创建成功!");
              this.dialogVisible = false;
              this.getList();
            });
          }
        }
      });
    },
    async getList() {
      const res = await getCommonList();
      this.tableData = res.data instanceof Array ? res.data : [];
      console.log(res);
    },
    editClick(item) {
      this.dialogVisible = true;
      this.form.text = item.content;
      this.form.id = item.id;
    },
  },
  mounted() {
    this.getList();
  },
});
