
import { defineComponent, onMounted, reactive, ref } from "vue";
import TitleCom from "@/components/title/index.vue";
import { CirclePlus } from "@element-plus/icons";
import { getDeptTree, deptTree, getRoleList, roles } from "./hook";
import {
  dialogVisible,
  current,
  Dept,
  companyForm,
} from "./components/addDeptHook";
import { getDeptUser, deleteDept } from "@/api/deli/setting";
import AddDept from "./components/addDept.vue";
export default defineComponent({
  components: {
    TitleCom,
    CirclePlus,
    AddDept,
  },
  setup() {
    onMounted(async () => {
      await getDeptTree();
      await getRoleList();
    });
    const filter = reactive({
      roleId: "",
      pageNum: 1,
      pageSize: 10,
      deptId: 0,
    });
    const defaultProps = reactive({
      children: "children",
      label: "deptName",
      class: (data) => {
        if (
          data.deptId === filter.deptId ||
          (data.deptName === "得力集团" && filter.deptId === 0)
        ) {
          return "is-penultimate";
        }
        return null;
      },
    });

    const users = reactive<{ list: any[]; loading: boolean }>({
      list: [],
      loading: false,
    });

    const hoverIndex = ref("");

    const total = ref(0);
    return {
      deptTree,
      defaultProps,
      roles,
      filter,
      users,
      total,
      hoverIndex,
    };
  },
  methods: {
    // 删除部门
    async deleteOpt(deptId: number) {
      await deleteDept({ deptId: deptId });
      this.$message.success("删除成功！");
      await getDeptTree();
    },
    handleNodeClick(data: { label: string; children: any[]; deptId?: number }) {
      this.filter.deptId = data.deptId || 0;
      this.getUserList();
    },
    createDept(data: Dept) {
      dialogVisible.value = true;
      current.deptName = data.deptName;
      current.children = data.children;
      companyForm.deptName = "";
      companyForm.deptCode = "";
      current.opt = "create";
      if ("parentDeptId" in data) {
        current.parentDeptId = data.parentDeptId;
      }
      current.root = data.root;
      current.deptId = data.deptId;
    },
    async getUserList() {
      this.users.loading = true;
      const res = await getDeptUser(this.filter);
      if (res.data !== null) {
        this.users.list =
          (res.data as any).records instanceof Array
            ? (res.data as any).records
            : [];
        this.users.loading = false;
        this.total = (res.data as any).total;
      }
    },
    async editDept(data: Dept) {
      dialogVisible.value = true;
      current.opt = "edit";
      current.deptId = data.deptId;
      current.deptName = data.deptName;
      current.parentDeptId = data.parentDeptId;
      companyForm.deptName = data.deptName;
      if (data.parentDeptId === 0) {
        companyForm.deptCode = data.deptCode as any;
      }
    },
  },
  mounted() {
    this.getUserList();
  },
});
