
// import PageHeader from "@/components/pageHader/index";
import TitleCom from "@/components/title/index.vue";
import { defineComponent, reactive, toRefs, ref } from "vue";
import SessionInfo from "./components/detail";
import { getConversationList } from "@/api/deli/conversation";
import { getAllChannels } from "@/api/deli/statistics";
import dayjs from "dayjs";
import Filter from "./components/filter.tsx";
import { getRecordInfo, filter, getTreeData } from "./components/hook";
import { checkAuth } from "@/utils/help";
export default defineComponent({
  components: {
    TitleCom,
    SessionInfo,
    Filter,
  },
  setup() {
    const state = reactive({
      shortcuts: [
        {
          text: "最近一周",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            return [start, end];
          },
        },
        {
          text: "最近一个月",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            return [start, end];
          },
        },
        {
          text: "最近三个月",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            return [start, end];
          },
        },
      ],
      time: [dayjs().subtract(1, "month"), dayjs()],
    });
    const tableData = reactive([]);
    const dataList = reactive({
      lists: [],
      loading: false,
    });
    const pageInfo = reactive({
      page: 1,
      limit: 10,
    });
    const total = ref(0);
    const channelInfo = reactive({
      loading: false,
      list: [],
    });
    return {
      ...toRefs(state),
      tableData,
      pageInfo,
      dataList,
      total,
      filter,
      channelInfo,
      getTreeData,
    };
  },
  methods: {
    dayjs,
    getRecordInfo,
    async search() {
      if (!this.time) {
        this.$message.error("请先选择日期");
        return;
      }
      this.dataList.loading = true;
      const config: any = { ...this.filter };
      const user = JSON.parse(localStorage.getItem("user") as string);
      if (checkAuth([2]) || user.deptType === 2) {
        config.deptId = config.dept.length === 1 ? config.dept[0] : "";
        config.companyId =
          config.dept.length === 2 ? config.dept[config.dept.length - 1] : "";
      } else if (checkAuth([3])) {
        config.deptId = "";
        config.companyId = user.deptId;
      } else {
        config.companyId =
          config.dept.length === 3 ? config.dept[config.dept.length - 1] : "";
        config.deptId = config.dept.length === 2 ? config.dept[1] : "";
      }
      delete config.dept;
      const res: any = await getConversationList({
        ...config,
        ...this.pageInfo,
        startTime: dayjs(
          dayjs(this.time[0]).format("YYYY-MM-DD") + " 00:00:00"
        ).format("YYYY-MM-DDTHH:mm:ss.SSS[Z]"),
        endTime: dayjs(
          dayjs(this.time[1]).format("YYYY-MM-DD") + " 23:59:59"
        ).format("YYYY-MM-DDTHH:mm:ss.SSS[Z]"),
        groupId: 1,
      });
      this.dataList.loading = false;
      this.dataList.lists = res.data.result.rows;
      this.total = res.data.result.records;
    },
    changePage(page) {
      this.pageInfo.page = page;
      this.search();
    },
    tableRowClassName({ row }) {
      if (row.status !== 1) {
        return "success-row";
      }
    },
    changeDate(val) {
      console.log("val", val);
      if (val) {
        this.pageInfo.page = 1;
        this.search();
      }
    },
    async getChannelsList() {
      this.channelInfo.loading = true;
      const res = await getAllChannels();
      this.channelInfo.list = res.data as any;
      this.channelInfo.loading = false;
    },
    async initData() {
      this.getChannelsList();
      if (checkAuth([1, 2, 3, 4])) {
        await this.getTreeData();
      }
      this.search();
    },
  },
  mounted() {
    this.initData();
  },
});
