
import { defineComponent, onMounted, reactive, ref } from "vue";
import PHeader from "@/components/pageHader/index";
import {
  getUsers,
  deleteUser,
  banUser,
  exportUsers,
} from "@/api/deli/userManagement";
import { User } from "@/gtypes/User";
import { deptList, getTreeDept } from "./memberHook";
import AddUser from "./components/addUser.vue";
import { dialogVisible, userForm } from "./components/hook";
import { checkAuth, resetReactiveData } from "@/utils/help";
export default defineComponent({
  components: {
    PHeader,
    AddUser,
  },
  setup() {
    const labels = reactive({
      label: "",
    });
    const states = reactive({
      options: [
        {
          value: 0,
          label: "离线",
        },
        {
          value: 1,
          label: "在线",
        },
      ],
    });
    const defaultProps = reactive({
      checkStrictly: true,
      label: "deptName",
      value: "deptId",
    });
    const tableData = reactive({
      list: [],
      loading: false,
    });
    const filter = reactive({
      dept: [],
      roleId: "",
      userNameKeyword: "",
      workIdKeyword: "",
      pageNum: 1,
      pageSize: 10,
      total: 0,
      onlineStatus: "",
    });
    const user: User = ref(JSON.parse(localStorage.getItem("user") || "{}"));
    const userAuth = ref(checkAuth([1]));
    onMounted(async () => {
      await getTreeDept(user.value.deptId);
    });
    return {
      defaultProps,
      tableData,
      filter,
      user,
      deptList,
      dialogVisible,
      userForm,
      userAuth,
      resetReactiveData,
      states,
      labels,
    };
  },
  methods: {
    // 导出
    async Export() {
      const deptId = this.filter.dept
        ? this.filter.dept[this.filter.dept.length - 1]
        : "";
      const parentDeptId =
        this.filter.dept && this.filter.dept.length === 2
          ? this.filter.dept && this.filter.dept[0]
          : this.filter.dept && this.filter.dept.length === 1
          ? 0
          : "";
      const roleId =
        (this.filter.roleId as any) === "" ? "" : (this.filter.roleId as any);
      const mes = this.$message({
        type: "info",
        duration: 0,
        message: "数据导出中....",
      });
      await exportUsers({
        deptId: (deptId as any) === 0 ? parentDeptId : deptId,
        onlineStatus: this.filter.onlineStatus as any,
        parentDeptId,
        roleId: roleId,
        userNameKeyword: this.filter.userNameKeyword,
        workIdKeyword: this.filter.workIdKeyword,
      });
      mes.close();
    },
    async getUserList() {
      this.tableData.loading = true;
      const parentDeptId =
        this.filter.dept && this.filter.dept.length === 2
          ? this.filter.dept && this.filter.dept[0]
          : this.filter.dept && this.filter.dept.length === 1
          ? 0
          : "";
      const deptId = this.filter.dept
        ? this.filter.dept[this.filter.dept.length - 1]
        : "";
      const res = await getUsers(
        { pageNum: this.filter.pageNum, pageSize: this.filter.pageSize },
        {
          onlineStatus: this.filter.onlineStatus as any,
          userNameKeyword: this.filter.userNameKeyword,
          workIdKeyword: this.filter.workIdKeyword,
          deptId: (deptId as any) === 0 ? parentDeptId : deptId,
          parentDeptId,
        }
      );
      this.tableData.loading = false;
      const result = res.data as any;
      this.tableData.list = result.records;
      this.filter.total = result.total;
    },
    async deleteOpt(id: number) {
      await deleteUser({ userId: id });
      this.$message.success("删除成功！");
      this.getUserList();
    },
    editUser(obj) {
      this.userForm.userId = obj.userId;
      this.userForm.userLoginName = obj.userLoginName;
      this.userForm.userName = obj.userName;
      this.userForm.userPhone = obj.userPhone || "";
      this.userForm.userEmail = obj.userEmail || "";
      this.userForm.workId = obj.workId;
      this.userForm.userRoleId = obj.roleId;
      if (obj.roleId === 3 && obj.parentDeptId === 0) {
        this.userForm.deptId = [obj.deptId, 0] as any;
        console.log("this.userForm.deptId", this.userForm.deptId);
      } else {
        this.userForm.deptId = [obj.deptId] as any;
      }
      this.userForm.deptName = obj.deptName;
      this.dialogVisible = true;
    },
    async startUserStatus(obj) {
      await banUser({ userId: obj.userId });
      this.$message.success("操作成功！");
      this.getUserList();
    },
    addUser() {
      this.resetReactiveData(this.userForm);
      this.dialogVisible = true;
      this.userForm.userRoleId = checkAuth([2]) ? 3 : "";
    },
    changeFilter(page) {
      this.filter.pageNum = page;
      this.getUserList();
    },
    resetFilter() {
      this.filter.pageNum = 1;
      this.filter.userNameKeyword = "";
      this.filter.workIdKeyword = "";
      this.filter.onlineStatus = "";
      this.filter.dept = [];
      this.getUserList();
    },
    State(value, label) {
      this.labels.label = label;
      this.filter.onlineStatus = value;
    },
  },
  mounted() {
    this.getUserList();
  },
});
