import { defineComponent } from "vue";
import { wexinEmo } from "@/utils/emo";
export default defineComponent({
  emits: ["sendEmo"],
  setup(props, context) {
    const selectEmo = (e, idx) => {
      /qqface([0-9]+)/g.test(e.target.className);
      context.emit(
        "sendEmo",
        Object.getOwnPropertyNames(wexinEmo)[Number(RegExp.$1)],
        idx
      );
    };
    return () => (
      <>
        <div class="flex flex-wrap">
          {new Array(109).fill("").map((item, idx) => {
            return (
              <div
                onMousedown={(e) => {
                  e.preventDefault();
                  selectEmo(e, idx);
                  // context.emit('sendEmo', idx)
                }}
                class={["qqface", "pointer", `qqface${idx}`]}
              ></div>
            );
          })}
        </div>
      </>
    );
  },
});
