
import { on, off } from "./hook";
import { defineComponent, PropType, ref } from "vue";
export default defineComponent({
  name: "Split",
  emits: ["input", "on-moving", "on-move-start", "on-move-end"],
  props: {
    mode: {
      validator(value: string) {
        return ["horizontal", "vertical"].includes(value);
      },
      type: String as PropType<string>,
      default: "horizontal",
    },
    value: {
      type: [Number, String] as PropType<string | number>,
      default: 0.5,
    },
    min: {
      type: [Number, String] as PropType<string | number>,
      default: "40px",
    },
    max: {
      type: [Number, String] as PropType<string | number>,
      default: "40px",
    },
  },
  setup() {
    const outerWrapper = ref<HTMLDivElement>();
    const isMoving = ref(false);
    const offset = ref(0);
    const computedMin = ref(0);
    const computedMax = ref(0);
    const currentValue = ref(0.5);
    const oldOffset = ref(0);
    const initOffset = ref();
    return {
      outerWrapper,
      isMoving,
      offset,
      computedMin,
      computedMax,
      currentValue,
      oldOffset,
      initOffset,
    };
  },
  computed: {
    isHorizontal(): boolean {
      return this.mode === "horizontal";
    },
    paneClasses(): any[] {
      return [
        `rsvp-pane`,
        {
          [`rsvp-pane-moving`]: this.isMoving,
        },
      ];
    },
    anotherOffset(): number {
      return 100 - this.offset;
    },
    offsetSize(): string {
      return this.isHorizontal ? "offsetWidth" : "offsetHeight";
    },
    valueIsPx(): boolean {
      return typeof this.value === "string";
    },
    triggerClasses(): string[] {
      return [
        "rsvp-trigger",
        this.isHorizontal ? `rsvp-trigger-vertical` : `rsvp-trigger-horizontal`,
      ];
    },
  },
  methods: {
    px2percent(numerator, denominator) {
      return parseFloat(numerator) / parseFloat(denominator);
    },
    getComputedThresholdValue(type: "min" | "max") {
      if (this.$refs.outerWrapper) {
        let size = (this.$refs.outerWrapper as HTMLDivElement)[this.offsetSize];
        if (this.valueIsPx) {
          return typeof this[type] === "string"
            ? this[type]
            : size * Number(this[type]);
        } else {
          return typeof this[type] === "string"
            ? this.px2percent(this[type], size)
            : this[type];
        }
      }
    },
    getAnotherOffset<T>(value: T): T {
      let res: any = 0;
      if (this.valueIsPx) {
        res = `${
          (this.$refs.outerWrapper as HTMLDivElement)[this.offsetSize] -
          parseFloat(value as any)
        }px`;
      } else if (typeof value === "number") {
        res = 1 - value;
      }
      return res;
    },
    computeOffset() {
      this.$nextTick(() => {
        this.computedMin = Number(this.getComputedThresholdValue("min"));
        this.computedMax = Number(this.getComputedThresholdValue("max"));
        let value: any = this.valueIsPx
          ? this.px2percent(
              this.value,
              (this.$refs.outerWrapper as HTMLDivElement)[this.offsetSize]
            )
          : this.value;
        let anotherValue = this.getAnotherOffset(value);
        if (parseFloat(value) <= parseFloat(this.computedMin as any))
          value = this.getMax(value, this.computedMin);
        if (parseFloat(anotherValue) <= parseFloat(this.computedMax as any))
          value = this.getAnotherOffset(
            this.getMax(anotherValue, this.computedMax)
          );
        this.offset = (value * 10000) / 100;
        this.currentValue = value;
        this.$emit("input", value);
      });
    },
    getMin(value1, value2) {
      if (this.valueIsPx)
        return `${Math.min(parseFloat(value1), parseFloat(value2))}px`;
      else return Math.min(value1, value2);
    },
    getMax(value1, value2) {
      if (this.valueIsPx)
        return `${Math.max(parseFloat(value1), parseFloat(value2))}px`;
      else return Math.max(value1, value2);
    },
    handleMousedown(e) {
      this.initOffset = this.isHorizontal ? e.pageX : e.pageY;
      this.oldOffset = this.value as any;
      this.isMoving = true;
      on(document, "mousemove", this.handleMove);
      on(document, "mouseup", this.handleUp);
      this.$emit("on-move-start");
    },
    handleMove(e) {
      let pageOffset = this.isHorizontal ? e.pageX : e.pageY;
      let offset = pageOffset - this.initOffset;
      let outerWidth = (this.$refs.outerWrapper as HTMLDivElement)[
        this.offsetSize
      ];
      let value = this.valueIsPx
        ? `${parseFloat(this.oldOffset as any) + offset}px`
        : this.px2percent(outerWidth * this.oldOffset + offset, outerWidth);
      let anotherValue = this.getAnotherOffset(value);
      if (parseFloat(value as any) <= parseFloat(this.computedMin as any))
        value = this.getMax(value, this.computedMin);
      if (
        parseFloat(anotherValue as any) <= parseFloat(this.computedMax as any)
      )
        value = this.getAnotherOffset(
          this.getMax(anotherValue, this.computedMax)
        );
      e.atMin = this.value === this.computedMin;
      e.atMax = this.valueIsPx
        ? this.getAnotherOffset(this.value) === this.computedMax
        : Number(this.getAnotherOffset(this.value)).toFixed(5) ===
          this.computedMax.toFixed(5);
      this.$emit("input", value);
      this.$emit("on-moving", e);
    },
    handleUp() {
      this.isMoving = false;
      off(document, "mousemove", this.handleMove);
      off(document, "mouseup", this.handleUp);
      this.$emit("on-move-end");
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.computeOffset();
    });
    // on(window, "resize", this.computeOffset);
  },
  beforeUnmount() {
    // off(window, "resize", this.computeOffset);
  },
  watch: {
    value(val) {
      console.log("val", val);
      if (val !== this.currentValue) {
        this.currentValue = val;
        this.computeOffset();
      }
    },
  },
});
