const socket: {
  instance: any;
  id: string;
  status: "success" | "connecting" | "fail" | "connect";
  reconnectCount: number;
  timeout: number;
  time: undefined | NodeJS.Timer;
  call: any;
} = reactive({
  instance: undefined,
  id: "",
  status: "connect",
  reconnectCount: 10,
  timeout: 3000,
  time: undefined,
  call: undefined,
});

const currentGroupId = ref("");

