// 获取历史记录消息
const getHistory = (params: { linkmans: string[] }, list: any[]) => {
  socket.instance.emit("getLinkmansLastMessagesV2", params, (obj) => {
    console.log("getLinkmansLastMessagesV2", obj);
    chatList.value = [];
    for (const key in obj) {
      const arr: any[] = [];
      // let name = "";
      // let avatar = "";
      obj[key].messages.forEach((element) => {
        element.speak = element.from.type;
        // if (element.speak === 0 && !name) {
        //   name = element.from.username;
        //   avatar = element.from.avatar;
        // }
        arr.push(element);
      });
      const groupId =
        list.find((item) => item.userId === key) &&
        list.find((item) => item.userId === key)._id;
      console.log("list", list);
      const name =
        list.find((item) => item.userId === key) &&
        list.find((item) => item.userId === key).name;
      console.log("name", name);

      chatList.value.push({
        groupId: groupId,
        username: name,
        // avatar: avatar,
        portrait: {
          accountName:
            (list.find((item) => item.userId === key) &&
              list.find((item) => item.userId === key).sap_name) ||
            "",
        },
        userId: key,
        list: [...arr],
      });
    }
    if (chatList.value.length > 0) {
      currentGroupId.value = chatList.value[0].groupId as string;
      socket.call && socket.call();
    }
    lintenShowGroup();
    console.log("chatList", chatList);
  });
};

