const close = () => {
  console.warn("im close");
  socket.instance && socket.instance.close();
  socket.id = "";
  socket.instance = null;
  if (socket.time) {
    clearInterval(socket.time as any);
    socket.time = undefined;
  }
};

// 获取团队客服
const getCustomList = (callback: (users) => void) => {
  socket.instance.emit("getStaffs", { group_id: "1" }, callback);
};

const getUserId = (paramsId?: number) => {
  let id = "";
  if (paramsId) {
    const obj = chatList.value.find((item) => item.groupId === paramsId);
    id = obj?.userId || "";
  } else if (currentGroupId.value) {
    const obj = chatList.value.find(
      (item) => item.groupId === currentGroupId.value
    );
    id = obj?.userId || "";
  }
  return id;
};

const deleteGroup = () => {
  socket.instance.on("deleteGroup", ({ groupId }) => {
    console.log("deleteGroup", currentGroupId.value);
    const index = chatList.value.findIndex((item) => item.groupId === groupId);
    if (index !== -1) {
      chatList.value.splice(index, 1);
      if (chatList.value.length > 0) {
        currentGroupId.value = chatList.value[0].groupId as string;
        socket?.call();
      } else {
        currentGroupId.value = "";
      }
    }
  });
};

