import { getCategoryTree, getProductTree } from "@/api/deli/faq";
import { reactive } from "vue";
const categoryType: { lists: any[]; loading: boolean } = reactive({
  lists: [],
  loading: false,
});
const categoryType1: { lists: any[]; loading: boolean } = reactive({
  lists: [],
  loading: false,
});

const gettreeList = async (id: number): Promise<void> => {
  if (id) {
    categoryType.loading = true;
    const res = await getCategoryTree({
      baseId: id,
    });
    categoryType.loading = false;
    const data: any = res.data;

    categoryType.lists =
      data.categoryList instanceof Array
        ? [
            {
              categoryTitle: "全部分类",
              categoryId: "",
              children: [...data.categoryList],
            },
          ]
        : [
            {
              categoryTitle: "全部分类",
              categoryId: "",
              children: [],
            },
          ];
  }
};

const getProductTreeList = async (id: number): Promise<void> => {
  if (id) {
    categoryType.loading = true;
    const res = await getProductTree({
      baseId: id,
    });
    categoryType.loading = false;
    const data: any = res.data;
    categoryType.lists =
      data.categoryList instanceof Array
        ? [
            {
              categoryTitle: "全部分类",
              categoryId: "",
              children: [...data.categoryList],
            },
          ]
        : [
            {
              categoryTitle: "全部分类",
              categoryId: "",
              children: [],
            },
          ];
  }
};

// 递归 查找categoryId数组
const findids = (
  id: "" | number,
  arr: any[] = [],
  data = categoryType.lists
): any => {
  const list = [...arr];
  if (id === "" || id === 0) {
    list.unshift("", "-1");
    return list;
  } else {
    let result: any;
    for (let i = 0; i < data.length; i++) {
      if (data[i].categoryId === id.toString()) {
        list.unshift("", id.toString());
        result = findids(data[i].parentCategoryId, list, categoryType.lists);
        return list;
      } else {
        if (data[i].children && data[i].children.length > 0) {
          result = findids(id, list, data[i].children);
        }
      }
    }
    // data.forEach((item) => {
    //   if (item.categoryId === id.toString()) {
    //     list.unshift(id);
    //     result = findids(item.parentCategoryId, list, categoryType.lists);
    //     return list;
    //   } else {
    //     if (item.children && item.children.length > 0) {
    //       result = findids(id, list, item.children);
    //       console.log(333);
    //     }
    //   }
    // });
    if (result) {
      return result;
    }
  }
};

export {
  gettreeList,
  categoryType,
  findids,
  getProductTreeList,
  categoryType1,
};

