import { ref, reactive } from "vue";
import {
  createUser as apiCreateUser,
  updateUserInfo,
} from "@/api/deli/userManagement";
import { ElMessage } from "element-plus";
import { checkAuth } from "@/utils/help";
const dialogVisible = ref(false);

type NumverType = number | "";

interface UserForm {
  [x: string]: any;
  userId?: number;
  deptId: [];
  workId: string;
  userEmail: string;
  userLoginName: string;
  userLoginPassword: string;
  userName: string;
  userNickName: string;
  userPhone: NumverType;
  userRoleId: NumverType;
}

const userForm: UserForm = reactive({
  deptId: [],
  userEmail: "",
  userLoginName: "",
  userLoginPassword: "",
  userName: "",
  userNickName: "",
  userPhone: "",
  userRoleId: "",
  workId: "",
  userRole: "",
});

const createUser: () => Promise<void> = async () => {
  if (userForm.userId) {
    await updateUserInfo(
      { userId: userForm.userId },
      {
        userDeptId:
          Number(userForm.deptId[userForm.deptId.length - 1]) === 0
            ? userForm.deptId[userForm.deptId.length - 2]
            : userForm.deptId[userForm.deptId.length - 1],
        userEmail: userForm.userEmail,
        userId: userForm.userId,
        userName: userForm.userName,
        userNickName: userForm.userNickName,
        userPhone: userForm.userPhone,
        userRoleId: userForm.userRoleId,
        workId: userForm.workId,
      }
    );
    ElMessage.success("修改成功！");
  } else {
    // 如果是坐席，部门默认有两级
    await apiCreateUser({
      ...userForm,
      deptId:
        Number(userForm.deptId[userForm.deptId.length - 1]) === 0
          ? userForm.deptId[userForm.deptId.length - 2]
          : userForm.deptId[userForm.deptId.length - 1],
      userRoleId: userForm.userRoleId as any,
    });
    ElMessage.success("创建成功！");
  }
  dialogVisible.value = false;
};

export { dialogVisible, userForm, createUser };
