
import { defineComponent, ref, onMounted } from "vue";
import { mapGetters } from "vuex";
import {
  getMineCalendarData,
  getUserCalendarData,
  getAllCalendarData,
} from "./hook";
import ScheduleCom from "./components/schedule.vue";
import CalendarCom from "./components/calendar.tsx";
import { checkAuth } from "@/utils/help";
import TitleCom from "@/components/title/index.vue";
export default defineComponent({
  components: {
    CalendarCom,
    ScheduleCom,
    TitleCom,
  },
  computed: {
    ...mapGetters({
      userInfo: "getUserInfo",
    }),
  },
  setup() {
    const activeCalendar = ref("mine");
    onMounted(() => {
      getMineCalendarData();
      if (checkAuth([2, 4])) {
        getUserCalendarData();
        getAllCalendarData();
      }
    });
    return {
      activeCalendar,
      checkAuth,
    };
  },
});
