import { getDeptTree } from "@/api/deli/setting";
import { ref } from "vue";
import { checkAuth } from "@/utils/help";

const options = ref<any[]>();
const treeLoading = ref(false);
const jtId = ref("");

const getTreeList = async () => {
  treeLoading.value = true;
  try {
    const res: any = await getDeptTree();
    res.data.deptList.forEach((element) => {
      if (element.deptType !== 1) {
        element.children.push({
          deptName: "省级客服组",
          deptId: "0",
          children: [],
        });
      }
    });
    jtId.value = res.data.deptList.find((item) => item.deptType === 1)?.deptId;
    const user = JSON.parse(localStorage.getItem("user") as string);
    if (checkAuth([1, 4])) {
      options.value = [
        {
          deptName: res.data.groupName,
          deptId: "0",
          children: res.data.deptList,
        },
      ];
    } else if (checkAuth([2, 3])) {
      const obj = res.data.deptList.find((item) => item.deptId === user.deptId);
      console.log("obj", res.data.deptList, user.deptId);
      options.value = [
        {
          deptName: obj.deptName,
          deptId: obj.deptId,
          children: obj.children,
        },
      ];
    }
  } catch (error) {
    console.log("error", error);
  }
  treeLoading.value = false;
};

const getDeptIdAndCompany = (deptIds: string[]) => {
  const user = JSON.parse(localStorage.getItem("user") as string);
  console.log("user", user);
  let companyId = "";
  let deptId = "";
  console.log("deptIds", deptIds);

  if (checkAuth([3])) {
    deptId = user.parentDeptId;
    companyId = user.deptId;
    if (!user.parentDeptId && user.parentDeptId !== 0) {
      deptId = deptIds[0];
      companyId = deptIds[1] || "";
      console.log("user", user);
    } else if (user.parentDeptId === 0) {
      deptId = user.deptId;
      companyId = "0";
    }
  } else if (checkAuth([2]) && deptIds.length === 2) {
    companyId = deptIds[1];
  } else if (deptIds.length === 3) {
    companyId = deptIds[2];
  }

  if (checkAuth([2])) {
    deptId = user.deptId;
  } else if (deptIds.length >= 2 && !checkAuth([3])) {
    deptId = deptIds[1];
  }
  console.log("deptId", deptId, "companyId", companyId, "deptIds", deptIds);

  return {
    companyId,
    deptId,
  };
};

export { getTreeList, options, treeLoading, getDeptIdAndCompany, jtId };
