
import { defineComponent, reactive, ref } from "vue";
import UserList from "./components/userList";
import ChatCom from "@/components/chat/chat";
import RecomardInfo from "./components/recommendInfo.vue";
import { initIM, sendMsg, chatList, currentGroupId, close } from "./imhook";
import { offlineTipVisible } from "./components/offlineHook";
import { getCommonList } from "@/api/deli/setting";
import { checkAuth } from "@/utils/help";
import StoreSearch from "./components/storeSearch.vue";
import { storeVisible } from "./components/storeHook";
import KnowledgeBuild from "@/components/knowledge/build.vue";
export default defineComponent({
  name: "online",
  components: {
    UserList,
    ChatCom,
    StoreSearch,
    KnowledgeBuild,
    RecomardInfo,
  },
  setup() {
    const tabsActive = ref("first");
    const showHot = ref(true);
    const showUser = ref(true);
    const name = ref("");
    const recomardDom = ref<InstanceType<typeof RecomardInfo>>();
    const commonInfo = reactive({
      loading: false,
      list: [],
    });
    const knowledgeDom = ref<InstanceType<typeof KnowledgeBuild>>();
    return {
      tabsActive,
      showHot,
      showUser,
      chatList,
      currentGroupId,
      name,
      commonInfo,
      storeVisible,
      close,
      knowledgeDom,
      recomardDom,
      offlineTipVisible,
    };
  },
  async created() {
    this.initData();
  },
  methods: {
    send(msg) {
      sendMsg({
        type: "text",
        content: msg,
      });
    },
    changeGroupId(id) {
      console.log("changeGroupId", id);
      this.currentGroupId = id;
      const index = chatList.value.findIndex((item) => item.groupId === id);
      chatList.value[index].list.forEach((item, idx) => {
        chatList.value[index].list[idx] = {
          ...item,
          noRead: false,
        };
      });
      this.getUserData();
    },
    getUserData() {
      this.$refs.recomardDom &&
        (
          this.$refs.recomardDom as InstanceType<typeof RecomardInfo>
        ).getAllData();
    },
    sendFile(file, type) {
      sendMsg({
        type: type,
        file: file,
        fileName: file.name,
      });
    },
    async getCommonWords() {
      this.commonInfo.loading = true;
      try {
        const res: any = await getCommonList();
        initIM(this.getUserData);
        this.commonInfo.list = res.data;
      } catch (error) {
        console.log("error32423432", error);
      }
      this.commonInfo.loading = false;
    },
    saveStore(val) {
      if (!val.answer || !val.question) {
        this.$message.error("问答对内容不能为空！");
        return;
      }
      const dom = this.$refs.knowledgeDom as InstanceType<
        typeof KnowledgeBuild
      >;
      dom.initValue();
      dom.dialogVisible = true;
      dom.questionList = [];
      dom.answerGroup = [];
      dom.questionList.push({
        content: val.question,
        faqQuestionId: "",
      });
      dom.answerGroup.push({
        plainText: val.answer,
        richAnswer: val.answer,
      });
    },
    async initData() {
      // if (checkAuth([2, 3, 4])) {
      await this.getCommonWords();
      // }
    },
  },
});
