
import TitleCom from "@/components/title/index.vue";
import { UserInfo } from "@/gtypes/User";
import { defineComponent, reactive, computed, ref, onMounted } from "vue";
import { useStore } from "vuex";
import {
  getUserInfo,
  uolpadAvatar,
  updateUserInfo,
  updatePassword,
} from "@/api/deli/userManagement";
import { Plus } from "@element-plus/icons";
import { passForm, passwordRules } from "./rules";
import { prefix } from "@/utils/request";
export default defineComponent({
  components: {
    TitleCom,
    Plus,
  },
  setup() {
    const updateVisible = ref(0);
    const store = useStore();
    const user = computed(() => store.getters.getUserInfo);
    const info = ref<UserInfo>();
    const form: UserInfo = reactive({
      userEmail: "",
      userId: user.value.userId,
      userName: "",
      userNickName: "",
      workId: "",
      userPhone: "",
    });
    const fromDom = ref<HTMLFormElement>();
    const avatarFile = ref();
    const imageUrl = ref();
    const loading = ref(false);
    const rules = reactive({
      userPhone: [
        {
          required: true,
          message: "格式不正确！",
          trigger: "blur",
          validator: (rule, value) =>
            /^1[3|4|5|7|8|9|6|2][0-9]\d{4,8}$/.test(value),
        },
      ],
    });
    onMounted(async () => {
      const res = await getUserInfo({
        userId: user.value.userId,
      });
      info.value = res.data as UserInfo;
      store.commit("setUser", info);
    });

    return {
      updateVisible,
      user,
      form,
      info,
      imageUrl,
      rules,
      passwordRules,
      passForm,
      fromDom,
      prefix,
      avatarFile,
      loading,
    };
  },
  methods: {
    updateInfo(type: number) {
      this.updateVisible = type;
      if (type === 1) {
        this.form = Object.assign(this.form, this.info);
        if (this.user.userAvatarUrl) {
          this.imageUrl = this.user.userAvatarUrl;
        }
      }
    },
    async fileChange(file) {
      if (file.size / 1024 / 1024 > 1) {
        this.$message.error("请上传1M以内的头像图片！");
        return;
      }
      this.imageUrl = window.URL.createObjectURL(file.raw);
      this.avatarFile = file.raw;
      // this.imageUrl = res.data;
      // this.user.userAvatarUrl = this.imageUrl;
      // this.$store.commit("setUser", this.user);
    },
    async submitForm() {
      (this.$refs.fromDom as HTMLFormElement).validate(async (valid) => {
        if (valid) {
          if (this.updateVisible === 1) {
            this.loading = true;
            if (this.avatarFile) {
              let obj = new FormData();
              obj.append("file", this.avatarFile);
              await uolpadAvatar(
                {
                  userId: this.user.userId,
                },
                obj
              );
            }
            const config = Object.assign({}, this.form);
            delete config.userAvatarUrl;
            await updateUserInfo({ userId: this.user.userId }, config as any);
            this.loading = false;
            this.$message.success("修改成功！");
            this.updateVisible = 0;
            const res = await getUserInfo({
              userId: this.user.userId,
            });
            this.info = res.data as UserInfo;
            this.$store.commit("setUser", {
              ...this.user,
              ...this.form,
              ...this.info,
            });
          } else {
            this.loading = true;
            await updatePassword({
              oldPassword: this.passForm.oldPassword,
              newPassword: this.passForm.newPassword,
            });
            this.loading = false;
            this.$message.success("修改成功！");
            this.updateVisible = 0;
          }
        } else {
          return false;
        }
      });
    },
  },
});
