
import { defineComponent, ref, reactive, toRefs } from "vue";
import PageHader from "@/components/pageHader";
import dayjs from "dayjs";
import { checkAuth } from "@/utils/help";
import * as echarts from "echarts";
import { ElMessage } from "element-plus";
import {
  sessionCount,
  satisfactionList,
  sessionSatisfactionCount,
  sessionSatisfactionList,
} from "@/api/deli/statistics";
import {
  getTreeList,
  options,
  treeLoading,
  getDeptIdAndCompany,
  jtId,
} from "./hook";
import { exportConversation } from "@/api/deli/conversation";
import TitleCom1 from "./components/title.tsx";
export default defineComponent({
  components: {
    PageHader,
    TitleCom1,
  },
  setup() {
    const state = reactive({
      shortcuts: [
        {
          text: "最近一周",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            return [start, end];
          },
        },
        {
          text: "最近一个月",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            return [start, end];
          },
        },
        {
          text: "最近三个月",
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            return [start, end];
          },
        },
      ],
      time: [dayjs().subtract(1, "month"), dayjs()],
    });
    const commentDom = ref<HTMLDivElement>();
    const totalDom = ref<HTMLDivElement>();
    const satisfactionsInfo = reactive({
      loading: false,
      list: [],
    });

    const countInfo = reactive({
      loading: false,
      list: [],
      count: 0,
    });
    const treePropos = reactive({
      label: "deptName",
      value: "deptId",
      checkStrictly: true,
    });
    const deptIds = ref(["0"]);
    const mycharts = ref();
    const mycharts1 = ref();
    const month = ref();
    const exportVisible = ref(false);
    const isDownLoading = ref(false);
    // 分页
    const page = reactive({
      pageNum: 1,
      pageSize: 10,
    });
    const uid = ref();
    const total = ref(0);
    const depttype = ref(0);
    // 饼图单独数据
    const mycharts1info = ref(0);
    const piechart = reactive({
      list: [],
    });
    return {
      uid,
      piechart,
      depttype,
      mycharts1info,
      total,
      page,
      commentDom,
      totalDom,
      countInfo,
      satisfactionsInfo,
      options,
      treePropos,
      deptIds,
      mycharts,
      mycharts1,
      month,
      exportVisible,
      isDownLoading,
      checkAuth,
      getTreeList,
      treeLoading,
      getDeptIdAndCompany,
      ...toRefs(state),
      jtId,
    };
  },
  computed: {
    showProvice() {
      let result = "";
      if (checkAuth([1, 4])) {
        if (this.deptIds.length === 1) {
          result = "all";
        } else if (
          this.deptIds.length === 2 &&
          parseInt(this.deptIds[1]) !== parseInt(this.jtId)
        ) {
          result = "dept";
        } else {
          result = "company";
        }
      } else if (checkAuth([2, 3])) {
        console.log("this.deptIds", this.deptIds, this.jtId);
        console.log("123456789", parseInt(this.deptIds[0]));
        if (
          this.deptIds.length === 1 &&
          parseInt(this.deptIds[0]) !== parseInt(this.jtId) &&
          this.deptIds[0] &&
          JSON.parse(localStorage.getItem("user") as string).parentDeptId !== 0
        ) {
          result = "dept";
        } else {
          result = "company";
        }
      } else {
        result = "company";
      }
      console.log("result", result);
      return result;
    },
  },
  methods: {
    async initData(page) {
      if (!this.time) {
        return;
      }
      let time = [
        dayjs(this.time[0]).format("YYYY-MM-DD") + " 00:00:00",
        dayjs(this.time[1]).format("YYYY-MM-DD") + " 23:59:59",
      ];
      this.page.pageNum = page;
      this.satisfactionsInfo.loading = true;
      const res: any = await satisfactionList(this.page, {
        ...this.getDeptIdAndCompany(this.deptIds),
        endTime: time[1],
        groupId: "1",
        startTime: time[0],
        uid: "",
      });
      console.log("res", res);
      this.total = res.data.total;
      if (res.code !== 0) {
        this.$message.error(res.msg);
        return;
      }
      this.satisfactionsInfo.list = res.data.records;
      this.piechart.list = res.data.records;
      console.log("res111", res);
      this.countInfo.loading = false;
      this.satisfactionsInfo.loading = false;

      // 饼图会话满意度列表
      const resuit2: any = await sessionSatisfactionList({
        ...this.getDeptIdAndCompany(this.deptIds),
        endTime: time[1],
        groupId: "1",
        startTime: time[0],
        uid: "",
      });
      console.log("resuit2", resuit2);
      if (resuit2.data.code !== 20000) {
        this.$message.error(resuit2.data.msg);
        return;
      }
      this.piechart.list = resuit2.data.result;

      const result: any = await sessionCount({
        ...this.getDeptIdAndCompany(this.deptIds),
        endTime: time[1],
        groupId: "1",
        startTime: time[0],
      });
      if (result.data.code !== 20000) {
        this.$message.error(result.data.msg);
        return;
      }
      console.log("result", result);

      this.countInfo.loading = false;
      this.countInfo.list = result.data.result.list;
      this.countInfo.count = result.data.result.count;

      // 饼图接口
      const resuit1: any = await sessionSatisfactionCount({
        ...this.getDeptIdAndCompany(this.deptIds),
        endTime: time[1],
        groupId: "1",
        startTime: time[0],
        uid: "",
      });
      console.log("resuit1", resuit1);
      this.mycharts1info = resuit1.data;

      this.drawEcharts();
    },
    drawEcharts() {
      let Alltotal = 0;
      this.countInfo.list.forEach((item: { count: number }) => {
        Alltotal += item.count;
      });
      this.$nextTick(() => {
        // 先折线图
        this.mycharts = echarts.init(this.$refs.totalDom as HTMLElement);
        this.mycharts.setOption({
          title: {
            text: `总会话次数：${Alltotal}`,
          },
          xAxis: {
            type: "category",
            data: this.countInfo.list.map((item: any) =>
              item.date.substring(5)
            ),
          },
          yAxis: {
            type: "value",
          },
          series: [
            {
              data: this.countInfo.list.map((item: any) => item.count),
              type: "line",
            },
          ],
        });

        // 饼图
        this.mycharts1 = echarts.init(this.$refs.commentDom as HTMLElement);
        const chatsData = [
          { value: 0, name: "非常不满意", num: 0 },
          { value: 0, name: "不满意", num: 0 },
          { value: 0, name: "一般", num: 0 },
          { value: 0, name: "满意", num: 0 },
          { value: 0, name: "非常满意", num: 0 },
        ];
        let total = 0;
        console.log("list");
        console.log("list", this.satisfactionsInfo.list);
        this.piechart.list.forEach((item: any) => {
          chatsData[0].num += item.s1;
          chatsData[1].num += item.s2;
          chatsData[2].num += item.s3;
          chatsData[3].num += item.s4;
          chatsData[4].num += item.s5;
          total = this.mycharts1info;
        });
        chatsData.forEach((item, idx) => {
          const data: number =
            chatsData[idx].num === 0
              ? 0
              : ((chatsData[idx].num / total) as any) * 100;
          chatsData[idx].value = Number(data.toFixed(1));
        });

        this.mycharts1.setOption({
          tooltip: {
            trigger: "item",
            formatter: "{b}:{c}%",
          },
          legend: {
            orient: "vertical",
            bottom: "10%",
            left: "30px",
            formatter: function (name) {
              const obj: any = chatsData.find((item) => item.name === name);
              return `${name}: ${obj.value}%`;
            },
          },
          series: [
            {
              name: "客服满意度",
              type: "pie",
              radius: ["40%", "70%"],
              avoidLabelOverlap: false,
              label: {
                show: true,
                position: "center",
                formatter: () => `总评价次数\n${total}`,
                fontSize: "20",
                fontWeight: "bold",
              },
              emphasis: {
                label: {
                  show: true,
                  fontSize: "20",
                  fontWeight: "bold",
                  // formatter: () => "总会话次数\n 18272",
                },
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)",
                },
              },
              data: chatsData,
            },
          ],
        });
      });
    },
    async exportData() {
      const user = JSON.parse(localStorage.getItem("user") as string);
      // console.log("this", this.month);
      const start = new Date(this.month);
      const end = new Date(this.month);
      const year = start.getFullYear();
      const month = start.getMonth();
      const monthDay = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
      let day = monthDay[month];
      if (month === 1 && year % 4 === 0 && year % 100 !== 0) {
        day = 29;
      }
      end.setTime(start.getTime() + 3600 * 1000 * 24 * day);
      const downMessage = ElMessage.info({
        message: "正在导出，请稍后",
        duration: 0,
      });
      this.isDownLoading = true;
      const res = await exportConversation({
        companyId: "",
        deptId: user.deptId,
        endTime: dayjs(end).format("YYYY-MM-DDTHH:mm:ss.SSS[Z]"),
        groupId: "1",
        startTime: dayjs(start).format("YYYY-MM-DDTHH:mm:ss.SSS[Z]"),
      });
      downMessage.close();
      this.isDownLoading = false;
      console.log("res", res);
    },
    async getTreeData() {
      await this.getTreeList();
      const id = this.options && this.options[0].deptId;
      this.deptIds = [id] as any[];
    },
  },
  mounted() {
    if (checkAuth([3])) {
      this.uid = JSON.parse(
        localStorage.getItem("user") as string
      ).userId.toString();
    }
    this.getTreeData();
    this.initData(1);
    this.depttype = JSON.parse(localStorage.getItem("user") as string).deptType;
  },
});
