import { createApp, Plugin } from "vue";

import App from "./App.vue";
import router from "./router";
import store from "./store";
import { Calendar } from "ant-design-vue";
import directive from "./directive/";

import ElementPlus, { ElMessage } from "element-plus";
import RegisterComponents from "./globalComponents";

import v3SimpleImgPreview from "v3-simple-img-preview";
// import "element-plus/dist/index.css";

// 前端监控
import webSee from "@websee/core";
import performance from "@websee/performance";
import recordscreen from "@websee/recordscreen";

// import { ElMessage } from "element-plus";
import "element-plus/theme-chalk/el-message.css";

import "./style/init.css";
import "./style/global.scss";

// import "ant-design-vue/lib/tabs/style/index.css";
// import "ant-design-vue/lib/button/style/index.css";
import "ant-design-vue/lib/calendar/style/index.css";
import "ant-design-vue/lib/date-picker/style/index.css";
// import { SFCWithInstall } from "element-plus/es/utils/types";
import "./style/index.scss";
import { Store } from "vuex";

import CheckType from "./utils/checkType";

// import "@/assets/iconfount/download/font_jupfor0uetc/iconfont.css";
declare module "@vue/runtime-core" {
  interface ComponentCustomProperties {
    $message: any; // SFCWithInstall<Message> jenkins打包会失败，提示没有SFCWithInstall，也是绝了，先去掉吧
    $store: Store<any>;
    $checkType: typeof CheckType;
  }
}

const app = createApp(App);
app.config.globalProperties.$message = ElMessage;
app.config.globalProperties.$checkType = CheckType;
RegisterComponents(app);

// 会提示 router中的 install(app: App): void; 与user中的PluginInstallFunction = (app: App, ...options: any[]) => any;不兼容，因此引入Plugin，并断言

app
  .use(router as Plugin)
  .use(store as Plugin)
  .use(directive)
  .use(webSee, {
    dsn: "https://api.yeshaojun.com/v1/monitor/report",
    apikey: "6481295c4b309188c4f332be",
    userId: localStorage.getItem("user")
      ? JSON.parse(localStorage.getItem("user") as string).userId + ""
      : "暂未登录",
    repeatCodeError: true,
    silentRecordScreen: true,
    beforeDataReport: (data) => {
      console.log("data", data);
      if (data.type === "performance" && data.status === "ok") {
        return false;
      }
      return data;
    },
  })
  .use(ElementPlus)
  .use(Calendar)
  .use(v3SimpleImgPreview)
  .mount("#app");

// @ts-ignore
webSee.use(performance);
// @ts-ignore
webSee.use(recordscreen);
