import { ref, reactive } from "vue";
import * as api from "@/api/deli/setting";
import { ElMessage } from "element-plus";
import { getDeptTree } from "../hook";
interface Dept {
  deptName: string;
  root?: boolean;
  parentDeptId?: number;
  children: Dept[];
  deptCode?: string;
  deptId?: number;
  opt?: string;
}

const dialogVisible = ref(false);
const current = reactive<Dept>({
  deptName: "",
  children: [],
});

const form = reactive({
  deptName: "",
  deptType: undefined,
  deptCode: "",
});

const companyForm = reactive({
  deptName: "",
  deptCode: ""
});

const createDept: () => void = async () => {
  if (current?.root) {
    await api.createDept(form);
    form.deptName = "";
    form.deptType = undefined;
    form.deptCode = ""
  } else if (current.parentDeptId === 0 && current.deptId) {
    await api.createChildDept({
      deptName: companyForm.deptName,
      parentDeptId: current?.deptId,
    });
    companyForm.deptName = "";
    companyForm.deptCode = ""
  }
  ElMessage.success("创建成功！");
  dialogVisible.value = false;
  await getDeptTree();
};

const editDept = async (): Promise<void> => {
  const config: any = {
    deptId: current.deptId as number,
    deptName: companyForm.deptName,
  }

  if (current.parentDeptId === 0) {
    config.deptCode = companyForm.deptCode
  }
  await api.updateDept(config);
  ElMessage.success("修改成功！");
  dialogVisible.value = false;
  await getDeptTree();
};
export {
  Dept,
  dialogVisible,
  current,
  form,
  companyForm,
  createDept,
  editDept,
};
