import { defineComponent, ref, watchEffect } from "vue";
import { createUseStyles } from "vue-jss";
import type { ElScrollbar } from "element-plus";
import Msg from "./msg";
import { chatList } from "../chatHook";
const useStyles = createUseStyles({
  rightWrapper: {
    float: "right",
    width: "100%",
    marginRight: "10px",
    position: "relative",
    marginBottom: "10px",
  },
  leftWrapper: {
    display: "flex",
    position: "relative",
    marginBottom: "30px",
  },
});
export default defineComponent({
  setup() {
    const styles = useStyles();
    const scrollbarRef = ref<InstanceType<typeof ElScrollbar>>();
    watchEffect(() => {
      if (chatList.list.length > 0) {
        setTimeout(() => {
          scrollbarRef.value!.setScrollTop(99999999);
        }, 200);
      }
    });

    return () => (
      <>
        <el-scrollbar ref={scrollbarRef} height="100%">
          <ul>
            {chatList.list.map((item) => {
              return (
                <li class="clearfix">
                  <div
                    class={
                      item.is_custom
                        ? styles.value.leftWrapper
                        : styles.value.rightWrapper
                    }
                  >
                    {item.is_custom ? (
                      <img
                        src={require("@/assets/deli.png")}
                        style={{ width: "42px", height: "42px" }}
                        alt=""
                      />
                    ) : null}
                    {item.is_custom && (
                      <p
                        class="p-l-10 m-b-5 font-12"
                        style={"position: absolute;left: -10px; top: -20px"}
                      >
                        {item.userName}
                      </p>
                    )}
                    {
                      <p
                        class="p-l-10 m-b-5 font-12"
                        style={
                          item.is_custom
                            ? "position: absolute;left: -10px; top: -40px"
                            : "position: absolute;right: 10px; top: -20px"
                        }
                      >
                        {item.sendTime}
                      </p>
                    }
                    <Msg message={item} />
                  </div>
                </li>
              );
            })}
          </ul>
        </el-scrollbar>
      </>
    );
  },
});
