import { defineComponent, ref, provide } from "vue";
import dayjs, { Dayjs } from "dayjs";
const localeData = require("dayjs/plugin/localeData");
dayjs.extend(localeData);
import "dayjs/locale/zh-cn";
import "./calendar.scss";
dayjs.locale("zh-cn");

import {
  mineInfo,
  currentDate,
  getAllCalendarData,
  getUserCalendarData,
  caledarList,
  timeJoin,
  changeTime,
  getMineCalendarData,
} from "../hook";
import { number } from "echarts";
export default defineComponent({
  props: {
    activeCalendar: {
      type: String,
      default: "mine",
    },
  },
  setup(props: { activeCalendar: string }) {
    const calendar = ref<Dayjs>();
    const zhCN = {
      lang: {
        locale: "zh_CN",
        month: "月",
        year: "年",
      },
    };

    const haveS = (time: Dayjs) => {
      let res = 0;
      let type = 0;
      if (props.activeCalendar === "mine") {
        mineInfo.list.forEach((item: any) => {
          const date = timeJoin(
            item.arrangeYear,
            item.arrangeMonth,
            item.arrangeDay
          );
          if (date.indexOf(dayjs(time).format("YYYY-MM-DD")) !== -1) {
            res = item.arrangeDetail;
            type = Number(item.arrangeType);
          }
        });
      } else {
        Object.keys(caledarList.list).forEach((key) => {
          const date = key;
          if (date.indexOf(dayjs(time).format("YYYY-MM-DD")) !== -1) {
            res = caledarList.list[key];
          }
        });
      }
      return {
        hs: res,
        type: type,
      };
    };

    const getYears = (value: Dayjs) => {
      const year = value.year();
      const years: number[] = [];
      for (let i = year - 10; i < year + 10; i += 1) {
        years.push(i);
      }
      return years;
    };

    const getMonths = (value: Dayjs) => {
      // @ts-ignore
      const localeData = dayjs(value).localeData();
      const months: number[] = [];
      for (let i = 0; i < 12; i++) {
        months.push(localeData.monthsShort(value.month(i)));
      }
      return months;
    };

    const dateCellRender = ({ current }: { current: Dayjs }) => {
      const { hs, type } = haveS(current);
      // console.log("hs", hs);
      if (hs) {
        if (props.activeCalendar === "mine") {
          return (
            <div class="p-t-10">
              <el-badge>
                <div
                  class={[
                    type === 3
                      ? "cal-diy-warn"
                      : type === 1
                      ? "cal-diy-success"
                      : "cal-diy-info",
                    "tag",
                  ]}
                  // type={isCurrent(current) > 0 ? "info" : "primary"}
                >
                  {type === 3 ? "请假" : null}
                  {hs}
                </div>
              </el-badge>
            </div>
          );
        } else {
          return (
            <div class="p-t-5">
              <el-space direction="vertical" size={3}>
                {(hs as any).map((item) => {
                  return (
                    <el-button
                      key={item.date}
                      size="small"
                      plain
                      type={
                        item.type === 3
                          ? "warning"
                          : item.type === 1
                          ? "primary"
                          : "success"
                      }
                    >
                      {item.type === 3
                        ? "请假"
                        : item.type === 1
                        ? "全班"
                        : "休息"}
                      {item.number}人
                    </el-button>
                  );
                })}
              </el-space>
            </div>
          );
        }
      }
    };

    const headerRender = ({ value }: { value: Dayjs }) => {
      console.log(value.format("YYYY年MM月"));
      return (
        <div class="clearfix">
          <span>{value.format("YYYY年MM月")}</span>
          {props.activeCalendar === "work" ? (
            <el-space class="f-r">
              <el-select
                modelValue={String(value.year())}
                style="width: 100px"
                onChange={(newYear: number) => {
                  calendar.value = value.year(newYear);
                  panelChange(value.year(newYear));
                }}
              >
                {getYears(value).map((val) => {
                  return (
                    <el-option
                      value={String(val)}
                      key={String(val)}
                      label={String(val) + "年"}
                    ></el-option>
                  );
                })}
              </el-select>
              <el-select
                modelValue={String(value.month())}
                style="width: 100px"
                onChange={(selectMonth: number) => {
                  calendar.value = value.month(selectMonth);
                  panelChange(value.month(selectMonth));
                }}
              >
                {getMonths(value).map((val, index) => {
                  return (
                    <el-option
                      key={index}
                      label={String(val)}
                      value={String(index)}
                    ></el-option>
                  );
                })}
              </el-select>
            </el-space>
          ) : null}
        </div>
      );
    };

    const panelChange = (e) => {
      caledarList.from = e.startOf("month").format("YYYY-MM-DD");
      caledarList.to = e.endOf("month").format("YYYY-MM-DD");
      getAllCalendarData();
      calendar.value = e;
      console.log("e", e);
      const changeM = e.format("M");
      changeTime(changeM, props.activeCalendar);
      console.log(111);
    };

    const calendarChange = (e) => {
      if (props.activeCalendar === "work") {
        currentDate.value = e.format("YYYY-MM-DD");
        getUserCalendarData();
        const changeM = e.format("M");
        changeTime(changeM, props.activeCalendar);
        panelChange(e);
      } else {
        const changeM = e.format("M");
        changeTime(changeM, props.activeCalendar);
      }
    };

    return () => (
      <>
        <a-calendar
          locale={zhCN}
          onChange={(e) => calendarChange(e)}
          value={calendar.value}
          // mode={mode.value}
          v-slots={{ headerRender, dateCellRender }}
        />
      </>
    );
  },
});
