import { defineComponent, PropType, toRefs, ref } from "vue";
import { ChatType } from "@/gtypes/Chat";
import { renderEmo } from "@/utils/help";
import { previewImage } from "v3-simple-img-preview";
import { link } from "fs";
export default defineComponent({
  props: {
    message: {
      type: Object as PropType<ChatType>,
      default() {
        return {};
      },
    },
  },
  setup(props: { message: ChatType }) {
    const { message } = toRefs(props);
    const renderText = () => {
      return (
        <span
          v-html={renderEmo(message.value.content).replaceAll("\n", "</br>")}
        ></span>
      );
    };

    const showModal = ref(false);
    const fileUrl = ref("");
    const downloadUrl = (url) => {
      showModal.value = true;
      fileUrl.value = url;
    };

    const renderLink = () => {
      let obj = {
        url: "",
        text: "",
      };
      obj = JSON.parse(message.value.content);
      if (
        obj.text === "请对本次服务进行评价" ||
        (obj.url && obj.url.indexOf("/#/evaluate") !== -1)
      ) {
        return <el-link type="primary">{obj.text}</el-link>;
      } else {
        return obj.text.indexOf(".pdf") === obj.text.length - 4 ? (
          <el-link
            type="primary"
            target="_blank"
            onClick={() => downloadUrl(obj.url)}
            download={obj.text}
          >
            {obj.text}
          </el-link>
        ) : (
          <el-link
            type="primary"
            target="_blank"
            href={obj.url}
            download={obj.text}
          >
            {obj.text}
          </el-link>
        );
      }
    };

    const previeImage = () => {
      previewImage({
        urls: [message.value.content],
        current: 1,
        maxZoom: 100000,
      });
    };

    const renderImage = () => {
      return (
        <img
          src={message.value.content}
          style={{
            width: "auto",
            maxWidth: "100%",
            height: "180px",
          }}
          alt=""
          class="pointer"
          onClick={() => previeImage()}
          srcset=""
        />
      );
    };

    const renderVideo = () => {
      return (
        <video
          src={message.value.content}
          style={{
            width: "auto",
            maxHeight: "180px",
            maxWidth: "100%",
            height: "100%",
          }}
          controls
        ></video>
      );
    };

    const renderAudio = () => {
      return (
        <audio
          src={message.value.content}
          controls
          style={{ width: "100%", height: "100px" }}
        ></audio>
      );
    };

    return () => (
      <>
        {message.value.contentType === "text" || message.value.type === "text"
          ? renderText()
          : null}
        {message.value.contentType === "link" || message.value.type === "link"
          ? renderLink()
          : null}
        {message.value.contentType === "image" || message.value.type === "image"
          ? renderImage()
          : null}
        {message.value.contentType === "video" || message.value.type === "video"
          ? renderVideo()
          : null}
        {message.value.contentType === "audio" || message.value.type === "audio"
          ? renderAudio()
          : null}
        {message.value.contentType === "system" ||
        message.value.type === "system"
          ? renderText()
          : null}
        <el-dialog v-model={showModal.value} width="90%" top="5vh">
          <iframe
            src={fileUrl.value}
            class="w-100"
            style={"height: calc(90vh - 120px)"}
            frameborder="0"
            scrolling="auto"
          ></iframe>
        </el-dialog>
      </>
    );
  },
});
