import { ref } from "vue";
import { getTreebyDept } from "@/api/deli/userManagement";
import { inputEmits } from "element-plus";

const deptList = ref();

async function getTreeDept(id: number): Promise<void> {
  const res = await getTreebyDept({
    deptId: 0,
  });
  const result = res.data as any;
  result.deptList.map((item) => {
    return {
      ...item,
      children:
        item.deptType === 2
          ? item.children.push({
              deptId: 0,
              parentDeptId: item.deptId,
              deptName: "省级客服部",
            })
          : item.children,
    };
  });
  deptList.value = result.deptList;
}

export { getTreeDept, deptList };
