// 部门树大概率是多个页面使用，故提出出来
import { getDeptTree as apiGetDeptTree, getRoles } from "@/api/deli/setting";
import { reactive } from "vue";
import { APIResult } from "@/gtypes/ApiResult";
const deptTree = reactive<APIResult>({
  list: [],
  loading: false,
  isRequest: false,
});

const roles = reactive<APIResult>({
  list: [],
  loading: false,
  isRequest: false,
});

// 获取部门树
const getDeptTree: () => void = async () => {
  deptTree.loading = true;
  const res = await apiGetDeptTree();
  deptTree.list =
    res.data instanceof Array
      ? res.data
      : [
          {
            root: true,
            deptName: (res.data as any).groupName,
            children: (res.data as any).deptList,
          },
        ];
  deptTree.loading = false;
  deptTree.isRequest = true;
};

// 获取角色列表
const getRoleList: () => void = async () => {
  roles.loading = true;
  const res = await getRoles();
  roles.loading = false;
  roles.isRequest = true;
  roles.list = res.data instanceof Array ? res.data : [];
};

export { deptTree, getDeptTree, getRoleList, roles };
