import { User } from "@/gtypes/User";
import { createStore } from "vuex";

interface StateType {
  userInfo: User;
}

// 创建一个新的 store 实例
const store = createStore({
  state(): StateType {
    return {
      userInfo: JSON.parse(localStorage.getItem("user") || "{}"),
    };
  },
  getters: {
    getUserInfo: (state: StateType) => state.userInfo,
  },
  mutations: {
    setUser(state: StateType, user) {
      state.userInfo = user;
    },
  }
});

// 如果项目比较大可以通过modules进行拆分
// 每个模块都有自己的状态
export default store;
