import process from "../views/process.vue";
import Online from "../views/assist/onlineAssist.vue";
import Clientupdate from "../views/clientupdate/clientUpdate.vue";
import ChatLive from "../views/deli/chat.vue";
import baseSet from "../views/set/base.vue";
import deptSet from "../views/set/dept.vue";
import commonSet from "../views/set/common.vue";
import message from "../views/set/message.vue";
import commonList from "../views/set/commonList.vue";
import person from "../views/member/person.vue";
import member from "../views/member/member.vue";
import session from "../views/session/index.vue";
import calendar from "../views/calendar/index.vue";
import store from "../views/store/index.vue";
