// 3.创建路由实例
const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  if (SpecialRouter.indexOf(to.path) !== -1) {
    next();
    return;
  } else {
    // 如果是管理员，则跳过弹屏页面
    if (to.path === "/online" && checkAuth([1])) {
      router.replace("session");
    }

    if (to.path !== "/" && to.matched.length === 0) {
      console.log("to", router);
      router.replace("404");
    } else if (to.meta.auth) {
      if (checkAuth(to.meta.auth as any)) {
        next();
      } else {
        router.replace("403");
      }
    } else {
      next();
    }
  }
});

// 路由懒加载，在服务器端偶发性的报错Loading chunk
//  一种可能是。户点击过的A模块被浏览器缓存了，当再重新打包上线后，用户在A模块依然是读取的缓存可以正常浏览；如果从A模块中点击链接到B模块中，由于每次打包的文件hash值不同，导致从服务器中找不到该模块

router.onError((error, to, from) => {
  console.log("router.onError", error, to, from);
  const pattern = /Loading chunk (\d)+ failed/g;
  const isChunkLoadFailed =
    error.match(pattern) || error.message.match(pattern);
  console.log(
    isChunkLoadFailed,
    "/Loading chunk (d)+ failed/g",
    "路由懒加载找不到对应的moudle"
  );
  if (isChunkLoadFailed) {
    window.location.reload();
  } else {
    console.log(error);
  }
});

// 挂载，这一步放到main.js中
export default router;
