
import { defineComponent, ref, reactive } from "vue";
import TitleCom from "@/components/title/index.vue";
import { CirclePlusFilled } from "@element-plus/icons";
import {
  gettreeList,
  categoryType,
  findids,
  getProductTreeList,
} from "./faqHook";
import { createCategory, updateCategory, deleteCategory } from "@/api/deli/faq";
import { checkAuth } from "@/utils/help";
export default defineComponent({
  emits: ["clear", "handleNodeClick"],
  components: {
    TitleCom,
    CirclePlusFilled,
  },
  setup() {
    const categoryVisible = ref(false);
    // 全部分类 创建修改的id用空来表示，返回数据用0表示【前端做一下兼容】
    const form: {
      categoryId?: number;
      parentId: (string | number)[];
      title: string;
    } = reactive({
      parentId: [""],
      title: "",
    });
    const props = reactive({
      value: "categoryId",
      label: "categoryTitle",
      checkStrictly: true,
    });
    const defaultProps = reactive({
      class: (data) => {
        if (
          data.categoryId === currentId.value
          // (data.deptName === "得力集团" && filter.deptId === 0)
        ) {
          return "is-penultimate";
        }
        return null;
      },
    });
    const currentId = ref("");
    const currentHoverId = ref("");

    const lists = ref<any[]>([]);
    return {
      categoryVisible,
      form,
      categoryType,
      props,
      checkAuth,
      currentId,
      defaultProps,
      currentHoverId,
      lists,
    };
  },
  computed: {
    filterList() {
      let arr = JSON.parse(JSON.stringify(this.categoryType.lists));
      arr[0].children = arr[0].children.filter(
        (item) => item.categoryId !== "-1"
      );
      return arr;
    },
  },
  methods: {
    async addFaqType() {
      // 可选参数 在严格模式下,会有undefined类型
      if (this.form.categoryId) {
        await updateCategory(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            ...this.form,
            parentId:
              this.form.parentId[this.form.parentId.length - 1] === ""
                ? 0
                : this.form.parentId[this.form.parentId.length - 1],
            categoryId: this.form.categoryId,
          }
        );
        this.$message.success("修改成功！");
      } else {
        await createCategory(
          {
            baseId: Number(this.$route.query.id),
          },
          {
            ...this.form,
            parentId: this.form.parentId[this.form.parentId.length - 1],
          }
        );
        this.$message.success("添加成功！");
      }
      this.categoryVisible = false;
      this.form.title = "";
      this.form.parentId = [""];
      gettreeList(Number(this.$route.query.id));
    },
    editFaq(item) {
      this.categoryVisible = true;
      this.form.title = item.categoryTitle;
      this.form.categoryId = item.categoryId;
      this.form.parentId = findids(item.parentCategoryId);
    },
    async delCategory(item) {
      await deleteCategory(
        { baseId: Number(this.$route.query.id) },
        {
          categoryId: item.categoryId,
        }
      );
      this.$message.success("删除成功！");
      this.$emit("clear");
      gettreeList(Number(this.$route.query.id));
    },
    handleNodeClick(item) {
      this.currentId = item.categoryId;
      this.$emit("handleNodeClick", item, findids(item.categoryId));
    },
  },
  mounted() {
    if (this.$route.query.type === "1") {
      getProductTreeList(Number(this.$route.query.id));
    } else {
      gettreeList(Number(this.$route.query.id));
    }
  },
});
