import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";
const BASE_URL = "/faq-management/";

interface BaseID {
  baseId: number;
}

interface BaseInfo {
  title: string;
  desc: string;
}

interface CategoryInfo {
  title: string;
  parentId: number | string;
  categoryId: number;
}

type CategoryId = {
  categoryId: number | string;
};

type FAQId = {
  faqId: number;
};

type AnswerId = {
  answerId: number;
};

type QuestionId = {
  questionId: number;
};
type Desc = {
  desc: string;
};

interface AnswerInfo extends FAQId {
  richText: string;
  plainText: string;
}

interface PageInfo {
  pageNum: number;
  pageSize: number;
}

interface FAQ extends CategoryId {
  minScore: number;
  question: string;
}

interface FAQQuestion {
  faqId: number;
  question: string;
}

enum FAQStatus {
  start = 1,
  ban = 0,
}

// 创建知识库
export function createBase(params: BaseInfo): Promise<Service.Result> {
  return Request({
    url: BASE_URL + "base",
    method: "post",
    params,
  });
}

// 删除知识库
export function deleteBase(params: BaseID): Promise<Service.Result> {
  return Request({
    url: BASE_URL + `base/${params.baseId}`,
    method: "delete",
  });
}

// 编辑知识库
export function updateBase(
  params: BaseID,
  data: BaseInfo
): Promise<Service.Result> {
  return Request({
    url: BASE_URL + `base/${params.baseId}`,
    method: "put",
    params,
  });
}

// 查看知识库列表
export function getBases(): Promise<Service.Result> {
  return Request({
    url: BASE_URL + "bases",
  });
}

// 查询知识树分类树
export function getCategoryTree(params: BaseID): Promise<Service.Result> {
  return Request({
    url: BASE_URL + `category/tree/${params.baseId}`,
  });
}

