// 批量导入FAQ知识
export function importFaq(
  path: { baseId: number },
  data: FormData
): Promise<Service.Result> {
  return Request({
    url: BASE_URL + `faq/${path.baseId}/import/batch`,
    data,
    method: "post",
  });
}

// 批量导出FAQ知识
export function exportFaq(
  path: { baseId: number },
  params: { start: number; end: number }
) {
  return Request({
    url: BASE_URL + `faq/${path.baseId}/export/batch`,
    params,
    responseType: "blob",
  });
}

// 获取当前用户可以提交审批的知识库id
