
import { defineComponent, ref, reactive } from "vue";
import TitleCom from "@/components/title/index.vue";
import TitleCom1 from "./components/title.tsx";
import {
  satisfactionList,
  getSessionAgingByuid,
  sessionCount,
} from "@/api/deli/statistics";
import dayjs from "dayjs";
import * as echarts from "echarts";
import { getTreeList, getDeptIdAndCompany, options } from "./hook";
export default defineComponent({
  components: {
    TitleCom,
    TitleCom1,
  },
  setup() {
    const tableData = ref([]);
    const time = ref([dayjs().subtract(1, "month"), dayjs()]);
    const timeInfo = ref<any[]>([]);
    const agingInfo = ref();
    const agingDom = ref<HTMLDivElement>();

    // 分页
    const page = reactive({
      pageNum: 1,
      pageSize: 10,
    });
    const deptIds = ref(["0"]);
    const total = ref(0);
    return {
      deptIds,
      page,
      tableData,
      time,
      timeInfo,
      agingInfo,
      agingDom,
      getDeptIdAndCompany,
      getTreeList,
      options,
      total,
    };
  },
  methods: {
    async getInitData() {
      if (this.time) {
        let time = [
          dayjs(this.time[0]).format("YYYY-MM-DD") + " 00:00:00",
          dayjs(this.time[1]).format("YYYY-MM-DD") + " 23:59:59",
        ];
        const user = JSON.parse(localStorage.getItem("user") as string);
        this.getInitAging();
        const res: any = await getSessionAgingByuid({
          ...this.getDeptIdAndCompany(this.deptIds),
          startTime: time[0],
          endTime: time[1],
          uid: user.userId,
        });
        const result = res.data.result;
        console.log("deptIds", this.deptIds);
        result.averageResp = res.data.result.averageResp.toFixed(1);
        result.averageSession = res.data.result.averageSession.toFixed(1);
        result.totalSession = res.data.result.totalSession.toFixed(1);
        this.total = res.data.result.sessionCount;
        this.timeInfo = [result];
      }
    },
    async getInitAging() {
      let time = [
        dayjs(this.time[0]).format("YYYY-MM-DD") + " 00:00:00",
        dayjs(this.time[1]).format("YYYY-MM-DD") + " 23:59:59",
      ];
      const user = JSON.parse(localStorage.getItem("user") as string);
      const res: any = await satisfactionList(this.page, {
        ...this.getDeptIdAndCompany(this.deptIds),
        uid: user.userId,
        endTime: time[1],
        startTime: time[0],
        groupId: "1",
      });
      const result = res.data.records[0];
      this.agingInfo = result;
      this.$nextTick(() => {
        const totalcharts = echarts.init(this.$refs.agingDom as HTMLElement);
        const chatsData = [
          { value: (result.s1Rate * 100).toFixed(1), name: "非常不满意" },
          { value: (result.s2Rate * 100).toFixed(1), name: "不满意" },
          { value: (result.s3Rate * 100).toFixed(1), name: "一般" },
          { value: (result.s4Rate * 100).toFixed(1), name: "满意" },
          { value: (result.s5Rate * 100).toFixed(1), name: "非常满意" },
        ];
        totalcharts.setOption({
          tooltip: {
            trigger: "item",
            formatter: "{b}:{c}%",
          },
          legend: {
            // orient: "vertical",
            bottom: "0%",
            // left: "30px",
            formatter: function (name) {
              const obj: any = chatsData.find((item) => item.name === name);
              console.log("name", name, obj);
              return `${name}: ${obj.value}%`;
            },
          },
          series: [
            {
              name: "客服满意度",
              type: "pie",
              radius: ["40%", "70%"],
              avoidLabelOverlap: false,
              label: {
                show: true,
                position: "center",
                formatter: () => `总评价次数\n ${result.totalNum}`,
                fontSize: "20",
                fontWeight: "bold",
              },
              emphasis: {
                label: {
                  show: true,
                  fontSize: "20",
                  fontWeight: "bold",
                },
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)",
                },
              },
              data: chatsData,
            },
          ],
        });
      });
    },
  },
  mounted() {
    this.getInitData();
    this.getInitAging();
    this.getTreeList();
    const id = this.options && this.options[0].deptId;
    this.deptIds = [id] as any[];
  },
});
