import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";

interface LoginPrams {
  loginName: string;
  password: string;
  oldToken?: string;
}

export const login: (data: LoginPrams) => Promise<Service.Result> = (data) => {
  return Request({
    url: "/sys/login",
    data,
    method: "post",
  });
};

export const logout: () => Promise<Service.Result> = () => {
  return Request({
    url: "/sys/logout",
    method: "post",
  });
};
