import dayjs from "dayjs";
type IMStatus = "success" | "error" | "connecting" | "reconnecting" | "close";
type typeEnum =
  | "text"
  | "image"
  | "file"
  | "video"
  | "audio"
  | "link"
  | "heart_beat";
import { ElMessage } from "element-plus";
import { ChatTypeList } from "@/config/index";
class IM {
  private status: IMStatus = "connecting";
  private im!: WebSocket;
  private count = 5;
  private timer!: NodeJS.Timer;
  private headerTimer!: NodeJS.Timer;
  private conversationId!: string;
  private userId!: number;
  static IMInstance: IM;
  static defaultMessage: any[];
  private constructor(
    private url,
    private successCallback,
    private errorCallback: (params: IMStatus) => void
  ) {
    this.init();
  }
  public init() {
    if (window.WebSocket) {
      console.log("init");
      this.close();
      this.im = new window.WebSocket(this.url);
    } else {
      ElMessage.error("当前浏览器不支持websocket连接，请重试！");
    }
    this.im.onopen = async (evt) => {
      console.log("websocket连接成功！");
      this.status = "success";

      this.heartbeat(10000);
      this.successCallback();
    };
    this.im.onclose = (e) => {
      console.log("onclose msg", e);
      console.log("websocket连接关闭");
      this.status = "close";
      this.errorCallback(this.status);
      if (e.code === 1011) {
        // 不进行重连
        this.close();
        ElMessage.error(e.reason);
      } else if (e.reason === "无法开始会话") {
        this.close();
        ElMessage.error(e.reason);
      } else {
        if (this.count > 0) {
          this.count--;
          this.reconnect();
        } else {
          this.close();
        }
      }
      // 根据conversationId判断是否重练
    };

    this.im.onerror = (e) => {
      // 表示第一次就未链接成功, 则连接5次之后不再连接
      console.log("websocket异常", e, this.status);
      if (this.status === "connecting") {
        if (this.count > 0) {
          this.count--;
          this.init();
        } else {
          ElMessage.error("连接失败！");
        }
      } else {
        this.reconnect();
      }
    };
    this.im.onmessage = (evt) => {
      console.log("evt", evt);
      const result = JSON.parse(evt.data);
      if (result.data) {
        this.conversationId = result.data.conversationId;
        this.userId = result.data.userId;
        IM.defaultMessage = [...result.data.chatResponseList];
      } else {
        this.successCallback(result);
      }
    };
  }

  public close() {
    try {
      clearInterval(this.timer);
      clearInterval(this.headerTimer);
      this.headerTimer = undefined as any;
      this.timer = undefined as any;
      this.im && this.im.close(1000, this.conversationId);
    } catch (error) {
      console.log("close error", error);
    }
  }

  private heartbeat(time) {
    this.headerTimer = setInterval(() => {
      this.send({ type: "heart_beat", content: "" });
    }, time);
  }

  public reconnect() {
    console.log("websocket重连中");
    if (this.headerTimer) {
      clearInterval(this.headerTimer);
    }
    this.status = "reconnecting";
    this.errorCallback(this.status);

    if (!this.timer) {
      this.timer = setInterval(() => {
        this.init();
      }, 2000);
    }
  }

  public send(msg: { type: typeEnum; content: string }) {
    // console.log(
    //   "msg455",
    //   JSON.stringify({
    //     type: ChatTypeList[msg.type],
    //     content: msg.content,
    //     conversationId: this.conversationId,
    //     userId: this.userId,
    //     sendTime: dayjs().format("YYYY-MM-DD HH:mm:ss"),
    //   })
    // );
    if (msg.type && this.conversationId) {
      // console.log("msg", msg);
      this.im.send(
        JSON.stringify({
          type: ChatTypeList[msg.type],
          content: msg.content,
          conversationId: this.conversationId,
          userId: this.userId,
          sendTime: dayjs().format("YYYY-MM-DD HH:mm:ss"),
        })
      );
    }
  }

  static getInstance(url, cal1, cal2: (params: IMStatus) => void) {
    if (!IM.IMInstance) {
      IM.IMInstance = new IM(url, cal1, cal2);
    }
    return IM.IMInstance;
  }

  get getStatus() {
    return this.status;
  }
}

export default IM;
