
import { defineComponent, ref, onMounted, reactive } from "vue";
import { Plus, Right } from "@element-plus/icons";
import ChatList from "./components/chatList";
// import { visible } from "./components/hook";
import DrawerCom from "./components/drawer";
import { ElNotification } from "element-plus";
import { sendMessage, visible, chatList, getHistoryList } from "./chatHook";
// import RichTextareaDom from "@/components/rsvp-richTextarea/index.vue";
import dayjs from "dayjs";
import { useRoute } from "vue-router";
import IM from "./websocket";
import { UserIMUrl } from "@/config/index";
export default defineComponent({
  components: {
    Plus,
    Right,
    ChatList,
    DrawerCom,
  },
  setup() {
    const msg = ref();
    const im = ref<IM>();
    const ontice = ref();
    const route = useRoute();
    const successCall = async (message: any) => {
      ontice.value && ontice.value.close();
      if (message) {
        chatList.list.push({
          type: message.messageType,
          is_custom: true,
          userName: message.serverName,
          content: message.messageContent,
          sendTime: dayjs(message.sendTime).format("YYYY-MM-DD HH:mm:ss"),
        });
      } else {
        getHistoryList({
          customerName: route.query.uid as string,
          groupId: route.query.gid as string,
          limit: 100,
        });
      }
    };
    const errorCall = (msg) => {
      // console.log("error msg", msg);
      ontice.value && ontice.value.close();
      if (msg === "close") {
        ontice.value = ElNotification({
          type: "error",
          duration: 0,
          message: "会话已结束！",
        });
      } else if (msg === "reconnecting") {
        ontice.value = ElNotification({
          type: "info",
          duration: 0,
          message: "会话正在重连！",
        });
      }
    };
    onMounted(() => {
      if (route.query.gid && route.query.uid) {
        // 192.168.1.117:8635
        // 183.131.249.58:18635
        im.value = IM.getInstance(
          `${UserIMUrl}/im/ws/${route.query.gid}/${route.query.uid}/${
            route.query.channel || "website"
          }`,
          successCall,
          errorCall
        );
      }
    });
    return {
      msg,
      visible,
      im,
    };
  },
  methods: {
    sendMsg() {
      if (this.im?.getStatus === "close") {
        this.im.init();
      } else if (
        this.im?.getStatus === "reconnecting" ||
        this.im?.getStatus === "error"
      ) {
        this.im?.getStatus === "reconnecting"
          ? this.$message("会话正在重连，请稍后再试！")
          : this.$message("会话连接失败，请刷新页面重新连接！");
        return;
      } else {
        if (this.msg && this.msg.replaceAll(" ", "")) {
          sendMessage({
            type: "text",
            content: this.msg,
            is_custom: false,
          });
        }
        this.msg = "";
      }
    },
    keydown(e) {
      if (e.keyCode === 13 && !e.shiftKey) {
        e.preventDefault();
        this.sendMsg();
      }
    },
  },
  unmounted() {
    this.im?.close();
  },
});
