import { defineComponent, reactive, onMounted, ref, toRefs } from "vue";
import { checkAuth, resetReactiveData } from "@/utils/help";
import { filter, treeInfo } from "./hook";
export default defineComponent({
  props: ["channelInfo"],
  setup(
    props: {
      channelInfo: {
        loading: boolean;
        list: any[];
      };
    },
    context
  ) {
    const { channelInfo } = toRefs(props);
    // console.log("channelInfo", channelInfo.value);
    const search = () => {
      context.emit("search", filter);
    };

    const resetFilter = () => {
      const deptId = (treeInfo.list[0] as any)?.deptId;
      resetReactiveData(filter, {
        dept: deptId ? [deptId] : [],
      });
      context.emit("search", filter);
    };

    return () => (
      <div>
        <el-space>
          {(checkAuth([1, 2, 4]) ||
            JSON.parse(localStorage.getItem("user") as string).deptType ===
              2) && (
            <rsvp-cascader
              options={treeInfo.list}
              v-model={filter.dept}
              placeholder="请选择部门"
              props={{
                checkStrictly: true,
                label: "deptName",
                value: "deptId",
              }}
            ></rsvp-cascader>
          )}
          <el-select v-model={filter.channel} placeholder="请选择渠道类型">
            {channelInfo.value.list.map((item) => (
              <el-option
                value={item.channelName}
                label={item.displayName}
              ></el-option>
            ))}
          </el-select>
          <el-select placeholder="客服处理状态" v-model={filter.status}>
            <el-option label="处理中" value={0}></el-option>
            <el-option label="已结束" value={1}></el-option>
          </el-select>
          <el-select v-model={filter.score} placeholder="请选择是否满意">
            <el-option value={5} label="非常满意"></el-option>
            <el-option value={4} label="满意"></el-option>
            <el-option value={3} label="一般"></el-option>
            <el-option value={2} label="不满意"></el-option>
            <el-option value={1} label="非常不满意"></el-option>
          </el-select>
          <el-input
            placeholder="客户名称或编码"
            class="width-170"
            v-model={filter.customerCompanyName}
          ></el-input>
          <el-space>
            <el-button onClick={() => resetFilter()}>重置</el-button>
            <el-button type="primary" onClick={search}>
              搜索
            </el-button>
          </el-space>
        </el-space>
      </div>
    );
  },
});
