import { App } from "@vue/runtime-core";
import moment from "moment";
import { DirectiveBinding } from "vue";
// import { ElLoading, ILoadingInstance } from "element-plus";
// let loadingInstance: ILoadingInstance;
export default function (app: App) {
  app.directive("debounce", {
    mounted: function (el: HTMLElement, binding: DirectiveBinding) {
      let timer: any;
      el.addEventListener("click", () => {
        if (timer) {
          clearTimeout(timer);
        }
        timer = setTimeout(() => {
          binding.value();
        }, 500);
      });
    },
  });

  app.directive("time", {
    mounted: function (el: HTMLElement, binding: DirectiveBinding) {
      el.textContent = moment(binding.value).format(
        binding.arg || "YYYY-MM-DD hh:mm:ss"
      );
    },
  });

  app.directive("focus", {
    mounted(el) {
      console.log("el", el);
      if (el.children) {
        for (let i = 0; i < el.children.length; i++) {
          if (el.children[i].tagName === "INPUT") {
            el.children[i].focus();
          }
        }
      } else {
        el.children[0].focus();
      }
    },
  });

  // // 临时解决方案
  // app.directive("rsvp-loading", {
  //   updated(el: HTMLElement, binding: DirectiveBinding) {
  //     if (binding.value && !loadingInstance) {
  //       loadingInstance = ElLoading.service({
  //         target: el,
  //         lock: true,
  //         text: "数据加载中",
  //       });
  //     }
  //     if (!binding.value) {
  //       loadingInstance && loadingInstance.close();
  //     }
  //   },
  // });
}
