import { defineComponent, h } from "vue";
import { createUseStyles } from "vue-jss";
import { renderEmo } from "@/utils/help";
import { previewImage } from "v3-simple-img-preview";
const useStyles = createUseStyles({
  wrapper: {
    marginBottom: "15px",
    padding: "15px",
    boxSizing: "border-box",
    maxWidth: "70%",
    // "& .el-link": {
    //   color: "#f0f0f0",
    // },
  },
  rightW: {
    borderRadius: "10px 10px 0 10px",
    backgroundColor: "#6d7c9c",
    color: "#fff",
    marginRight: "10px",
    float: "right",
    wordBreak: "break-all",
  },
  leftW: {
    borderRadius: "10px 10px 10px 0",
    backgroundColor: "#fff",
    marginLeft: "10px",
    wordBreak: "break-all",
  },
  imageWrapper: {
    marginBottom: "10px",
    maxWidth: "70%",
    // backgroundColor: "#3367D6",
    // padding: "12px 16px",
    cursor: "pointer",
  },
  imageLeft: {
    borderRadius: "13px 13px 13px 0",
  },
  imageRight: {
    borderRadius: "12px 13px 0 13px",
    float: "right",
    marginRight: "10px",
  },
});
export default defineComponent({
  props: {
    message: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const styles = useStyles();
    const renderText = (item) => {
      return (
        <div
          class={[
            item.is_custom ? styles.value.leftW : styles.value.rightW,
            styles.value.wrapper,
          ]}
          v-html={renderEmo(item.content).replaceAll("\n", "</br>")}
        ></div>
      );
    };

    const previewImg = (url) => {
      previewImage({
        urls: [url],
        current: 1,
      });
    };
    const renderImage = (item) => {
      return (
        <div
          class={[
            item.is_custom ? styles.value.imageLeft : styles.value.imageRight,
            styles.value.imageWrapper,
            "m-b-20",
          ]}
          onClick={() => previewImg(item.content)}
        >
          <img
            src={item.content}
            class="m-b-20"
            style={{ maxHeight: "180px", maxWidth: "100%" }}
            alt=""
          />
        </div>
      );
    };

    const renderFile = (item) => {
      return (
        <div
          class={[
            item.is_custom ? styles.value.leftW : styles.value.rightW,
            styles.value.wrapper,
          ]}
        >
          <el-link href={item.content} style={{ coloe: "#fff" }}>
            {item.fileName}
          </el-link>
        </div>
      );
    };

    const renderLink = (item) => {
      let obj = {
        url: "",
        text: "",
      };
      obj = JSON.parse(item.content);
      return (
        <div
          class={[
            item.is_custom ? styles.value.leftW : styles.value.rightW,
            styles.value.wrapper,
          ]}
        >
          {obj.text === "请对本次服务进行评价" ? (
            <el-link
              onClick={() => window.open(obj.url, "_blank")}
              type="warning"
              style={{ coloe: "#fff" }}
              name={obj?.text}
            >
              {obj?.text}
            </el-link>
          ) : (
            <el-link
              href={obj?.url}
              type="warning"
              target="_blank"
              style={{ coloe: "#fff" }}
              download={obj?.text}
            >
              {obj?.text}
            </el-link>
          )}
        </div>
      );
    };

    const renderVideo = (item) => {
      return (
        <div
          class={[
            item.is_custom ? styles.value.leftW : styles.value.rightW,
            styles.value.wrapper,
          ]}
          style={"height: 180px"}
        >
          <video
            src={item.content}
            style={{ height: "100%", width: "100%" }}
            controls
          ></video>
        </div>
      );
    };

    const renderAudio = (item) => {
      return (
        <div
          class={[
            item.is_custom ? styles.value.leftW : styles.value.rightW,
            styles.value.wrapper,
          ]}
          style={"height: 120px"}
        >
          <audio
            src={item.content}
            style={{ height: "100%", maxWidth: "100%" }}
            controls
          ></audio>
        </div>
      );
    };
    return () => (
      <>
        {props.message.type === "text" && renderText(props.message)}
        {props.message.type === "image" && renderImage(props.message)}
        {props.message.type === "file" && renderFile(props.message)}
        {props.message.type === "link" && renderLink(props.message)}
        {props.message.type === "video" && renderVideo(props.message)}
        {props.message.type === "audio" && renderAudio(props.message)}
      </>
    );
  },
});
