import { ref, reactive } from "vue";
import { getProductTree, getProductpage } from "@/api/deli/faq";
import { ElMessage } from "element-plus";

const storeVisible = ref(false);
const categoryInfo = reactive({
  loading: false,
  list: [],
});

const filter = reactive({
  question: "",
  pageNum: 1,
  pageSize: 10,
  categoryId: [],
});

const initData = async () => {
  categoryInfo.loading = true;
  const res: any = await getProductTree({
    baseId: 1,
  });

  categoryInfo.list = [
    {
      categoryTitle: res.data.baseName,
      categoryId: "0",
      children: res.data.categoryList,
    },
  ] as any;
  categoryInfo.loading = false;
  filter.categoryId = ["0"] as any;
};

const productInfo = reactive({
  loading: false,
  total: 0,
  isRequest: false,
  list: {},
});

const search = async () => {
  if (!filter.question) {
    ElMessage.error("请先输入查询条件");
    return;
  }

  productInfo.loading = true;
  const res: any = await getProductpage(
    {
      baseId: 1,
    },
    {
      ...filter,
      question: encodeURIComponent(filter.question),
      categoryId: filter.categoryId[filter.categoryId.length - 1] || 0,
    }
  );
  productInfo.loading = false;
  productInfo.list = res.data.records;
  productInfo.total = res.data.total;
  productInfo.isRequest = true;
};

export { storeVisible, initData, categoryInfo, filter, search, productInfo };
