import { getDeptTree } from "@/api/deli/setting";
import { ref } from "vue";

const answer = ref();
const question = ref();
const treeList = ref([]);

const inputDom = ref<HTMLTextAreaElement>();
const getTree = async () => {
  const res: any = await getDeptTree();
  treeList.value = res.data.deptList;
};
export { answer, question, getTree, treeList, inputDom };
